import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import {actionHandlers } from './actions';
import style from './kbFlagModal.scss';

export const snKbFlagModal = 'sn-kb-flag-modal';

createCustomElement(snKbFlagModal, {
	renderer: {
		type: snabbdom,
		view
	},

	initialState: {
		flaggedResponse: null,
		flagInput: ""
	},

	properties: {
		openModal: {
			default:false
		},
		articleId:{
			default:""
		},
		enableActionableFeedback:{
			default:true
		}
	},
	styles: style,
	actionHandlers: actionHandlers
});