import {
    KNOWLEDGE_BASE_SELECTED
} from './constants';

const filterResults = (searchTerm, options, dispatch, updateState) => {
    if (searchTerm.length >= 1) {
        updateState({
            knowledgeBases: options.filter(obj => obj.displayValue.toLowerCase().indexOf(searchTerm.toLowerCase()) !== -1).map(obj => ({
                "value": obj.value,
                "displayValue": obj.displayValue
            }))
        });
    } else {
        updateState({
            knowledgeBases: options,
            selectedKnowledgeBase: { value: '', displayValue: '' }
        });
    }
};

export const onSearchTermChange = (event, options, selectedOptionDisplayValue, dispatch, updateState) => {
    if (event) {
        let newState = {};
        var searchTerm = event.target.value.replace(/^\s+/g, '');

        if (selectedOptionDisplayValue != null && selectedOptionDisplayValue.length > 0) {
            newState.selectedOptionDisplayValue = '';
            updateState({
                selectedKnowledgeBase: { value: '', displayValue: '' }
            });
        }

        filterResults(searchTerm, options, dispatch, updateState);
        newState.searchTerm = searchTerm;
        updateState(newState);
    }
};

export const onSelectionChange = (selectionValue, selectionDisplayValue, dispatch, updateState) => {
    updateState({
        searchTerm: selectionDisplayValue,
        selectedOptionDisplayValue: selectionDisplayValue,
        selectedKnowledgeBase: { value: selectionValue, displayValue: selectionDisplayValue },
        selectedArticleTemplate: { value: '', displayValue: '-- Select --' }
    });

    dispatch(KNOWLEDGE_BASE_SELECTED, { id: selectionValue })
};