export const reason_choices = `query {
    AppKnowledge_Query {
        getFeedbackChoiceList {
            label
            value
        }
    }
  }`;

export const update_feedback = `mutation ($articleId: String, $comment: String, $reason: String, $useful: String, $rating: String){
    GlideRecord_Mutation{
        insert_kb_feedback(article: $articleId, comments: $comment, reason: $reason, useful: $useful, rating: $rating) {
            sys_id {
                value
            }
        }
    }
}`

