import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './kbAttachment.scss';
import {actions, actionHandlers} from './actionHandlers';
import {
	LOAD_ATTACHMENT,
	LOAD_CLASSNAME
} from './actions';
import view from './view';
import { timeAgoBehaviors } from "../../_shared/behaviors";

createCustomElement("sn-knowledge-kb-attachment", {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		table: "",
		sysId: "",
		locale: "",
		docviewerEnabled: {default: false}
	},
	behaviors: [...timeAgoBehaviors],
	actions,
	actionHandlers,
	onConnect(host, dispatch) {
		dispatch(LOAD_ATTACHMENT, {
			sysId: host.sysId,
			table: "kb_knowledge"
		});
		dispatch(LOAD_CLASSNAME, { sysId: host.sysId });
	},
	styles: styles
});
