const knowledge_blocks_data_read = `query($articleSysId: String!, $userId: String!, $blocks: String!, $template: String!){
  AppKnowledgeBlocks_Query {
    getKBBlocksFromArticleForUser(articleSysId: $articleSysId, userId: $userId,
      blocks: $blocks, template: $template)
  }
}`;

export default knowledge_blocks_data_read;
