export const processArticleAction = (coeffects) => {
    if (coeffects.action && coeffects.action.payload && coeffects.action.payload.item) {
        const hasFunction = coeffects.action.payload.item.action;
        if (hasFunction)
            hasFunction(coeffects);
    }
    return;
};

const _selectKnowledgeBlocksForSection = (state, dispatch, content) => {
    const parentDiv = document.createElement('div');
	parentDiv.innerHTML = state.impersonatedContentHTML ? state.impersonatedContentHTML : content.data.props.innerHTML;
	state.blocks.forEach(block => {
        const kbBlock = parentDiv.querySelector('.' + block.number + ':not(.block-selected)');
		if (kbBlock) {
			if (state.properties.blocksSelected) {
				kbBlock.classList.add('block-selected');

				const childDiv = document.createElement('div');
				childDiv.id = 'detail_' + block.number;
				childDiv.classList.add('block-detail');

				const iconSpan = document.createElement('span');
				iconSpan.innerHTML =
					`<svg xmlns="http://www.w3.org/2000/svg"
					aria-hidden="true"
					viewBox="0 0 12 12"
					style="width: 0.75rem; height: 0.75rem;">
					${NOW_EYE_OUTLINE_ICON_SMALL}
				</svg>`;
				childDiv.appendChild(iconSpan);

				const numberSpan = document.createElement('span');
				numberSpan.innerHTML = block.number;
				childDiv.appendChild(numberSpan);

				const descSpan = document.createElement('span');
				descSpan.innerHTML = block.short_description;
				childDiv.appendChild(descSpan);

				if (block.can_read_user_criteria) {
					const canReadSpan = document.createElement('span');
					canReadSpan.innerHTML = t('Can Read') + ' - ' + block.can_read_user_criteria;
					childDiv.appendChild(canReadSpan);
				}

				if (block.cannot_read_user_criteria) {
					const cannotReadSpan = document.createElement('span');
					cannotReadSpan.innerHTML = t('Cannot Read') + ' - ' + block.cannot_read_user_criteria;
					childDiv.appendChild(cannotReadSpan);
				}
                kbBlock.parentNode.insertBefore(childDiv, kbBlock);
			} else {
				kbBlock.classList.remove('block-selected');
				const detailDiv = document.getElementById('detail_' + block.number);
				if (detailDiv) {
					parentDiv.removeChild(detailDiv);
				}
			}
		}
	});

	content.data.props.innerHTML = parentDiv.innerHTML;
}

export const selectKnowledgeBlocks = (state, dispatch, content) => {
    // If `content` is an array
    // `content` contains data for each section (corresponding to respective HTML Editor) in an article
    if (content instanceof Array) {
        for (let item of content) {
            let sectionalContent = item
                && item.children
                && item.children[0]
                && item.children[0].children
                && item.children[0].children[1];

            let collapsableContent = sectionalContent
                && sectionalContent.children
                && sectionalContent.children.length
                && sectionalContent.children[0];

            let contentToRender = collapsableContent || sectionalContent;
            if (contentToRender) {
                _selectKnowledgeBlocksForSection(state, dispatch, contentToRender);
            }
        }
    } else {
        // If `content` is an object
        _selectKnowledgeBlocksForSection(state, dispatch, content);
    }
};
