import styles from './knowledgeDiscussionWrapper.scss';
import { createCustomElement } from '@servicenow/ui-core';
import view from './view';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { actions, actionHandlers } from './actions';
import { LOAD_ANSWER, LOAD_QUESTION } from './constants';

createCustomElement('sn-knowledge-discussion-wrapper', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		alertMessages: [],
		messageDisplayed: false
	},
	properties: {
		table: '',
		sysId: '',
		active: '',
		dehydrate: '',
		baseTable: '',
		detailToLoad: '',
		sysIdToLoad: '',
		mode: { default:'view' },
		view: { default:'workspace' },
		src: '',
		query: '',
		alerts:{ default:'' },
		selectedTabIndex: 0,
		defaultTab: '',
		readOnlyForm: '',
		hideDetails:'',
		detailsContext: '',
		views: { default:'workspace' },
		title:''
	},
	actions,
	actionHandlers,
	onConnect(host, dispatch) {
		if (host.table != "kb_knowledge" && !host.table.startsWith('kb_template') && !host.table.startsWith('u_kb_template')) {
			dispatch(LOAD_QUESTION, { questionID: host.sysId, condition: 'name=sn_communities.knowledge_base_id', answerID: host.sysId });
			dispatch(LOAD_ANSWER, { questionID: host.sysId, condition: 'name=sn_communities.knowledge_base_id', answerID: host.sysId });
		}
	},
	styles: styles
});
