import {actionTypes} from '@servicenow/ui-core';
import {
    LOAD_LANGUAGES, 
    LOAD_LANGUAGES_SUCCESS,
    LANGUAGES_SELECTED
} from './constants';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {getAvailableLanguages} from './queries';
import {get} from 'lodash';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

const fetchLanguageDetails = createGraphQLEffect(getAvailableLanguages,{
    variableList: ['sourceArticle'],
    successActionType: LOAD_LANGUAGES_SUCCESS
})

export const actions = {
    [LOAD_LANGUAGES]:{
        private: true
    }
};
export const actionHandlers = {
    [COMPONENT_BOOTSTRAPPED]: ({dispatch, properties}) => {
        dispatch(LOAD_LANGUAGES , {
            ...properties
        });
    },
    [LOAD_LANGUAGES]: fetchLanguageDetails,
    [LOAD_LANGUAGES_SUCCESS]: {
        stopPropagation: true,
        effect: ({action: {payload}, updateState}) => {
            let results = get(payload,"data.knowledge.languages",[]);
            const languageItems = results.map(({language, label,disabled}) => ({
                id: language,
                label: label,
                disabled: disabled
            }));
            updateState({languageItems: languageItems});
        }
    },
    [LANGUAGES_SELECTED] : ({action:{payload}, dispatch}) => {
        dispatch('SN_SCRIPTED_MODAL#DATA_SET', {
            selectedLanguages: payload.value.toString()
        });
    }
};