import {Fragment, createElement} from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '../communityQAComment';
import '@servicenow/now-icon';
import '@servicenow/now-avatar';
import '../timeAgo'
import {
	SET_STATE
} from './actions';

const view = (state, {dispatch}) => {
	if (!state || !state.communityQA || !state.communityQAAnswer) {
		return (
			<Fragment>
				<div>{t('loading...')}</div>
			</Fragment>
		);
	}
	const articleResult = state.communityQA.data[0];
	const answerResult = state.communityQAAnswer;
	if (!articleResult) {
		return null;
	}
	else if (articleResult){
		let lastAnswer = articleResult.latestReply && articleResult.latestReply.edited_date && articleResult.latestReply.edited_date.length > 0 ? articleResult.latestReply.edited_date : null;
		const frameless = state.properties.frameless === 'true' || state.properties.frameless === true;
		let noBorderClass = '-bottom-border';
		if(frameless)
			noBorderClass = '-no-border';

		const detailBody = (
			<div className={'sn-card ' + noBorderClass} tabIndex='0'>
				<header className="sn-card-header">
					<div className="sn-card-row"><span>{articleResult.forum.name} {articleResult.topics.length > 0 ? ' | ' + articleResult.topics[0].name : ''}</span></div>
					<div className="sn-card-row -title"><span>{articleResult.title}</span></div>
					<div className="sn-card-row has-avatar">
						<div className="sn-card-content -static">
							<now-avatar
								size="md"
								image-src={'/' + articleResult.userAvatarObject.userImage}
								user-name={articleResult.userAvatarObject.name} />
						</div>
						<div className="sn-card-content">
							<ul className="sn-meta-list">
								<li className="sn-meta-list-item"><now-icon icon="user-outline" size="sm" ></now-icon><span>{t('Authored by') + ' ' + articleResult.userAvatarObject.name}</span></li>
								<li className="sn-meta-list-item"><now-icon icon="calendar-outline" size="sm" ></now-icon><span>{t('Asked') + ' '}<sn-timeago time-stamp={articleResult.published_date} behaviors={state.behaviors} system-time={true}/></span></li>
							</ul>
						</div>
					</div>
				</header>
				<div className="sn-card-body" ref = {(article_content) => {
					if (!state.articleContent) {
						dispatch(SET_STATE, {articleContent: article_content}, {stopPropagation: true});
					}
				}}>
					{createElement('div',{props: {innerHTML: articleResult.description}})}
				</div>
				<div className="sn-card-footer">
					<ul className="sn-meta-list">
						<li className="sn-meta-list-item">
							<span>{lastAnswer ? <span>{t('Answered') + ' '}<sn-timeago time-stamp={lastAnswer} behaviors={state.behaviors} system-time={true}/></span> : t('Not Answered')}</span>
						</li>
						<li>
							<span>{articleResult.view_count + ' ' + (articleResult.view_count == 1 ? t('view') : t('views'))}  {articleResult.upvote_count + ' ' + (articleResult.upvote_count == 1 ? t('vote') : t('votes'))}  {articleResult.comment_count + ' ' + (articleResult.comment_count == 1 ? t('reply') : t('replies'))}</span>
						</li>
					</ul>
				</div>
				<div className="sn-card-list">
					{
						(articleResult.accepted_answer && articleResult.accepted_answer.sys_id) ?
							<sn-community-qa-comment key={articleResult.accepted_answer.sys_id} comment={articleResult.accepted_answer} acceptedid={articleResult.accepted_answer.sys_id} sysid={articleResult.accepted_answer.sys_id} disablenest={true} behaviors={state.behaviors}/>
							: ''
					}

					{
						(answerResult.length == 1 && articleResult.accepted_answer && articleResult.accepted_answer.sys_id) ? ''

							: answerResult.map((item) => {
								return (<sn-community-qa-comment key={item.sys_id} comment={item} acceptedid={articleResult.accepted_answer.sys_id} sysid={item.sys_id} behaviors={state.behaviors}/>);
							})
					}
				</div>
			</div>
		);
		if (state.articleContent) {
			var aTags = state.articleContent.querySelectorAll('a');
			aTags.forEach(function (item) {
				item.addEventListener('click', function(e) {
					var redirectURL = e.currentTarget || e.target || e.srcElement;
					window.open(redirectURL, '_blank');
					e.stopPropagation();
					e.preventDefault();
				});
			});
		}
		return (
			<div className='sn-agent-assist'>
				{ frameless ? (detailBody) : (
					<div className="sn-panel">
						{detailBody}
					</div>)
				}
			</div>
		);
	}
	return null;
};

export default view;
