import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './communityBlogDetail.scss';
import '@servicenow/now-icon';
import '@servicenow/now-avatar';
import view from './view';
import {actions, actionHandlers} from './actionHandlers';
import { LOAD_COMMUNITYBLOG } from './actions';
import { timeAgoBehaviors } from "../../_shared/behaviors";

createCustomElement('sn-community-blog-detail', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {

	},
	behaviors: [...timeAgoBehaviors],
	properties: {
		table: '',
		sysId: '',
		frameless: true
	},
	actions,
	actionHandlers,
	onConnect(host, dispatch){
		if (host.table === 'blog' || host.table === 'sn_communities_blog') {
			dispatch(LOAD_COMMUNITYBLOG, {id: host.sysId, content_type : 'cc3fcaa0dbd26600b1f6f78eaf96192e'});
		}
	},
	onDisconnect(host) {
		if (host.articleContent) {
			var aTags = host.articleContent.querySelectorAll('a');
			aTags.forEach(function (item) {
				item.removeEventListener('click', function(e) {
					var redirectURL = e.currentTarget || e.target || e.srcElement;
					window.open(redirectURL, '_blank');
					e.stopPropagation();
					e.preventDefault();
				});
			});
		}
	},
	styles: styles
});
