import {
	LOAD_COMMUNITYQA,
	LOAD_COMMUNITYQA_ANSWER,
	LOAD_COMMUNITYQA_SUCCEEDED,
	LOAD_COMMUNITYQA_FAILED,
	LOAD_COMMUNITYQA_ANSWER_SUCCEEDED,
	LOAD_COMMUNITYQA_ANSWER_FAILED,
	SET_STATE
} from './actions';
import {createHttpEffect} from '@servicenow/ui-effect-http';

export const actionHandlers = {
	[LOAD_COMMUNITYQA]: createHttpEffect(
		'/api/sn_communities/v1/community/contents/:id', 
		{
			successActionType: LOAD_COMMUNITYQA_SUCCEEDED,
			errorActionType: LOAD_COMMUNITYQA_FAILED,
			pathParams: ['id'],
			queryParams: ['content_type']
		}
	), 
	[LOAD_COMMUNITYQA_ANSWER]: createHttpEffect(
		'/api/sn_communities/v1/community/contents/:id/answers', 
		{
			successActionType: LOAD_COMMUNITYQA_ANSWER_SUCCEEDED,
			errorActionType: LOAD_COMMUNITYQA_ANSWER_FAILED,
			pathParams: ['id'],
			queryParams: ['content_type', 'depth']
		}
	),
	[LOAD_COMMUNITYQA_SUCCEEDED]: {
		stopPropagation: true,
		effect: ({state, action, updateState}) => {
			updateState({
				...state,
				communityQA: action.payload.result
			});
		}
	},
	[LOAD_COMMUNITYQA_FAILED]: {
		stopPropagation: true,
		effect: ({state, updateState}) => {
			updateState( {...state});
		}
	},
	[LOAD_COMMUNITYQA_ANSWER_SUCCEEDED]: {
		stopPropagation: true,
		effect: ({action, updateState}) => {
			updateState( {
				communityQAAnswer: action.payload.result
			});
		}
	},
	[LOAD_COMMUNITYQA_ANSWER_FAILED]: {
		stopPropagation: true,
		effect: ({state, updateState}) => {
			updateState( {...state});
		}
	}, 
	[SET_STATE]: {
		stopPropagation: true,
		effect: ({action, updateState}) => {
			updateState( {
				articleContent: action.payload.articleContent
			});
		}
	}
};