import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '../kbSharedComparator';
import '@servicenow/now-label-value';
import '@servicenow/now-heading';
import '@servicenow/now-dropdown';
import '@servicenow/now-modal';
import '@servicenow/now-button';
import { t } from 'sn-translate';


export default ({
    languageItems, 
    selectedLanguage, 
    languageWarningOpened, 
    comparisonData, 
    disableLanguage, 
    hideCaret, 
    disableCreateArticle, 
    sourceLanguage, 
    isDynamicTranslationEnabled,
    modalFor,
		isTranslating,
		properties: {table}
}) => {
    let machinTranslateButton = <Fragment/>;
    if (isDynamicTranslationEnabled) {
        machinTranslateButton = 
        <now-button 
            label={t('Machine translate')} 
            variant="secondary" 
            append-to-payload={{buttonFor: "machineTranslate"}}
            config-aria = {{'aria-label':t('Machine translate')}}/>
    }

    let loaderFill = <Fragment/>;
    let comparatorMode = disableCreateArticle ? 'normal' : 'editable';
    if (isTranslating)
        loaderFill = <now-icon icon="loader-fill" spin></now-icon>;
	
		let labels = {
			title: t('Translate Article'),
			button : t('Create draft article')
		};
		if (table == "kb_knowledge_block") {
			labels.title = t('Translate Block');
			labels.button = t('Create draft block');
		}
        
    return (
        <Fragment>
                <nav>
                    <now-heading has-no-margin className="header-left" label={labels.title} variant="header-primary"/>
                    <div className="header-right">
                        <now-button 
                            label={labels.button} 
                            variant="secondary" 
                            disabled={disableCreateArticle} 
                            append-to-payload={{buttonFor: "createDraftArticle"}}
                            config-aria = {{'aria-label':labels.button}}/>
                        {machinTranslateButton}
                        {loaderFill}
                    </div>
                </nav>
                <ul>
                    <li className="comparator-row">
                        <section className = "old-content content-block">
                            <now-label-value-inline label = {t('Original language')}/>
                            <section innerHTML = {sourceLanguage.label}/>
                        </section>
                        <section className="new-content content-block">
                            <now-label-value-inline id={name} label={t('Translated language')}/>
                            <section>
                            <now-dropdown 
                                bare
                                items = {languageItems}
                                selected-items= {selectedLanguage}
                                select="single"
                                disabled = {disableLanguage}
                                config-aria = {{'aria-label':t('Select languages')}}/>
                            </section>
                        </section>
                    </li>
                </ul>
                <sn-kb-shared-comparator comparisonData={comparisonData} mode={comparatorMode}/>
                <now-modal 
                    content={t('This action will overwrite any existing changes.')} 
                    footer-actions='[{"label":"Yes", "variant": "primary"},{"label":"No", "variant": "secondary"}]' 
                    header-label={t('Warning')} 
                    size="sm"
                    append-to-payload={{modalFor: {modalFor}}}
                    opened={languageWarningOpened}>
                </now-modal>
        </Fragment>
    );
}