import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {
    LOAD_LANGUAGES_AND_SOURCE_SUCCESS,
    LOAD_TRANSLATED_SUCCESS,
    PREVIEW_TRANSLATED_RECORD,
    GET_BLOCK_COUNT_SUCCESS
    } from '../constants';
    
const load_languages_and_source_query = `
query($table: String!, $sourceArticle: String!, $types: [String!], $exclude: [String!]){
  knowledge:AppKnowledge_Query {
    source: getArticleByFieldTypes(table: $table, sys_id: $sourceArticle, types: $types, exclude: $exclude) {
      name
      type
      label
      value
    }
    languages: getAvailableLanguages(articleID:$sourceArticle){
        language,
        label,
        disabled
    }
  }
gr: GlideRecord_Query {
  knowledge: kb_knowledge(queryConditions:"sys_id={{sourceArticle}}") {
    _results {
      language{
        displayValue,
        value
      }
    }
  }
}
}`;

const load_translation_query = `query ($sourceArticle: String!, $table: String!, $types: [String!], $exclude: [String!]) {
  src: GlideRecord_Query {
    knowledge: kb_knowledge(sys_id: $sourceArticle) {
      _results {
        article_id {
          value
        }
        _query {
          trans: GlideRecord_Query {
            knowledge: kb_knowledge(queryConditions: "ORDERBYDESCsys_created_on^parent=$$parent.article_id$$^language={{selectedLanguage}}", pagination: {limit: 1}) {
              _results {
                sys_id {
                  value
                }
                workflow_state{
                  value
                }
                _query {
                  knowledge: AppKnowledge_Query {
                    translated: getArticleByFieldTypes(table: $table, sys_id: "$$parent.sys_id$$", types: $types, exclude: $exclude) {
                      name
                      value
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

`;

const get_block_count = `
query($sourceArticle:String!){
  knowledge: AppKnowledge_Query {
    getBlockCount(articleID: $sourceArticle) 
  }
}
`;

const create_or_checkout_mutation = `
mutation($sourceRecord:String!, $translatedLanguage: String!, $translationData: String!) {
    glide: AppKnowledge_Mutation {createOrCheckout(articleID:$sourceRecord, translatedLanguage:$translatedLanguage, translationData:$translationData)
    }
  }
`;


export const fetchLanguageAndSourceDetails = createGraphQLEffect(load_languages_and_source_query, {
    variableList:['table', 'sourceArticle', 'types', 'exclude'],
    templateVarList: ['sourceArticle'],
    successActionType: LOAD_LANGUAGES_AND_SOURCE_SUCCESS
});

export const getBlockCount = createGraphQLEffect(get_block_count, {
  variableList: ['sourceArticle'],
  successActionType: GET_BLOCK_COUNT_SUCCESS
});

export const loadTranslatedArticle = createGraphQLEffect(load_translation_query, {
    variableList: ['sourceArticle', 'table', 'types', 'exclude'],
    templateVarList: ['selectedLanguage'],
    successActionType: LOAD_TRANSLATED_SUCCESS
});

export const createOrCheckout = createGraphQLEffect(create_or_checkout_mutation, {
    variableList: ['sourceRecord', 'translatedLanguage', 'translationData'],
    successActionType: PREVIEW_TRANSLATED_RECORD
});
