import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import view from "./view";
import {actionHandlers} from './actions'
import style from "./snComments.scss";
import { timeAgoBehaviors } from "../../../../../_shared/behaviors";


export const snComponentComments = "sn-comments";

createCustomElement(snComponentComments, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		value: ""
	},
	behaviors: [...timeAgoBehaviors],
	properties: {
		items: { default: [] },
		metaData: { default: [] },
		buttons: { default: [] },
		actions: { default: [] },
		isEditing: { default: "" },
		currentEditorId: { default: "" },
		expandedItems: { default: [] },
		manageExpanded: { default: false },
		language: { default : "en" }
	},
	actionHandlers,
	styles: style
});


