import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment, dangerouslyCreateElementFromString } from '@servicenow/ui-renderer-snabbdom';
import {nowLinkInterceptorBehavior} from '@servicenow/now-activity-stream';
import { actionHandlers } from "./actions";
import {
	BLOCK_FETCH_STARTED,
	IMPERSONATE_MODAL_STYLE,
	IMPERSONATE_MODAL_FIRST_ITEM_STYLE,
	IMPERSONATE_MODAL_ITEM_STYLE,
	HIDDEN_BLOCKS_MODAL_ITEM_STYLE,
	DATE_FORMAT
} from './constants';
import {selectKnowledgeBlocks, impersonateActions} from '../../utils/actionUtil';
import styles from './kbContent.scss';
import {t} from "sn-translate";
import "@servicenow/now-icon";
import "@servicenow/now-modal";
import "@servicenow/now-record-field-lookup-connected";
import "@servicenow/now-record-date-picker";
import { get, clone } from "lodash";
import moment from 'moment';

const getDateFormat = () => {
	// TODO: use sn-workspace-header prefetched data source and remove this function
	const uxGlobalsUserStr = `ux_globals.presource["sn-workspace-header:wsUserData"].data.GlideDomain_Query.user`;
	const dateFormat = get(
        window,
        `${uxGlobalsUserStr}.dateFormat`,
        ''
	);
	const timeFormat = get(
        window,
        `${uxGlobalsUserStr}.timeFormat`,
        ''
	);

	return (dateFormat && timeFormat) ? `${dateFormat.toUpperCase()} ${timeFormat}` : DATE_FORMAT;
}

const getArticleHTMLContent = props => props.articleData!=null ? [props.articleData] : [];

const view = (state, {dispatch}) => {
	const content = clone(state.properties.content);
	if (state.blocks.length > 0) {
		selectKnowledgeBlocks(state, dispatch, content);
	}

	const dateFilter = state.properties.workflowState === 'published' ? (<div className="kb-control-item">
			<now-record-date-picker
				style={IMPERSONATE_MODAL_ITEM_STYLE}
				name="viewOn"
				format={state.dateFormat}
				label={t('Select Date')}
				value={state.viewDate}
				timePicker />
		</div>) : "";

	const modalContent = (<div style={IMPERSONATE_MODAL_STYLE}>
		<div className="kb-control-item">
			<now-record-field-lookup-connected
				style={IMPERSONATE_MODAL_FIRST_ITEM_STYLE}
				name="viewAs"
				label={t('Select User')}
				tableName="sys_user"
				fieldName="name"
				value={state.properties.userId}
				displayValue={state.properties.userName}/>
		</div>
		{dateFilter}
	</div>);

	const hiddenBlocksContent = state.hiddenBlocks.map((hiddenBlock) => (
		<div style={HIDDEN_BLOCKS_MODAL_ITEM_STYLE}>
			{hiddenBlock.number} {hiddenBlock.short_description}
		</div>
	));

	const hiddenBlocksModalContent = (<div>
		<div style={HIDDEN_BLOCKS_MODAL_ITEM_STYLE}>
			{t('The content for the following knowledge blocks are unavailable for ') + state.properties.userName + ':'}
		</div>
		<div>{hiddenBlocksContent}</div>
	</div>);

	return (
		<Fragment>
			<article>{content}</article>
			<now-modal manage-opened
					   opened={state.properties.blocksImpersonated}
					   footer-actions={impersonateActions}
					   header-label={t('View as User')}
					   append-to-payload={{key: 'impersonate-modal'}}
					   size="md">
				{modalContent}
			</now-modal>
			<now-modal manage-opened
					   opened={state.showHiddenBlocks}
					   content={hiddenBlocksModalContent}
					   header-label={t('Unavailable Blocks')}
					   append-to-payload={{key: 'hidden-blocks-modal'}}
					   size="md"/>
		</Fragment>
	)
};

createCustomElement("sn-kb-content", {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		{
			behavior: nowLinkInterceptorBehavior,
			options: {
				propsToHtmlArray: getArticleHTMLContent
			}
		}
	],
	initialState: {
		blocks: [],
		viewDate: moment().format(getDateFormat()),
		dateFormat: getDateFormat(),
		selectedValue: {},
		showHiddenBlocks: false,
		hiddenBlocks: [],
		impersonatedContentHTML: ''
	},
	properties: {
		table: '',
		articleData : {default : ""},
		content : "",
		parentSysId: "",
		parentNumber: "",
		blocksSelected: false,
		blocksImpersonated: false,
		workflowState: "",
		knowledgeBase: "",
		userId : "",
		userName: ""
	},
	actionHandlers,
	onConnect(host, dispatch) {
		dispatch(BLOCK_FETCH_STARTED, {
			articleSysId: host.parentSysId,
			userId: host.userId,
			blocks: "",
			template: (host.table && host.table.startsWith('kb_template')) ? host.table : "kb_knowledge"
		});
	},
	styles: styles
});
