import { get, isEmpty } from "lodash";
import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import { reason_choices, update_feedback } from './query';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
    LOAD_CHOICES,
    CHOICE_LOADED,
    CHOICE_LOADING_FAILED,
    UPDATE_FEEDBACK,
    UPDATE_FEEDBACK_SUCCEEDED,
    UPDATE_FEEDBACK_FAILED,
    FOOTER_ACTION_CLICKED,
    FEEDBACK_MODAL_SUBMITTED,
    FEEDBACK_MODAL_CLOSED,
    BUTTON_CLICKED,
    DROPDOWN_CLICKED,
    MODAL_BUTTON_ESC,
    SHOW_MESSAGE,
    FEEDBACK_MODAL_RESET,
    OTHER_REASON,
    MESSAGES,
    UPDATE_HELPFUL_COUNT
} from './constants';

const getFeedbackChoices = createGraphQLEffect(reason_choices, {
    successActionType: CHOICE_LOADED,
    errorActionType: CHOICE_LOADING_FAILED,
    variableList: ['sys_id']
});

const updateFeedbackEffect = createGraphQLEffect(update_feedback, {
    successActionType: UPDATE_FEEDBACK_SUCCEEDED,
    errorActionType: UPDATE_FEEDBACK_FAILED,
    variableList: ['articleId', 'comment', 'reason', 'useful', 'rating']
});

const resetModalState = (updateState) => {
    updateState({
        comments: "",
        reason: OTHER_REASON
    });
}

const extractContent = (str) => {	
    return (new DOMParser).parseFromString(str, "text/html").documentElement.textContent.toString().trim();	
}

export const actionHandlers = {
    [COMPONENT_BOOTSTRAPPED]: {
        effect: ({ dispatch, state }) => {
            dispatch(LOAD_CHOICES, {
                'sys_id': state.properties.articleId
            });
        }
    },
    [LOAD_CHOICES]: getFeedbackChoices,
    [CHOICE_LOADED]: {
        effect: ({ action, updateProperties }) => {
            updateProperties({
                choiceList: get(action, "payload.data.AppKnowledge_Query.getFeedbackChoiceList")
            });
        },
        stopPropagation: true
    },
    [CHOICE_LOADING_FAILED]: (props) => {
        console.log(props);
    },
    [FOOTER_ACTION_CLICKED]: {
        effect:(props) => {
            if (props.action.payload.footerAction.id == "submit_ok")
                props.dispatch(FEEDBACK_MODAL_SUBMITTED, {});
            else if (props.action.payload.footerAction.id == "submit_cancel"){
                resetModalState(props.updateState);
                props.dispatch(FEEDBACK_MODAL_CLOSED, {});
            }
        },
        stopPropagation: true
    },
    [FEEDBACK_MODAL_SUBMITTED]: {
        effect: ({ properties, state, dispatch }) => {
            dispatch(UPDATE_FEEDBACK, {
                'articleId': properties.articleId,
                'comment': isEmpty(extractContent(state.comments)) ? "" : state.comments,
                'reason': state.reason,
                'useful': properties.helpful,
                'rating': properties.rating
            });
        }
    },
    [FEEDBACK_MODAL_CLOSED]: {
        effect: ({ updateState, properties, dispatch }) => {
            dispatch(UPDATE_FEEDBACK, {
                'articleId': properties.articleId,
                'useful': properties.helpful,
                'reason': OTHER_REASON,
                'rating': properties.rating
            });
            resetModalState(updateState);
        }
    },
    [UPDATE_FEEDBACK]: updateFeedbackEffect,
    [UPDATE_FEEDBACK_SUCCEEDED]: {
        effect: ({ updateState, action, dispatch }) => {
            if(!action.payload.data.GlideRecord_Mutation.insert_kb_feedback)
                dispatch(SHOW_MESSAGE, {
                    status: "critical",
                    iconName: "circle-info-outline",
                    content: MESSAGES.LIMIT_REACHED_MESSAGE,
                    action: { type: "dismiss" }
                });
            else{
                dispatch(SHOW_MESSAGE, {
                    status: "info",
                    iconName: "circle-info-outline",
                    content: MESSAGES.THANK_YOU,
                    action: { type: "dismiss" }
                });
				let isRatingFeedback = get(action, "meta.options.variables.useful", "yes") == "";
                let isHelpful = get(action, "meta.options.variables.useful", "yes") == "yes"? true : false;
                if(!isRatingFeedback) dispatch(UPDATE_HELPFUL_COUNT, { isHelpful });
            }

        resetModalState(updateState);
        }
    },
    [UPDATE_FEEDBACK_FAILED]: {
        effect: ({ dispatch }) => {
            dispatch(SHOW_MESSAGE, {
                status: "critical",
                iconName: "circle-info-outline",
                content: MESSAGES.LIMIT_REACHED_MESSAGE,
                action: { type: "dismiss" }
            });
        }
    },
    [BUTTON_CLICKED]: {
        effect: ({ action, dispatch }) => {
            dispatch(FEEDBACK_MODAL_RESET, action);
        },
        stopPropagation: true
    },
    [MODAL_BUTTON_ESC]: {
        effect: ({ action, dispatch, updateState, properties }) => {
            resetModalState(updateState);
            dispatch(UPDATE_FEEDBACK, {
                'articleId': properties.articleId,
                'useful': properties.helpful,
                'reason': OTHER_REASON,
                'rating': properties.rating
            });

            dispatch(FEEDBACK_MODAL_RESET, action);
        },
        stopPropagation: true
    },
    [DROPDOWN_CLICKED]: {
        effect: ({ action, updateState }) => {
            updateState({
                reason: action.payload.item.id
            })
        }
    },
}
