const flag_update = `mutation ($flagComment: String, $articleId: String, $reason: String) {
    GlideRecord_Mutation {
      insert_kb_feedback(comments: $flagComment, article: $articleId, flagged: "true", reason: $reason) {
        sys_id {
          value
        }
      }
    }
  }`;
    
export default flag_update;
  
    