import { t } from 'sn-translate';
import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import '@servicenow/now-icon';
import '@servicenow/now-legacy-icon';
import '@servicenow/now-text-link';
import '../components/kbFlagModal';
import ArticleActions from "./articleActions"

export default ({ payload, fullView, artileTimeToRead, openFlagModal, behaviors }) => {
  const linkPayload = !fullView ? { table: 'kb_knowledge', sys_id: payload.articleResult.sysId, number: payload.articleResult.number } : "";
  return (
    <Fragment>
      <header className="sn-card-header kb-header">
        <div className="kb-header-top">
          <div className="kb-top-left">
            <div className="sn-card-row kb-breadcrumb">
              <span>{payload.articleResult.kbKnowledgeBase} {(payload.articleResult.kbCategory && payload.articleResult.kbCategory.length > 0) ? ' | ' + payload.articleResult.kbCategory : ''}</span>
            </div>
            <div className="sn-card-row kb-title -title" role="heading" aria-level="2">
              {/* <now-heading has-no-margin label={articleResult.short_description.displayValue} level="1" variant="heading-secondary"></now-heading> */}
              <span>{payload.articleResult.shortDescription}</span>
            </div>
          </div>
          {fullView ?
            <div className="kb-top-right">
              <div className="kb-action">
                <ArticleActions payload={payload} />
              </div>
            </div> : ""}
        </div>
        <div className="sn-card-row kb-header-bottom">
          <div className="sn-card-content kb-meta">
            {fullView ? "" : <div className="sn-card-row">
              <now-text-link label={payload.articleResult.number} href="javascript:void(0)" append-to-payload={linkPayload}></now-text-link>
            </div>}
            <div className="sn-card-row">
              <div className="sn-card-content">
                <ul className="sn-meta-list">
                  <li className="sn-meta-list-item">
                    <now-icon icon="clock-outline" size="sm"></now-icon>
                    <span>{t('{0} min read', artileTimeToRead)}</span>
                  </li>
                  <li className="sn-meta-list-item">
                    <now-icon icon="calendar-outline" size="sm"></now-icon>
                    <span>{t('Updated') + ' '} <sn-timeago time-stamp={payload.articleResult.sysUpdatedOn} behaviors={behaviors} system-time={true}/></span>
                  </li>
                  {(payload.articleResult.sysViewCount != '-1' ? 
                  <li className="sn-meta-list-item">
                    <now-icon icon="eye-outline" size="sm"></now-icon>
                    <span>{(payload.articleResult.sysViewCount == 1 ? t('{0} view', payload.articleResult.sysViewCount) : t('{0} views', payload.articleResult.sysViewCount))}</span>
                  </li> : '')}
                  {(payload.article_attributes.showStarRating && payload.articleResult.rating != '-1' ? 
                  <li className="sn-meta-list-item">
                    <now-icon icon="star-outline" className="full-star default-star"  size="sm"></now-icon>
                    <span>{t('Average rating {0}/5', payload.articleResult.rating ? payload.articleResult.rating.toFixed(1).replace(/\.0+$/,'') : "0")}</span>
                  </li> : '')}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </header>
      {payload.article_attributes.showFlag ?
        <sn-kb-flag-modal openModal={openFlagModal} articleId={payload.articleResult.sysId} enableActionableFeedback={payload.article_attributes.enableActionableFeedbackForFlag}/> : ""}
    </Fragment>
  )
}