import {
	LOAD_REPLIES_FAILED,
	LOAD_REPLIES_SUCCEEDED,
	LOAD_COMMENT,
	STATE_SET} from './actions';
import {createHttpEffect} from '@servicenow/ui-effect-http';

export const actionHandlers = {
	[LOAD_COMMENT] : {
		stopPropagation: true,
		effect: ({state, updateState}) => {
			updateState({
				...state
			});
		}	
	},
	LOAD_REPLIES:  createHttpEffect(
		'/api/sn_communities/v1/community/contents/:id/answers',
		{
			successActionType: LOAD_REPLIES_SUCCEEDED,
			errorActionType: LOAD_REPLIES_FAILED,
			pathParams: ['id'],
			queryParams: ['content_type', 'depth']
		}
	),
	[LOAD_REPLIES_SUCCEEDED]: {
		stopPropagation: true,
		effect: (action, updateState) => {
			updateState({
				communityAnswers: action.payload.result
			})
		}
	},
	[LOAD_REPLIES_FAILED]: {
		stopPropagation: true,
		effect: ({state, updateState}) => {
			updateState({
				...state
			});
		}	
	},
	[STATE_SET]: {
		stopPropagation: true,
		effect: (state, action, updateState) => {
			state.properties.showReplies = action.payload.showReplies;
			updateState({
				...state
			})
		}
	}
};
