import { createElement } from '@servicenow/ui-renderer-snabbdom';
import '../kbAttachment';
import { get } from 'lodash';
import ArticleHeader from "./templates/articleHeader";
import ArticleFooter from "./templates/articleFooter";
import ArticleFeedback from "./templates/articleFeedback";
import ArticleTemplate from "./templates/articleTemplate";
import ArticleComments from "./templates/articleComments";
import _timeToRead from "./utils/timeToReadUtil";
import './components/kbContent';

const ArticleContent = ({ table, content, contentHTML, parentSysId, parentNumber, workflowState, knowledgeBase, userId, userName,
							blocksSelected, blocksImpersonated }) => {
	return (
		<div className="sn-card-body kb-content">
			<sn-kb-content
				table={table}
				content={content}
				articleData={contentHTML}
				parentSysId={parentSysId}
				parentNumber={parentNumber}
				workflowState={workflowState}
				knowledgeBase={knowledgeBase}
				userId={userId} userName={userName}
				blocksSelected={blocksSelected}
				blocksImpersonated={blocksImpersonated}
			/>
		</div>
	)
};

const ArticleAttachment = ({ displayAttachment, articleResult, docviewerEnabled }) => {
	if (displayAttachment)
		return (
			<div className="sn-card-row">
				<sn-knowledge-kb-attachment className="context-card" table='kb_knowledge' sys-id={articleResult.sysId} docviewer-enabled={docviewerEnabled}/>
			</div>
		);
	return "";
};

export const view = (state, { dispatch }) => {
	let articleResult = null;
	let article_html = null;
	const userDetail = {};
	if (!state.kbArticle) return null;
	if (!state.kbArticle.AppKnowledge_Query.getArticleDetails.articleSysId) return null;
	const article_attributes = state.kbArticle.AppKnowledge_Query.getArticleDetails;
	article_attributes.blocksSelected = state.blocksSelected;
	article_attributes.mode = state.properties.mode;
	article_attributes.hasViewAccess = state.hasViewAccess;
	article_attributes.selectedSysId = state.selectedSysId;

	if (!article_attributes) return null;
	articleResult = state.kbArticle.AppKnowledge_Query.getArticleDetails.articleInfo;
	article_html = article_attributes.articleHTML;

	if (!articleResult)
		return null;

	else if (articleResult) {
		const displayAttachment = articleResult.displayAttachments;
		const fullView = !(state.properties.frameless === 'true' || state.properties.frameless === true);

		if (fullView && state.kbArticle.GlideDomain_Query) {
			userDetail.userID = state.kbArticle.GlideDomain_Query.user.sys_id;
			userDetail.userLanguage = state.kbArticle.GlideDomain_Query.user.language;
			userDetail.avatar = state.kbArticle.GlideDomain_Query.user.avatar;
			userDetail.fullName = state.kbArticle.GlideDomain_Query.user.fullName;
		}

		let contentHTML = article_html || "";

		const articleContentHTML = (
			article_attributes.templateFieldData.length ? article_attributes.templateFieldData.map(field => {
				contentHTML += field.content;
				return <ArticleTemplate
					label={field.label}
					content={field.content}
					column={field.column}
					type={field.type}
					collapsible={field.collapsible}
					collapsed={field.collapsed}
					headingStyle={field.headingStyle}
					fieldStyle={field.fieldStyle}
					dispatch={dispatch}
				/>
			})
				: createElement('div', { props: { innerHTML: article_html } })
		);

		const detailBody = (
			fullView ?
				<div className="kb-article-body">
					<div className="sn-card -bottom-border kb-view-border no-padding-top">
						<ArticleHeader payload={{ articleResult, article_attributes, userDetail }}
									   fullView={fullView} artileTimeToRead={_timeToRead(contentHTML, "en")}
									   openFlagModal={state.openFlagModal} behaviors={state.behaviors}/>
						<ArticleContent table={state.properties.table} content={articleContentHTML} contentHTML={contentHTML}
										parentSysId={articleResult.sysId} parentNumber={articleResult.number}
										workflowState={articleResult.workflowState} knowledgeBase={articleResult.kbKnowledgeBase}
										userId={userDetail.userID} userName={userDetail.fullName}
										blocksSelected={state.blocksSelected} blocksImpersonated={state.blocksImpersonated}/>
						{
							state.hasViewAccess ?
								<ArticleAttachment displayAttachment={displayAttachment} articleResult={articleResult} docviewerEnabled={article_attributes.docviewerEnabled}/> : ""
						}
						{
							state.hasViewAccess ?
								<ArticleFooter payload={{articleResult, article_attributes, userDetail}} /> : ""
						}
						{
							state.hasViewAccess && get(article_attributes, "showFeedbackOptions", true) ?
								<ArticleFeedback state={state} userDetail={userDetail}/> : ""
						}

					</div>
					{
						state.hasViewAccess && get(article_attributes, "showFeedbackComments", true) ?
							<ArticleComments payload={{ articleResult, article_attributes, userDetail }} /> : ""
					}
				</div>
				:
				<div className="sn-card -no-border">
					<ArticleHeader payload={{ articleResult, article_attributes, userDetail }}
								   fullView={fullView} artileTimeToRead={_timeToRead(contentHTML, "en")}
								   openFlagModal={state.openFlagModal} />
					<ArticleContent table={state.properties.table} content={articleContentHTML} contentHTML={contentHTML}
									parentSysId={articleResult.sysId} parentNumber={articleResult.number}
									workflowState={articleResult.workflowState} knowledgeBase={articleResult.kbKnowledgeBase}
									userId={userDetail.userID} userName={userDetail.fullName}
									blocksSelected={state.blocksSelected} blocksImpersonated={state.blocksImpersonated}/>
					<ArticleAttachment displayAttachment={displayAttachment} articleResult={articleResult} docviewerEnabled={article_attributes.docviewerEnabled} />
				</div>
		);

		return (
			<div className='sn-agent-assist'>
				{detailBody}
			</div>
		);
	}
	return null;
};
