import {FIELD_UPDATED} from './constants';

export const actionHandlers = {
    [FIELD_UPDATED]: {
		stopPropagation: true,
		effect: ({action: {payload: {name, content}}, properties: {comparisonData}, updateProperties}) => {
            comparisonData.forEach(item => {
                if(item.name === name){
                    item.changedContent = content;
                }
            });
            updateProperties({
                comparisonData
            })
        }
    }
}