import '@servicenow/now-collapse';
import '@servicenow/now-icon';
import {FIELD_COLLAPSED} from '../constants';

export default props => {
    const {
        label,
        content,
        column,
        type,
        collapsible,
        collapsed,
        headingStyle,
        fieldStyle,
        dispatch
    } = props;

    return (
        <div>
            {collapsible?
                (<div style={{cssText: fieldStyle}}>
                    <now-collapse-trigger controls="templateContent" on-click={() => dispatch(FIELD_COLLAPSED, {column})}>
                        <div className="collapsible-label-container">
                            <h2 className="label" style={{cssText: headingStyle}}>
                                {label}
                                <now-icon className={collapsed ? "label-icon selector rotate down":"label-icon selector rotate"} icon="chevron-up-fill" size="md"></now-icon>
                            </h2>
                        </div>
                    </now-collapse-trigger>
                    <now-collapse id="templateContent" expanded={!collapsed}>
                        {type == "html" ? <p  className="content" innerHTML={content}/>: <section  className="content" innerHTML={content}/>}
                    </now-collapse>
                </div>) :
                (<div style={{cssText: fieldStyle}}>
                    <h2 className="label" style={{cssText: headingStyle}}>{label}</h2>
                    {type == "html" ? <p  className="content" innerHTML={content}/>: <section  className="content" innerHTML={content}/>}
                </div>)
            }
        </div>
    );
};