import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import { get } from 'lodash';
import HelpfulFeedback from "./helpfulFeedback";
import RatingFeedback from "./ratingFeedback";
import "../components/kbFeedbackModal";

export default ({state, userDetail}) => {
    return (
        <Fragment>
            <div className="kb-feedback">
                <div className="kb-feedback-left">
                    {
                        get(state.kbArticle.AppKnowledge_Query.getArticleDetails, "showYNRating", true) ?
                            <HelpfulFeedback props={{ kbArticle: state.kbArticle, helpfulData: state.helpfulData}} /> : ""
                    }
                    
                </div>
                <div className="kb-feedback-right">
                    {
                        get(state.kbArticle.AppKnowledge_Query.getArticleDetails, "showStarRating", true) ?
                            <RatingFeedback props={state.kbArticle} rating={state.rating}/> : ""
                    }
                    
                </div>
            </div>
            <sn-kb-feedback-modal userDetail={userDetail} openModal={state.openFeedbackModal} rating={state.rating} helpful={state.helpful} articleId={state.properties.sysId}/>
        </Fragment>
    )
}