import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-content-tree';
import '@servicenow/now-modal';
import '@servicenow/now-loader';
import { t } from 'sn-translate';

export default (state) => {
	return (
		<Fragment>
			<now-modal
				header-label={t('Select a category')}
				opened={state.opened}
				size="md"
				shouldFocusOnOpen={true}>
				{state.loading ?
					<now-loader size="lg" label={t('Loading')} /> : state.items.length == 0 ? <span>{state.errorMessage}</span>
						:
						<now-content-tree
							expanded-items={state.expandedItems}
							items={state.items}
							select="single"
							loading-items={state.loadingItems}
							selected-items={state.selectedItems}
						/>
				}
			</now-modal>
		</Fragment>
	);
};