import Comparator from './Comparator';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';

export default (state, {updateState, dispatch}) => {
    const {comparisonData, mode} = state.properties;
    let rowList = <Fragment/>;

    if((mode ==='diff' && window.diff_match_patch) || mode !== 'diff'){
        rowList = comparisonData.map(({
                heading,
                originalContent,
                changedContent,
                name,
                contentType
            }) => 
            <Comparator 
                contentType={contentType}
                heading={heading} 
                originalContent={originalContent} 
                changedContent={changedContent}
                name={name}
                mode={mode}
                dispatch={dispatch}
            />
        ) 
    }

    const scriptLoad = {
        operation:'set',
        path:'scriptLoaded',
        value: true
    };

	return (
        <Fragment>
            <ul className="comparator-list">
                {rowList}
            </ul>
            {mode==='diff'? <script async on-load={() => updateState(scriptLoad)} src="scripts/lib/diff_match_patch/javascript/diff_match_patch.js"/>: <Fragment/>}
        </Fragment>
	);
}
