import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import knowledge_blocks_data_read from "../../queries/knowledge_blocks.read";
import knowledge_preview_data_read from "../../queries/knowledge_preview.read";
import {
	BLOCK_FETCH_STARTED,
	BLOCK_FETCH_SUCCEEDED,
	PREVIEW_FETCH_STARTED,
	PREVIEW_FETCH_SUCCEEDED,
	SHOW_BLOCKS_MODAL,
	NOW_MODAL_FOOTER_ACTION_CLICKED,
	NOW_MODAL_OPENED_SET,
	NOW_RECORD_DATE_PICKER_VALUE_CHANGED,
	NOW_RECORD_FIELD_LOOKUP_VALUE_CHANGED
} from './constants';
import {
	IMPERSONATE_BLOCKS,
	SHOW_MESSAGE,
	ARTICLE_VIEW_ACCESS,
	VERSION_CHANGE
} from "../../constants";
import {t} from "sn-translate";
import {actionTypes} from "@servicenow/ui-core";
const {COMPONENT_RENDERED} = actionTypes;

const fetchKnowledgeBlocksEffect = createGraphQLEffect(knowledge_blocks_data_read, {
	successActionType: BLOCK_FETCH_SUCCEEDED,
	variableList: ["articleSysId", "userId", "blocks", "template"]
});

const handleKnowledgeBlocksFetchSucceeded = (coeffects) => {
	const {action, updateState} = coeffects;
	let blocks = action.payload.data.AppKnowledgeBlocks_Query.getKBBlocksFromArticleForUser;

	if (blocks) {
		blocks = JSON.parse(blocks);
		updateState({ blocks: blocks });
	}
};

const fetchKnowledgePreviewEffect = createGraphQLEffect(knowledge_preview_data_read, {
	successActionType: PREVIEW_FETCH_SUCCEEDED,
	variableList: ["articleNumber", "userId", "viewOnDate"]
});

const handleKnowledgePreviewFetchSucceeded = (coeffects) => {
	const {action, dispatch, state, updateState} = coeffects;
	let response = action.payload.data.AppKnowledgeBlocks_Query.getArticleContentForUser;
	response = JSON.parse(response);
	let hasViewAccess = true;
	let contentHTML = response.content;

	if (response.error) {
		if (response.error === "no_version_found")  {
			let publishDate = response.publishDate;
			const errorMessage = t('This article is not available on the selected date. Select a date on or after the first publication date: ' + publishDate + '.');
			dispatch(SHOW_MESSAGE, {
				action: {
					type: "dismiss"
				},
				status: "critical",
				content: errorMessage
			});
		} else if (response.error === "no_access") {
			hasViewAccess = false;
			contentHTML = createNoAccessContent(state);
		}
	}

	if (contentHTML) {
		dispatch(ARTICLE_VIEW_ACCESS, {hasViewAccess: hasViewAccess});
		updateState({impersonatedContentHTML: contentHTML});
		handleHiddenBlocks(state, dispatch, contentHTML, hasViewAccess);
		if (response.sysId) {
			dispatch(VERSION_CHANGE, {selectedSysId: response.sysId});
		}
	}
};

const handleImpersonateBlocks = (coeffects) => {
	const {action, dispatch, state, updateProperties, updateState} = coeffects;
	if (action.payload.key === 'impersonate-modal') {
		if (action.payload.action && action.payload.action.meta === "ok") {
			if (state.selectedValue.userId || state.selectedValue.viewDate) {
				let userId = state.selectedValue.userId ? state.selectedValue.userId : state.properties.userId;
				let userName = state.selectedValue.userName ? state.selectedValue.userName : state.properties.userName;
				let viewDate = state.selectedValue.viewDate ? state.selectedValue.viewDate : state.viewDate;
				updateProperties({
					viewDate: viewDate,
					userId: userId,
					userName: userName,
				});
				updateState({
					viewDate: viewDate
				});
				dispatch(PREVIEW_FETCH_STARTED, {
					articleNumber: state.properties.parentNumber,
					userId: userId,
					viewOnDate: viewDate
				});
			}
		} else {
			updateState({selectedValue: {}});
		}
		dispatch(IMPERSONATE_BLOCKS);
	} else if (action.payload.key === 'hidden-blocks-modal') {
		updateState({
			showHiddenBlocks: false,
			hiddenBlocks: []
		});
	}
};

const handleModalValueChange = (coeffects) => {
	const {action, state, updateState} = coeffects;
	if (action.payload.name === "viewAs") {
		updateState({selectedValue: {
			...state.selectedValue,
			userId: action.payload.value,
			userName: action.payload.displayValue
		}});
	} else if (action.payload.name === "viewOn") {
		updateState({selectedValue: {
			...state.selectedValue,
			viewDate: action.payload.value
		}});
	}
};

const handleShowBlocksModal = (coeffects) => {
	const {action, updateState} = coeffects;
	updateState({
		showHiddenBlocks: true,
		hiddenBlocks: action.payload.hiddenBlocks
	});
};

const handleHiddenBlocks = (state, dispatch, impersonatedContentHTML, hasViewAccess) => {
	const parentDiv = document.createElement('div');
	parentDiv.innerHTML = impersonatedContentHTML;
	let hiddenBlocks = [];
	state.blocks.forEach(block => {
		const kbBlock = parentDiv.querySelector('.' + block.number);
		if (!kbBlock) {
			hiddenBlocks.push(block);
		}
	});

	const hiddenBlocksDiv = hasViewAccess && hiddenBlocks.length > 0 ?
		<div>
			<a href="javascript:void(0)" on-click={() => dispatch(SHOW_BLOCKS_MODAL, {
			hiddenBlocks: hiddenBlocks
		})}>{t('See unavailable blocks.')}</a>
		</div> : '';

	dispatch(SHOW_MESSAGE, {
		action: {
			type: "dismiss"
		},
		iconName: "circle-info-outline",
		status: "info",
		content: {
			type: 'jsx',
			value: <div>
				<div>{t('You are viewing article as ' + state.properties.userName + '.')}</div>
				{hiddenBlocksDiv}
			</div>
		}
	});
};

const componentRendered = ({ host }) => {
	let content = host.shadowRoot.querySelector("article");
	if(content){
		content.querySelectorAll("a").forEach(link => {
			if(link.href.includes("#")){
				let targetID = link.href.split("#")[1];
				link.onclick = () => {
					var target = content.querySelector("#"+targetID) ? content.querySelector("#"+targetID) : content.querySelector("[name="+targetID+"]");
					if(target) {
						target.scrollIntoView({
							behavior: 'smooth',
							block: 'start'
						});
						return false;
					}
				};
			}
		});
	}
}


const createNoAccessContent = (state) => {
	const parentDiv = document.createElement('div');
	const headerDiv = document.createElement('div');
	headerDiv.innerHTML = t('Access denied');
	headerDiv.classList.add('kb-no-access-header');
	parentDiv.appendChild(headerDiv);
	const childDiv = document.createElement('div');
	childDiv.innerHTML =  t('Based on user criteria definitions, ') + state.properties.userName
		+ t(' does not have access to this knowledge article,') + '<br/>'
		+ t('unless explicitly granted using ACL rules.');
	parentDiv.appendChild(childDiv);
	return parentDiv.innerHTML;
};

export const actionHandlers = {
	[BLOCK_FETCH_STARTED]: fetchKnowledgeBlocksEffect,
	[COMPONENT_RENDERED]: componentRendered,
	[BLOCK_FETCH_SUCCEEDED]: handleKnowledgeBlocksFetchSucceeded,
	[PREVIEW_FETCH_STARTED]: fetchKnowledgePreviewEffect,
	[PREVIEW_FETCH_SUCCEEDED]: handleKnowledgePreviewFetchSucceeded,
	[NOW_MODAL_FOOTER_ACTION_CLICKED]: handleImpersonateBlocks,
	[NOW_MODAL_OPENED_SET]: handleImpersonateBlocks,
	[NOW_RECORD_DATE_PICKER_VALUE_CHANGED]: handleModalValueChange,
	[NOW_RECORD_FIELD_LOOKUP_VALUE_CHANGED]: handleModalValueChange,
	[SHOW_BLOCKS_MODAL]: handleShowBlocksModal
};
