import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {
	LOAD_ATTACHMENT_SUCCEEDED,
	LOAD_ATTACHMENT_FAILED,
	LOAD_CLASSNAME_SUCCEEDED,
	LOAD_CLASSNAME_FAILED,
	PREVIEW_RECORD} from './actions';

const AttachmentGraphQLQuery = `
query ($table: String!, $sysId: String!) {
    query: GlideAttachmentQuery_Query {
        model: attachments(table: $table, sysId: $sysId) {
            attachments: records{
                name
                userId
                userName
                date
                dateLabel
                type
                size
                sysId
            }
            types
            extensions
        }
    }
}
`;

const ClassNameQuery = `
query ($sysId: String) {
    GlideRecord_Query {
        kb_knowledge(sys_id: $sysId) {
            _results {
            sys_class_name {
            value
            }
        }
        }
    }
    }
`;


// function keyPressHandler(e, itemId) {
// 		if(e.key == 'Enter') {
// 				downloadAttachment(itemId);
// 		}
// }

const fetchAttachmentEffect = createGraphQLEffect(AttachmentGraphQLQuery, {
	successActionType: LOAD_ATTACHMENT_SUCCEEDED,
	errorActionType: LOAD_ATTACHMENT_FAILED, 
	variableList: ['sysId', 'table']
});

const fetchClassnameEffect = createGraphQLEffect(ClassNameQuery, {
	successActionType: LOAD_CLASSNAME_SUCCEEDED,
	errorActionType: LOAD_CLASSNAME_FAILED, 
	variableList: ['sysId']
});

export const actions = {
	LOAD_ATTACHMENT: {private: true},
	LOAD_CLASSNAME: {private: true}
};

export const actionHandlers = {
	LOAD_ATTACHMENT: fetchAttachmentEffect,
	LOAD_CLASSNAME: fetchClassnameEffect,
	LOAD_ATTACHMENT_SUCCEEDED: {
		effect : ({action, updateState}) => updateState({
			graphQL: action.payload.data
		}),
		stopPropagation: true
	}, 
	LOAD_ATTACHMENT_FAILED: {
		effect : ({updateState}) => updateState({
			...state
		}),
		stopPropagation: true
	},
	LOAD_CLASSNAME_FAILED: {
		effect : ({updateState}) => updateState({
			...state
		}),
		stopPropagation: true
	},
	LOAD_CLASSNAME_SUCCEEDED: {
		effect : ({updateState,action}) => updateState({
			classNameGraphQl: action.payload.data
		}),
		stopPropagation: true
	},
	DOWNLOAD_ATTACHMENT: {
		effect: ({ action, properties, dispatch }) => {
			var id = action.payload.sysId;
				let location = `/sys_attachment.do?sys_id=${id}`;
				if (properties.docviewerEnabled) {
					dispatch(PREVIEW_RECORD, {
						table: "kb_knowledge",
						sys_id: id,
						params: { mode: "docviewer", title: action.payload.name },
					});
				} else window.location.assign(location);
		}
	}
};