import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import {actionHandlers} from './actions';
import { t } from 'sn-translate';

const knowledgeCategoryPicker = 'sn-kb-category-picker';

createCustomElement(knowledgeCategoryPicker, {
	renderer: {
		type: snabbdom,
		view
	},

	initialState: {
		items: [],
		opened: true,
		expandedItems: [],
		selectedItems:[],
		loading: true,
		loadingItems:[],
		kbKnowledgeBase: '',
		errorMessage: t('No categories found')
	},
	properties: {
		recordSysId: { default: '' },
		serializedChanges: { default: '{}' },
		value: { default: '' },
		kbKnowledgeBase: { default: '' },
		encodedRecord: { default: '' }
	},
	actionHandlers: actionHandlers
});