import '@servicenow/now-modal';
import { t } from 'sn-translate';

export const view = (state, {updateState}) => {
    let choices = state.properties.choiceList.map((obj) => {
        return {'id':obj.value, 'label':obj.label}
    });

    const content = (
        <div className="kb-feedback-modal">
            <div className="kb-feedback-choice">
             <div className="kb-feedback-label">{t("Select a reason")}</div>
                <now-dropdown bare items={choices} panel-fit-props='{}' select="single" selected-items={[state.reason]} size="md" variant="secondary"></now-dropdown>
            </div>
            <div className="kb-feedback-comment">
                <div className="kb-feedback-label">{t("Additional details")}</div>
                <textarea id="detailsComment" className="kb-feedback-comment-text" rows="4" value={state.comments} on-input={(e) => { e.stopPropagation(); updateState({ comments: e.target.value}) }} />
            </div>
        </div>
    );
    return (
        <now-modal
            size="md"
            manage-opened
            opened={state.properties.openModal}
            header-label={t("Help us improve this article")}
            footer-actions={[
                {
                    variant: "primary",
                    label: t('Submit'),
                    id: "submit_ok"
                },
                {
                    variant: "secondary",
                    label: t("No thanks"),
                    id: "submit_cancel"
                }
            ]}>{content}</now-modal>
    );
};
export default view;