import {createHttpEffect} from '@servicenow/ui-effect-http';
import {LOAD_COMMUNITYBLOG, LOAD_COMMUNITYBLOG_FAILED, LOAD_COMMUNITYBLOG_SUCCEEDED, SET_STATE} from './actions';

export const actionHandlers = {
	[LOAD_COMMUNITYBLOG]: createHttpEffect(
		'/api/sn_communities/v1/community/contents/:id', 
		{
			successActionType: LOAD_COMMUNITYBLOG_SUCCEEDED,
			errorActionType: LOAD_COMMUNITYBLOG_FAILED,
			pathParams: ['id'],
			queryParams: ['content_type']
		}
	), 
	[LOAD_COMMUNITYBLOG_SUCCEEDED]: {
		stopPropagation: true,
		effect: ({action, updateState}) => updateState({
		communityBlog: action.payload.result
	})},
	[LOAD_COMMUNITYBLOG_FAILED]: {
		stopPropagation: true,
		effect: ({state,updateState}) => updateState({
		...state
		})
	}, 
	[SET_STATE]: {
		stopPropagation: true,
		effect: ({action, updateState}) => updateState({
			articleContent: action.payload.articleContent
		})
	}
}; 