const addDiffIndicationTags = (diffString, operation) => {
    if(operation === 0)
        return diffString;

    const closeTag = text => operation === -1? `${text}</del>` : `${text}</ins>`;
    const openTag = text => operation === 1? `${text}<ins>` : `${text}<del>`;

    let tagOpen = true;
    let outputText = openTag('');
    for(let i=0; i< diffString.length; i++){
        if(diffString.charCodeAt(i) < 0xE000) {
            if(!tagOpen) {
                outputText = openTag(outputText);
                tagOpen = true;
            }
        } else {
            if(tagOpen) {
                outputText = closeTag(outputText);
                tagOpen = false;
            }
        }
        outputText += diffString[i];
    }
    if(tagOpen) {
        outputText = closeTag(outputText);
    }
    return outputText;
};

const convertHtmlToEncodedString = (htmlText, tagMap) => {
    htmlText = htmlText.replace(/&nbsp;/g, tagMap.get('&nbsp;'));
    let diffableString = '';
    let offset = 0;
    while (offset < htmlText.length) {
        let tagStart = htmlText.indexOf('<', offset);
        if (tagStart < 0) {
            diffableString += htmlText.substr(offset);
            break;
        }
        else {
            var tagEnd = htmlText.indexOf('>', tagStart);
            if (tagEnd < 0) {
                // Invalid HTML
                // Truncate at the start of the tag
                diffableString += htmlText.substr(offset, tagStart - offset);
                break;
            }
            let tagString = htmlText.substr(tagStart, tagEnd + 1 - tagStart);
            // Is this tag already mapped?
            let unicodeCharacter = tagMap.get(tagString);
            if (!unicodeCharacter) {
                // Nope, need to map it
                unicodeCharacter = String.fromCharCode(0xE000 + tagMap.size/2);
                tagMap.set(tagString, unicodeCharacter);
                tagMap.set(unicodeCharacter, tagString);
            }
            // At this point it has been mapped, so now we can use it
            diffableString += htmlText.substr(offset, tagStart - offset);
            diffableString += unicodeCharacter;
            offset = tagEnd + 1;
        }
    }
    return diffableString;
};

const convertEncodedStringToHtml = (text, tagMap) => {
    let htmlString = '';
    for (let x = 0; x < text.length; x++) {
        let charCode = text.charCodeAt(x);
        if (charCode < 0xE000) {
            htmlString += text[x];
            continue;
        }
        let tagString = tagMap.get(text[x]);
        if (!tagString) {
            // We somehow have a character that is above our range but didn't map
            htmlString += text[x];
        }
        else {
            htmlString += tagString;
        }
    }
    return htmlString;
};

const findDifferences = (baseContent, compareContent, isHtml, tagMap) => {
    const unicodeChar = String.fromCharCode(0xE000);
    tagMap.set('&nbsp;', unicodeChar);
    tagMap.set(unicodeChar, '&nbsp;');

    const baseString = isHtml? convertHtmlToEncodedString(baseContent, tagMap) : baseContent;
    const compareString = isHtml? convertHtmlToEncodedString(compareContent, tagMap) : compareContent;

    const dmp = new diff_match_patch();
    let diffs = dmp.diff_main(baseString, compareString);
    dmp.diff_cleanupSemantic(diffs);

    return diffs;
};

export const getDiffContent = (baseContent='', compareContent='', isHtml, keepSpacing) => {
    const tagMap = new Map();
    let diffs = findDifferences(baseContent || '', compareContent || '', isHtml, tagMap);

    const originalContent = diffs.reduce((output, diff) => {
        let taggedString;
        if(diff[0] === -1 || diff[0] === 0 || keepSpacing){
            taggedString = addDiffIndicationTags(diff[1], diff[0]);
            return output + convertEncodedStringToHtml(taggedString, tagMap);
        } else {
            return output;
        }
    }, '');

    const changedContent = diffs.reduce((output, diff) => {
        let taggedString;
        if(diff[0] === 1 || diff[0] === 0 || keepSpacing){
            taggedString = addDiffIndicationTags(diff[1], diff[0]);
            return output + convertEncodedStringToHtml(taggedString, tagMap);
        } else {
            return output;
        }
    }, '');

    return {
        originalContent,
        changedContent
    };
};

