import { t } from 'sn-translate';
import '@servicenow/now-dropdown';
import {_aricleVersionChanged, _aricleLanguageChanged} from '../utils/actionUtil';

const getRevisionString = (articleResult, versioningInstalled) => {
    if(versioningInstalled && (articleResult.revisedBy || articleResult.baseVersion)){
        return t('Revised by {0}',articleResult.revisedBy);
    } else {
        return t('Authored by {0}', articleResult.author);
    }
};

export default (props) => {
    const { articleResult, article_attributes } = props.payload;
    let versionList = [];
    let currentVersion = [];
    if (article_attributes.versionDetails && article_attributes.versionDetails.showHistory && article_attributes.versionDetails.history.length)
        versionList = article_attributes.versionDetails.history.map(ver => {
			if (article_attributes.selectedSysId) {
				if (ver.sysId === article_attributes.selectedSysId)
					currentVersion.push(ver.sysId);
			} else {
				if (ver.sysId === articleResult.sysId)
					currentVersion.push(ver.sysId);
			}
            return { "id": ver.sysId, "label": ver.versionNumber, "action": _aricleVersionChanged};
        });

    let languageList = [];
    let currentLang = [];
    if (article_attributes.languageDetails && article_attributes.languageDetails.length > 1)
        languageList = article_attributes.languageDetails.map(lang => {
            if (lang.selected) currentLang.push(lang.sysId);
            return { "id": lang.sysId, "label": lang.label, "action": _aricleLanguageChanged};
        });

    return (
        <div className="sn-card-footer kb-footer">
            <div className="kb-footer-left">
                <div className="sn-card-content kb-meta">
                    <div className="sn-meta-list-item">
                        <span>{getRevisionString(articleResult, article_attributes.versionDetails.isInstalled)}</span>
                    </div>
                    <span className="kb-meta-pipe">|</span>
                    <div className="sn-meta-list-item">
                        <span>{articleResult.number}</span>
                    </div>
                </div>
            </div>
            <div className="kb-footer-right">
                <div className="sn-card-content kb-meta">
                    {versionList.length ?
                        <div className="sn-meta-list-item">
                            <div className="kb-meta-label">{t('Version')} </div>
                            <now-dropdown bare items={versionList} select="none" selected-items={currentVersion} size="md" variant="secondary-selected"/>
                        </div> : ""
                    }
                    {(languageList.length && versionList.length) ?
                        <span className="kb-meta-pad"></span> : ""
                    }
                    {languageList.length ?
                        <div className="sn-meta-list-item">
                            <div className="kb-meta-label">{t('Language')}</div>
                            <now-dropdown bare items={languageList} select="none" selected-items={currentLang} size="md" variant="secondary-selected"/>
                        </div> : ""
                    }
                </div>
            </div>
        </div>
    )
}
