import {
	LOAD_KNOWLEDGE,
	LOAD_KNOWLEDGE_SUCCEEDED,
	LOAD_KNOWLEDGE_FAILED,
	UPDATE_HELPFUL,
	UPDATE_HELPFUL_SUCCEEDED,
	UPDATE_HELPFUL_FAILED,
	FIELD_COLLAPSED,
	TEXT_LINK_CLICKED,
	BUTTON_CLICKED,
	DROPDOWN_CLICKED,
	FLAG_MODAL_RESET,
	FEEDBACK_MODAL_RESET,
	SHOW_MESSAGE,
	UPDATE_FEEDBACK_RATING,
	MESSAGES,
	PREVIEW_RECORD,
	UPDATE_HELPFUL_COUNT,
	SELECT_BLOCKS,
	IMPERSONATE_BLOCKS,
	ARTICLE_VIEW_ACCESS,
	VERSION_CHANGE
} from './constants';
import {t} from 'sn-translate';
import { get } from 'lodash'
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { knowledge_read,article_metadata } from './queries/knowledge.read';
import { update_helpful } from './queries/feedback.update';
import { processArticleAction, performVersionCheck } from './utils/actionUtil'

const fetchKnowledgeEffect = createGraphQLEffect(knowledge_read, {
	successActionType: LOAD_KNOWLEDGE_SUCCEEDED,
	errorActionType: LOAD_KNOWLEDGE_FAILED,
	variableList: ['sys_id', 'ts_query_kb_id', 'top_click_rank']
});

const updateHelpfulEffect = createGraphQLEffect(update_helpful, {
	successActionType: UPDATE_HELPFUL_SUCCEEDED,
	errorActionType: UPDATE_HELPFUL_FAILED,
	variableList: ['articleId', 'helpful']
});

export const actions = {
	[LOAD_KNOWLEDGE]: {
		private: true
	},
	[UPDATE_HELPFUL]: {
		private: true
	}
};

export const actionHandlers = {
	[LOAD_KNOWLEDGE]: fetchKnowledgeEffect,
	[LOAD_KNOWLEDGE_SUCCEEDED]: {
		effect: (coeffects) => {
			let result = coeffects.action.payload.data;
			coeffects.updateState({
				kbArticle: result,
				helpfulData: {
					totalUsefulCount: result.AppKnowledge_Query.getArticleDetails.totalUsefulCount,
					usefulYesCount: result.AppKnowledge_Query.getArticleDetails.usefulYesCount
				}
			})
			performVersionCheck(coeffects);
			const sys_id = result.AppKnowledge_Query.getArticleDetails.articleSysId;
			const {infoMessage, errorMessage} = result.AppKnowledge_Query.getArticleDetails;
			if(result.AppKnowledge_Query.getArticleDetails.articleInfo.isExpired){
				coeffects.dispatch(SHOW_MESSAGE, {
					action: {
						type: "dismiss"
					},
					status: "info",
					content: {
						type: 'jsx',
						value: <div>{t('Article expired. Update its ')}<i>{t('Valid to')}</i>{t(' date to make it available to users.')}</div>
					},
				});
			}
			if(infoMessage){
				coeffects.dispatch(SHOW_MESSAGE, {
					action: {
						type: "dismiss"
					},
					status: "info",
					content: infoMessage
				});
			}
			if(errorMessage) {
				coeffects.dispatch(SHOW_MESSAGE, {
					action: {
						type: "dismiss"
					},
					status: "critical",
					content: errorMessage
				});
			}
			if(!sys_id) return;
		},
		stopPropagation: true
	},
	[LOAD_KNOWLEDGE_FAILED]: {
		effect:({updateState})=>{
			updateState({

			})
		},
		stopPropagation: true
	},
	[BUTTON_CLICKED]: {
		effect: (coeffects) => processArticleAction(coeffects),
		stopPropagation: true
	},
	[DROPDOWN_CLICKED]: {
		effect: (coeffects) => processArticleAction(coeffects),
		stopPropagation: true
	},
	[TEXT_LINK_CLICKED]: {
		effect: ({ dispatch, action }) => {
			const {
				payload: { table, sys_id }
			} = action;
			dispatch(PREVIEW_RECORD, { 'table': table, 'sys_id': sys_id, params: { mode: 'view' } });
		},
		stopPropagation: true
	},
	[FIELD_COLLAPSED]: createUpdateStateEffect((state, action) => {
		state.kbArticle.AppKnowledge_Query.getArticleDetails.templateFieldData.forEach(function (item) {
			if (item.column == action.payload.column) {
				item.collapsed = !item.collapsed;
			}
		});
		return { ...state };
	}),
	[FLAG_MODAL_RESET]: {
		effect: ({ updateState }) => {
			updateState({
				openFlagModal: false
			})
		},
		stopPropagation: true
	},
	[FEEDBACK_MODAL_RESET]: {
		effect: ({ updateState }) => {
			updateState({
				openFeedbackModal: false
			})
		},
		stopPropagation: true
	},
	[UPDATE_FEEDBACK_RATING]: {
		effect: ({ state, action, updateState }) => {
			let rating = action.payload.rating;
			updateState({
				rating: rating + '',
                helpful: '',
				openFeedbackModal: true
			});
		}
	},
	[UPDATE_HELPFUL]: updateHelpfulEffect,
	[UPDATE_HELPFUL_SUCCEEDED]: {
		effect: ({ dispatch, action }) => {
			if (get(action,"payload.data.GlideRecord_Mutation.insert_kb_feedback","")) {
				dispatch(SHOW_MESSAGE, {
					status: "info",
					iconName: "circle-info-outline",
					content: MESSAGES.THANK_YOU,
					action: { type: "dismiss" }
				});
				let isHelpful = get(action, "meta.options.variables.helpful", "yes") == "yes" ? true : false;
				dispatch(UPDATE_HELPFUL_COUNT, { isHelpful })
			}
			else
				dispatch(SHOW_MESSAGE, {
					status: "critical",
					iconName: "circle-info-outline",
					content: MESSAGES.LIMIT_REACHED_MESSAGE,
					action: { type: "dismiss" }
				});
		},
		stopPropagation: true
	},
	[UPDATE_HELPFUL_FAILED]: {
		effect: ({ dispatch }) => {
			dispatch(SHOW_MESSAGE, {
				status: "critical",
				iconName: "circle-info-outline",
				content: MESSAGES.LIMIT_REACHED_MESSAGE,
				action: { type: "dismiss" }
			});
		},
		stopPropagation: true
	},
	[UPDATE_HELPFUL_COUNT]: {
		effect({ action, updateState, state: { helpfulData} }) {
			let usefulYesCount = get(action, "payload.isHelpful", true) ? helpfulData.usefulYesCount + 1 : helpfulData.usefulYesCount
			updateState({
				helpfulData: {
					usefulYesCount,
					totalUsefulCount: helpfulData.totalUsefulCount + 1
				}
			})
		},
		stopPropagation: true
	},
	[SELECT_BLOCKS]: {
		effect({ state, updateState }) {
			updateState({blocksSelected: !state.blocksSelected})
		}
	},
	[IMPERSONATE_BLOCKS]: {
		effect({ state, updateState }) {
			updateState({blocksImpersonated: !state.blocksImpersonated})
		},
		stopPropagation: true
	},
	[ARTICLE_VIEW_ACCESS]: {
		effect({ action, updateState }) {
			const hasViewAccess = action.payload.hasViewAccess;
			updateState({hasViewAccess: hasViewAccess});
		},
		stopPropagation: true
	},
	[VERSION_CHANGE]: {
		effect({ action, updateState }) {
			const selectedSysId = action.payload.selectedSysId;
			updateState({selectedSysId: selectedSysId});
		},
		stopPropagation: true
	}
};
