import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {actionTypes} from '@servicenow/ui-core';
import {READY_TO_FETCH, DATA_LOADED} from './constants';
import { t } from "sn-translate";

const {COMPONENT_BOOTSTRAPPED} = actionTypes;
const query = `
query($table: String!, $query: String!, $types: [String!], $exclude: [String!]){
    GlideRecord_Query {
        kb_version (queryConditions:$query){
            _results{
                knowledge{
                    value
                }
                snapshot{
                    value
                }
                version{
                    value
                }
                _query{
                    AppKnowledge_Query{
                        getArticleByFieldTypes(table:$table, sys_id:"$$parent.knowledge$$", snapshot:"$$parent.snapshot$$", types:$types, exclude: $exclude){
                            name,
                            type,
                            label,
                            value
                        }
                    }
                }
            }
        }
    }
}
`;

export const actions = {
    [READY_TO_FETCH]: {
        private: true
    }
};

export const actionHandlers = {
    [COMPONENT_BOOTSTRAPPED]: ({dispatch, properties:{table,firstVersion,secondVersion}}) => {
        setTimeout(() => dispatch("CONTENT_UPDATED", {
            title: t("Compare versions")
        }), 500);
        dispatch(READY_TO_FETCH, {
            table,
            query: `sys_idIN${firstVersion},${secondVersion}`,
            types: [
                "string",
                "html",
                "translated_html"
            ],
            exclude: [
                "article_id",
                "language",
                "topic",
                "number",
                "meta",
                "meta_description",
                "display_number",
                "sys_created_by",
                "sys_updated_by",
                "description",
                "category",
                "article_type"
            ]
        });
    },
    [READY_TO_FETCH]: createGraphQLEffect(query, {
        variableList: ['table','query','types','exclude'],
        successActionType: DATA_LOADED
    }),
    [DATA_LOADED]: {
        effect : ({action: {payload}, updateState, dispatch}) => {
            const {
                data: {
                    GlideRecord_Query: {
                        kb_version: {
                          _results: [
                                version1,
                                version2
                            ]
                        }
                    }
                }
            } = payload;
    
            let original, changed;
            let originalVersionNumber, changedVersionNumber;
            const versionNumber1 = parseFloat(version1.version.value);
            const versionNumber2 = parseFloat(version2.version.value)
            const getArticleData = version => version._query.AppKnowledge_Query.getArticleByFieldTypes;
    
            if(versionNumber1 < versionNumber2){
                originalVersionNumber = versionNumber1;
                changedVersionNumber = versionNumber2;
                original = getArticleData(version1);
                changed = getArticleData(version2);
            } else {
                originalVersionNumber = versionNumber2;
                changedVersionNumber = versionNumber1;
                original = getArticleData(version2);
                changed = getArticleData(version1);
            }
            
            const changedContentMap = new Map();
            changed.forEach(({name,value}) => changedContentMap.set(name,value));
    
            const comparisonData = original.map(({name,value,type,label}) => ({
                name,
                contentType: type==='translated_html'?'html':type,
                heading: label,
                originalContent: value,
                changedContent: changedContentMap.get(name)
            })).filter(({originalContent,changedContent}) => originalContent || changedContent);
            
            updateState({
                originalVersionNumber,
                changedVersionNumber,
                comparisonData
            });
        },
        stopPropagation: true
    } 
};