import { get, has, assign, isEmpty, isNaN } from "lodash";
import { actionTypes } from "@servicenow/ui-core";
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import { buttons, commentActions, metaData } from "./data";
import { createAmbSubscriptionEffect } from "@servicenow/ui-effect-amb";
import {
	RECORD_WATCHER_UPDATED,
	RW_ACTIONS,
	NOW_BUTTON_BARE_CLICKED,
	HTML_EDITOR_ACTION_PERFORMED,
	COMMENTS_FETCH_SUCCESS,
	COMMENTS_FETCH_FAILURE,
	FETCH_COMMENTS,
	REPLY_TO_COMMENT,
	NOW_BUTTON_ICONIC_CLICKED,
	NOW_MODAL_FOOTER_ACTION_CLICKED,
	LIKE_COMMENT,
	UNLIKE_COMMENT,
	DELETE_COMMENT,
	COMMENT_INSERT_SUCCESSFUL,
	COMMENT_INSERT_FAILED,
	LIKE_COMMENT_SUCCESSFUL,
	LIKE_COMMENT_FAILED,
	UNLIKE_COMMENT_SUCCESSFUL,
	UNLIKE_COMMENT_FAILED,
	DELETE_COMMENT_SUCCESSFUL,
	DELETE_COMMENT_FAILED,
	CHECK_FOR_CHILD_COMMENTS,
	SHOW_REPLY_BUTTON_CLICKED,
	HIDE_REPLY_BUTTON_CLICKED,
	NOW_MODAL_OPENED_SET,
	TOGGLE_DISPLAY_ALL_COMMENTS,
	SHOW_MESSAGE,
	MESSAGES,
	NOW_BUTTON_CLICKED,
	SHOW_MORE_COMMENTS,
	NOW_RECORD_HTML_EDITOR_STAGED_VALUE_CHANGED,
	NOW_RECORD_HTML_EDITOR_VALUE_CHANGED
} from "./constants";
import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import {
	commentsQuery,
	addCommentQuery,
	addLikesQuery,
	deleteLikesQuery,
	deleteCommentQuery
} from "./queries";
const recordWatcher = createAmbSubscriptionEffect(
	"/rw/default/:table/:filter",
	{
		subscribeStartedActionType: RW_ACTIONS.SUBSCRIPTION_STARTED,
		subscribeSucceededActionType: RW_ACTIONS.SUBSCRIPTION_SUCCEEDED,
		subscribeFailedActionType: RW_ACTIONS.SUBSCRIPTION_FAILED,
		messageReceivedActionType: RW_ACTIONS.MESSAGE_RECEIVED,
		unsubscribeSucceededActionType: RW_ACTIONS.SUBSCRIPTION_UNSUBSCRIBED
	}
);

var done = false;
var itemPath = [];

const getPath = (items, pid, level) => {
	for (let i = 0; i < items.length; i++) {
		if (!done) {
			itemPath.length = level;
			if (items[i].id == pid) {
				itemPath.push(i);
				done = true;
				return;
			} else {
				if (items[i].children && items[i].children.length > 0) {
					itemPath.push(i);
					getPath(items[i].children, pid, level + 1);
				}
			}
		} else return;
	}
};

const getPathinString = (path, key) => {
	let actualPath = "items";
	for (let i = 0; i < path.length - 1; i++) {
		actualPath += `[${path[i]}].children`;
	}
	if (path.length != 0 && !isEmpty(key))
		actualPath += `[${path[path.length - 1]}].${key}`;
	else if (path.length != 0 && isEmpty(key))
		actualPath += `[${path[path.length - 1]}]`;
	return actualPath;
};

const buildTree = comments => {
	var commentsTree = {};
	try {
		comments.forEach(x => {
			if (has(commentsTree, x.id)) {
				assign(commentsTree[x.id], x);
			} else {
				commentsTree[x.id] = x;
				commentsTree[x.id].children = [];
			}

			if (has(commentsTree, x.parent))
				commentsTree[x.parent].children.push(commentsTree[x.id]);
			else {
				commentsTree[x.parent] = {
					children: [commentsTree[x.id]]
				};
			}
		});
		return commentsTree;
	} catch (e) {
		return commentsTree;
	}
};

const getParentComment = (cmArray, parentId) => {
	newCommentParent = {};
	for (var i = 0; i < cmArray.length; i++) {
		if (cmArray[i] && cmArray[i].id == parentId) {
			newCommentParent = cmArray[i];
			return true;
		} else {
			if (cmArray[i].children.length)
				getParentComment(cmArray[i].children, parentId);
			if (has(newCommentParent, "id")) return true;
		}
	}

	return false;
};

const hasChildren = items => {
	for (let i = 0; i < items.length; i++) {
		if (get(items[i], "children.length", 0) > 0) {
			return true;
		}
	}
	return false;
};

const extractContent = (str) => {
    return (new DOMParser).parseFromString(str, "text/html").documentElement.textContent.toString().trim();
}

export const actions = {
	[RW_ACTIONS.SUBSCRIPTION_STARTED]: {
		private: true
	},
	[RW_ACTIONS.SUBSCRIPTION_SUCCEEDED]: {
		private: true
	},
	[RW_ACTIONS.SUBSCRIPTION_FAILED]: {
		private: true
	},
	[RW_ACTIONS.SUBSCRIPTION_UNSUBSCRIBED]: {
		private: true
	},
	[NOW_BUTTON_CLICKED]: {
		private: true
	}
};

export const actionHandlers = {
	[NOW_BUTTON_ICONIC_CLICKED]: {
		effect({ action, updateState }) {
			if (action.payload.attributes.meta == "delete") {
				let message = MESSAGES.DELETE_WARNING_1;
				if (action.payload.attributes.hasFeedback)
					message = MESSAGES.DELETE_WARNING_2;
				let deleteCommentData = {
					message: message,
					commentId: action.payload.itemId
				};
				updateState({
					deleteModalOpened: true,
					deleteCommentData: deleteCommentData
				});
			}
		},
		stopPropagation: true
	},

	[NOW_MODAL_FOOTER_ACTION_CLICKED]: {
		effect({ action, updateState, properties, dispatch, state }) {
			if (action.payload.action.meta == "delete")
				dispatch(DELETE_COMMENT, {
					commentId: state.deleteCommentData.commentId,
					articleId: properties.articleId
				});

			updateState({
				deleteModalOpened: false
			});
		},
		stopPropagation: true
	},

	[HTML_EDITOR_ACTION_PERFORMED]: {
		effect: ({ action, dispatch, updateState, properties }) => {
			if (action.payload.action == "cancel") {
				updateState({
					path: "currentEditorId",
					value: "",
					operation: "set"
				});
			}
			if (action.payload.action == "submit") {
				if (!isEmpty(action.payload.text)) {
					if (action.payload.type == "reply_comment") {
						dispatch(REPLY_TO_COMMENT, {
							user: properties.user.id,
							comments: action.payload.text,
							article: properties.articleId,
							parent_comment: action.payload.itemId
						});
						updateState({
							path: "currentEditorId",
							value: "",
							operation: "set"
						});
					} else if (action.payload.type == "new_comment") {
						dispatch(REPLY_TO_COMMENT, {
							user: properties.user.id,
							comments: action.payload.text,
							article: properties.articleId,
							parent_comment: ""
						});
						updateState({
							value: "",
							currentEditorId: ""
						});
					}
				}
			}
		},
		stopPropagation: true
	},

	[COMPONENT_BOOTSTRAPPED]: {
		effect({  updateState, dispatch, properties, updateProperties }) {
			let { commentsThreshold } = properties;

			if (!isEmpty(properties.articleId) && properties.user != null) {
				let maxComments = (isNaN(commentsThreshold) || commentsThreshold < 0) ? 0 : parseInt(commentsThreshold);
				
				let user = {
					name: properties.user.fullName,
					id: properties.user.userID,
					avatar: properties.user.avatar,
					userLanguage: properties.user.userLanguage
				};
				updateProperties({
					commentsThreshold: maxComments,
					user
				});

				updateState({
					metaData: metaData,
					actions: commentActions(user, properties),
					buttons: buttons
				});

				if (maxComments > 0 && properties.hasComments)
					dispatch(FETCH_COMMENTS, {
						id: properties.articleId,
						getRootComments: true
					});

				const table = `kb_feedback`;
				const filter = btoa(
					`commentsISNOTEMPTY^flagged=false^ratingISEMPTY^useful=^article=${properties.articleId}^user!=${user.id}`
				).replace(/=/g, "-");
				const recordWatcherSubscription = subscribe => {
					dispatch(RECORD_WATCHER_UPDATED, {
						table,
						filter,
						subscribe
					});
				};
				recordWatcherSubscription(true);
			}
		}
	},

	[COMMENTS_FETCH_SUCCESS]: {
		effect: ({ action, dispatch, state, updateState, properties }) => {			
			let comments = get(
				action,
				"payload.data.AppKnowledge_Query.getComments",
				[]
			);
			if (state.items.length > 0 && comments.length > 0) {
				let tree = buildTree(comments);
				let tempItems = JSON.parse(JSON.stringify(state.items));
				for (let i = 0; i < tempItems.length; i++) {
					let id = tempItems[i].id;
					if (has(tree, id)) {
						tempItems[i].children = tree[id].children;
					} else {
						tempItems[i].children = [];
					}
				}
				updateState({
					items: tempItems,
					hasChildren: true
				});
			} else {
				if (comments.length > 0) {
					comments.forEach(c => {
						c.children = [];
					});
					let commentsToDisplay = 0;
					if (comments.length > properties.commentsThreshold) {
						commentsToDisplay = properties.commentsThreshold;
					} else {
						commentsToDisplay = comments.length;
					}
					updateState({
						items: comments,
						commentsToDisplay: commentsToDisplay
					});
					dispatch(FETCH_COMMENTS, {
						id: properties.articleId,
						getRootComments: false
					});
				}
			}
		},
		stopPropagation: true
	},

	[FETCH_COMMENTS]: createGraphQLEffect(commentsQuery, {
		variableList: ["id", "getRootComments"],
		successActionType: COMMENTS_FETCH_SUCCESS,
		errorActionType: COMMENTS_FETCH_SUCCESS,
		batch: false
	}),

	[REPLY_TO_COMMENT]: createGraphQLEffect(addCommentQuery, {
		variableList: ["user", "comments", "article", "parent_comment"],
		successActionType: COMMENT_INSERT_SUCCESSFUL,
		errorActionType: COMMENT_INSERT_FAILED,
		batch: false
	}),

	[LIKE_COMMENT]: createGraphQLEffect(addLikesQuery, {
		variableList: ["commentId", "articleId"],
		successActionType: LIKE_COMMENT_SUCCESSFUL,
		errorActionType: LIKE_COMMENT_FAILED,
		batch: false
	}),

	[UNLIKE_COMMENT]: createGraphQLEffect(deleteLikesQuery, {
		variableList: ["commentId", "articleId"],
		successActionType: UNLIKE_COMMENT_SUCCESSFUL,
		errorActionType: UNLIKE_COMMENT_FAILED,
		batch: false
	}),

	[DELETE_COMMENT]: createGraphQLEffect(deleteCommentQuery, {
		variableList: ["commentId", "articleId"],
		successActionType: DELETE_COMMENT_SUCCESSFUL,
		errorActionType: DELETE_COMMENT_FAILED,
		batch: false
	}),

	[DELETE_COMMENT_SUCCESSFUL]: {
		effect: ({ action, state, updateState, dispatch }) => {
			if (
				get(action, "payload.data.AppKnowledge_Mutation.deleteComment", false)
			) {
				let sysId = get(action, "meta.options.variables.commentId", null);
				if (sysId != null) {
					getPath(state.items, sysId, 0);
					if (done && itemPath.length > 0) {
						done = false;
                        let ind = itemPath.pop();
						updateState({
							operation: "splice",
							value: "",
							path: getPathinString(itemPath, "children"),
							start: ind,
							deleteCount: 1
						});
						dispatch(CHECK_FOR_CHILD_COMMENTS);
						itemPath = [];
						dispatch(SHOW_MESSAGE, {
							status: "info",
							iconName: "circle-info-outline",
							content: MESSAGES.DELETE_SUCCESS,
							action: { type: "dismiss" }
						});
					}
				}
			}
		},
		stopPropagation: true
	},

	[COMMENT_INSERT_SUCCESSFUL]: {
		effect: ({ dispatch, state, action, updateState, properties }) => {
			let data = get(
				action,
				"payload.data.AppKnowledge_Mutation.addComment",
				null
			);
			data = JSON.parse(data);
			if (data && data.sys_id) {
				var commentRecord = {
					children: [],
					createdOn: data.created_on,
					text: data.comment_text,
					id: data.sys_id,
					isLiked: false,
					likesCount: 0,
					user: properties.user
				};
				if (get(data, "parent")) {
					commentRecord.parent = data.parent;
					getPath(state.items, commentRecord.parent, 0);
					if (done && itemPath.length > 0) {
						done = false;
						updateState({
							operation: "unshift",
							value: commentRecord,
							path: getPathinString(itemPath, "children")
						});

						let rootId = state.items[itemPath[0]].id;
						dispatch(SHOW_REPLY_BUTTON_CLICKED, { itemId: rootId })
						if (!state.hasChildren) {
							updateState({
								hasChildren: true,
								expanded: true
							});
						}
						itemPath = [];
					}
				} else {
					commentRecord.parent = "";
					updateState({
						items:[commentRecord,...state.items],
						commentsToDisplay: state.commentsToDisplay + 1
					})
					tinyMCE.activeEditor.setContent("");
				}
				dispatch(SHOW_MESSAGE, {
					status: "info",
					iconName: "circle-info-outline",
					content: MESSAGES.INSERT_SUCCESS,
					action: { type: "dismiss" }
				});
			} else {
				dispatch(COMMENT_INSERT_FAILED);
			}
		},
		stopPropagation: true
	},

	[COMMENT_INSERT_FAILED]: {
		effect({ dispatch }) {
			dispatch(SHOW_MESSAGE, {
				status: "critical",
				iconName: "circle-info-outline",
				content: MESSAGES.INSERT_FAILED,
				action: { type: "dismiss" }
			});
			tinyMCE.activeEditor.setContent("");
		},
		stopPropagation: true
	},

	[CHECK_FOR_CHILD_COMMENTS]: {
		effect({ state, updateState }) {
			let children = hasChildren(state.items);
			if (!children && state.hasChildren)
				updateState({
					hasChildren: false,
					expanded: false
				});
		},
		stopPropagation: true
	},

	[RECORD_WATCHER_UPDATED]: recordWatcher,
	[RW_ACTIONS.MESSAGE_RECEIVED]: {
		effect: ({
			action: {
				payload: { data }
			},
			state,
			updateState,
			dispatch
		}) => {
			if (data && data.operation == "insert") {
				let record = data.record;
				var commentRecord = {
					children: [],
					createdOn: record.sys_created_on.value,
					text: record.comments.value,
					id: data.sys_id,
					isLiked: false,
					likesCount: 0,
					user: {
						name: record.user.display_value,
						id: record.user.value
					}
				};

				if (has(record, "parent_comment")) {
					commentRecord.parent = record.parent_comment.value;
					getPath(state.items, commentRecord.parent, 0);
					if (done && itemPath.length > 0) {
						done = false;
						updateState({
							operation: "unshift",
							value: commentRecord,
							path: getPathinString(itemPath, "children")
						});

						let rootId = state.items[itemPath[0]].id;
						dispatch(SHOW_REPLY_BUTTON_CLICKED, { itemId: rootId})
						if (!state.hasChildren) {
							updateState({
								hasChildren: true,
								expanded: true
							});
						}
						itemPath = [];
					}
				} else {
					commentRecord.parent = "";
					updateState({
						items:[commentRecord,...state.items],
						commentsToDisplay: state.commentsToDisplay + 1
					})
				}
			} else if (data.operation == "delete") {
				getPath(state.items, data.sys_id, 0);
				if (done && itemPath.length > 0) {
					done = false;
					let ind = itemPath.pop();
					updateState({
						operation: "splice",
						value: commentRecord,
						path: getPathinString(itemPath, "children"),
						start: ind,
						deleteCount: 1
					});
					dispatch(CHECK_FOR_CHILD_COMMENTS);
					itemPath = [];
				}
			}
		},
		stopPropagation: true
	},
	[SHOW_REPLY_BUTTON_CLICKED]: {
		effect: ({ action, state, updateState }) => {
			let tempState = {}
			if (state.expandedItems.indexOf(action.payload.itemId) == -1) {
				tempState.expandedItems = [...state.expandedItems, action.payload.itemId];
				updateState(tempState);
			}
		},
		stopPropagation: true
	},
	[HIDE_REPLY_BUTTON_CLICKED]: {
		effect: ({ action, state, updateState }) => {
			let tempArr = [...state.expandedItems];
			let tempState = {}
			let ind = tempArr.indexOf(action.payload.itemId);
			if (ind != -1) {
				tempArr.splice(ind, 1);
			}
			tempState.expandedItems = tempArr;
			updateState(tempState);
		},
		stopPropagation: true
	},

	[NOW_BUTTON_BARE_CLICKED]: {
        effect: ({ action, properties, updateState, dispatch, state }) => {
            let commentId = get(action, "payload.item.id", "") 
			if (get(action, "payload.meta") == "like") {
				if (get(action, "payload.item.isLiked", "") == true) {
					dispatch(UNLIKE_COMMENT, {
						commentId: commentId,
						articleId: properties.articleId
					});
				} else if (get(action, "payload.item.isLiked", "") == false) {
					dispatch(LIKE_COMMENT, {
						commentId: commentId,
						articleId: properties.articleId
					});
				}
			} else if (get(action, "payload.meta", "") == "reply") {
				if (
					isEmpty(state.currentEditorId) ||
					commentId != state.currentEditorId
				) {
					updateState({
						currentEditorId: commentId,
						isEditing: false
					});
				} else {
					updateState({
						currentEditorId: "",
						isEditing: false
					});
				}
			}
		},
		stopPropagation: true
	},

	[NOW_MODAL_OPENED_SET]: {
		effect: ({ action, updateState }) => {
			updateState({
				path: "deleteModalOpened",
				value: action.payload.value,
				operation: "set"
			});
		},
		stopPropagation: true
	},

	[LIKE_COMMENT_SUCCESSFUL]: {
		effect: ({ action, state, updateState }) => {
			let data = get(
				action,
				"payload.data.AppKnowledge_Mutation.likeComment",
				{}
			);
			let commentId = get(action, "meta.options.variables.commentId", null);
			data = JSON.parse(data);
			if (data != null && data.sys_id) {
				getPath(state.items, commentId, 0);
				done = false;
				let path = getPathinString(itemPath, "");
				updateState({
					operation: "set",
					value: get(state, `${path}.likesCount`) + 1,
					path: `${path}.likesCount`
				});

				updateState({
					operation: "set",
					value: true,
					path: `${path}.isLiked`
				});
				itemPath = [];
			}
		},
		stopPropagation: true
	},

	[UNLIKE_COMMENT_SUCCESSFUL]: {
		effect: ({ action, state, updateState }) => {
			let commentId = get(action, "meta.options.variables.commentId");
			let result = get(
				action,
				"payload.data.AppKnowledge_Mutation.unLikeComment",
				null
			);
			if (result && commentId != null) {
				getPath(state.items, commentId, 0);
				done = false;
				let path = getPathinString(itemPath, "");

				updateState({
					operation: "set",
					value: get(state, `${path}.likesCount`) - 1,
					path: `${path}.likesCount`
				});

				updateState({
					operation: "set",
					value: false,
					path: `${path}.isLiked`
				});
				itemPath = [];
			}
		},
		stopPropagation: true
	},

	[TOGGLE_DISPLAY_ALL_COMMENTS]: {
		effect({ updateState, state }) {
			if (state.expanded) {
				updateState({
					expanded: false,
					expandedItems: []
				});
			} else {
				let arr = [];
				state.items.forEach(x => {
					if (x.children.length > 0) 
						arr.push(x.id);
				});
				updateState({
					expanded: true,
					expandedItems: arr
				});
			}
		},
		stopPropagation: true
	},

	[SHOW_MORE_COMMENTS]: {
		effect({ updateState, state: { commentsToDisplay, items }, properties: { commentsThreshold } }) { 
			if (commentsToDisplay + commentsThreshold >= items.length) {
				updateState({
					commentsToDisplay: items.length
				})
			}
			else {
				updateState({
					commentsToDisplay: commentsToDisplay + commentsThreshold
				});
			}
		},
		stopPropagation: true
	},

	[NOW_RECORD_HTML_EDITOR_STAGED_VALUE_CHANGED]: {
		effect({ action, updateState, state }) {
			let payloadValue = extractContent(action.payload.value);
			if(state.value  != action.payload.value && (isEmpty(state.value) || isEmpty(payloadValue)))
				updateState({
					value: payloadValue
				});
		},
		stopPropagation: true
	},

	[NOW_RECORD_HTML_EDITOR_VALUE_CHANGED]: {
		effect({ action, updateState, state }) {
			if(!isEmpty(extractContent(action.payload.value)))
				updateState({
					value: action.payload.value
				});
		}
	}
};
