import { t } from 'sn-translate';
import "../components/kbStarRating";

export default ({props, rating}) => {
    return (
        <div className="kb-rating">
            <span className="kb-rating-label">{t('Rate this article')}</span>
            <span className="kb-rating-icon"><sn-kb-star-rating size="lg" rating={rating} ariaLabel={t('Rate this article')} articleId={props.AppKnowledge_Query.getArticleDetails.articleInfo.sysId}
            threshold={props.AppKnowledge_Query.getArticleDetails.enableActionableFeedbackForRating}/></span>
        </div>
    )
}