import '@servicenow/now-label-value';
import {getDiffContent} from './contentDiff';
import {FIELD_UPDATED} from './constants';

export default props => {
    const {
        name,
        heading,
        contentType,
        mode,
        dispatch,
        keepSpacing
    } = props;

    let {
        originalContent,
        changedContent,
    } = props;

    if(mode === 'diff'){
        const comparedData = getDiffContent(originalContent,changedContent,contentType==='html',keepSpacing);
        originalContent = comparedData.originalContent;
        changedContent = comparedData.changedContent;
    }

    return (
		<li className="comparator-row">
            <section className="old-content content-block">
                <now-label-value-inline label={heading}/>
                <Content contentType={mode==='diff'?"html":contentType} content={originalContent} mode="normal" name={name} dispatch={dispatch}/>
            </section>
            <section className="new-content content-block">
                <now-label-value-inline id={name} label={heading}/>
                <Content contentType={contentType} content={changedContent} mode={mode} name={name} dispatch={dispatch}/>
            </section>
        </li>
	);
}

const Content = ({contentType, content, mode, dispatch, name}) => {
    const editable = mode === 'editable';
    const diff = mode === 'diff';

    if(contentType==='html') {
		return (
            <section 
                className={editable? 'editable' : 'noteditable'} 
                on-input={e => dispatch(FIELD_UPDATED, {name, content:e.target.innerHTML})} 
                contentEditable={editable} 
                innerHTML={content}
                aria-labelledby={name}
            />
        );
	} else if(editable && contentType === 'string'){
		return (
            <input
                className="input-field" 
                aria-labelledby={name}
                value={content} 
                type="text"
                on-input={e => dispatch(FIELD_UPDATED, {name, content:e.target.value})}
            />
        );
	} else if (diff){
		return <p innerHTML={content}/>;
    } else {
        return <p>{content}</p>
    }
}
