
const getTextReadTimeByCharacters = text => text.length/500;

const getTextReadTimeByWords = text => {
    const wordCount = text.split(/\s+/g).length;
    return wordCount/200;
};

const getImageReadTime = dom => {
    const imgTagCount = dom.querySelectorAll('img').length;
    const picTags = dom.querySelectorAll('picture');
    const imagesInPicturetags = Array.from(picTags).reduce((count,node) => count + node.querySelectorAll('source').length, 0);
    let count = imgTagCount + imagesInPicturetags;
    let seconds = 0, time = 12;
    while(count-->0){
        seconds += time;
        time = time!=3 ? time-1 : time;
    }
    return seconds/60;
};

export const getReadTimeFromDOM = (dom, languageCode) => {
    const text = dom.textContent;
    const characterBasedTextLangs = new Set(['zh','ja','ko','zt']);
    let textTime;
    if(characterBasedTextLangs.has(languageCode)) {
        textTime = getTextReadTimeByCharacters(text);
    }
    else {
        textTime = getTextReadTimeByWords(text);
    }
    const imageTime = getImageReadTime(dom);
    return Math.ceil(textTime + imageTime);
};

export default (html, languageCode='en') => {
    const parser = new DOMParser();
    const doc = parser.parseFromString(html, 'text/html');
    return getReadTimeFromDOM(doc.documentElement, languageCode);
};


