import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import style from './communityQADetail.scss';
import '../communityQAComment';
import '@servicenow/now-icon';
import '@servicenow/now-avatar';
import {actionHandlers} from './actionHandlers';
import {
	LOAD_COMMUNITYQA,
	LOAD_COMMUNITYQA_ANSWER
} from './actions';
import view from './view';
import { timeAgoBehaviors } from "../../_shared/behaviors";

createCustomElement('sn-community-qa-detail', {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [...timeAgoBehaviors],
	initialState: {

	},
	properties: {
		table: '',
		sysId: '',
		frameless: true
	},
	actionHandlers,
	onConnect(host, dispatch) {
		const {table} = host;
		if (table === 'community_answer' || table === 'community_question' || table === 'kb_social_qa_question' || table === 'kb_social_qa_answer'){
			dispatch(LOAD_COMMUNITYQA, {id:host.sysId, content_type:'5a2fcaa0dbd26600b1f6f78eaf9619a8'});
			dispatch(LOAD_COMMUNITYQA_ANSWER, {id:host.sysId, content_type:'5a2fcaa0dbd26600b1f6f78eaf9619a8', depth: 1});
		}
	},
	onDisconnect(host) {
		if (host.articleContent) {
			var aTags = host.articleContent.querySelectorAll('a');
			aTags.forEach(function (item) {
				item.removeEventListener('click', function(e) {
					var redirectURL = e.currentTarget || e.target || e.srcElement;
					window.open(redirectURL, '_blank');
					e.stopPropagation();
					e.preventDefault();
				});
			});
		}
	},
	styles: style
});
