import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {
    LOAD_TRANSLATION_TASK_SUCCESS,
    PREVIEW_TRANSLATED_RECORD} from '../constants'
const create_or_checkout_from_task_mutation = `
mutation($sourceRecord:String!, $translationData: String!) {
  glide: AppKnowledge_Mutation {createOrCheckoutFromTask(translationTaskID:$sourceRecord, translationData:$translationData)
  }
}`;

const load_translation_task_query = `
query ($sys_id: String!, $table: String!, $types: [String!], $exclude: [String!]) {
  glide: GlideRecord_Query {
    task: kb_translation_task(sys_id: $sys_id) {
      _results {
        source_article {
          value
        }
        translated_article {
          value
        }
        translated_language {
          value
        }
        _query {
          src: GlideRecord_Query {
            knowledge: kb_knowledge(queryConditions: "sys_id=$$parent.source_article$$", pagination: {limit: 1}) {
              _results {
                language {
                  displayValue
                  value
                }
              }
            }
          }
          trans: GlideRecord_Query {
            knowledge: kb_knowledge(queryConditions: "ORDERBYDESCsys_created_on^parent=$$parent.source_article.article_id$$^language=$$parent.translated_language$$" , pagination: {limit: 1}) {
              _results {
                sys_id {
                  value
                }
                workflow_state{
                  value
                }
                _query {
                  knowledge: AppKnowledge_Query {
                    translated: getArticleByFieldTypes(table: $table, sys_id: "$$parent.sys_id$$", types: $types, exclude: $exclude) {
                      name
                      value
                    }
                  }
                }
              }
            }
          }
          knowledge: AppKnowledge_Query {
            source: getArticleByFieldTypes(table: $table, sys_id: "$$parent.source_article$$", types: $types, exclude: $exclude) {
              name
              type
              label
              value
            }
            languages: getAvailableLanguages(articleID: "$$parent.source_article$$") {
              language
              label
            }
          }
        }
      }
    }
  }
}



`;

export const createOrCheckoutFromTask = createGraphQLEffect(create_or_checkout_from_task_mutation, {
    variableList: ['sourceRecord', 'translationData'],
    successActionType: PREVIEW_TRANSLATED_RECORD
})

export const loadTranslationTaskDetails = createGraphQLEffect(load_translation_task_query,{
    variableList: ['sys_id','table', 'types', 'exclude'],
    successActionType: LOAD_TRANSLATION_TASK_SUCCESS
})
