export const commentsQuery = `
query($id: String!, $getRootComments: Boolean!){
  AppKnowledge_Query {
    getComments(id: $id, getRootComments: $getRootComments) {
      id
      createdOn
      text
      parent
      user {
        name
        id
        avatar
      }
      likesCount
      isLiked
    }
  }
}
`;
export const addCommentQuery = `mutation ($comments: String!, $article: String!, $parent_comment: String!) {
  AppKnowledge_Mutation{
    addComment(comment:$comments ,articleId:$article, parentId:$parent_comment)
  }
  }`;

export const addLikesQuery = `mutation ($commentId: String!, $articleId: String!) {
    AppKnowledge_Mutation{
        likeComment(commentId:$commentId, articleId:$articleId)
    }
  }`;

export const deleteLikesQuery = `mutation ($commentId: String!, $articleId: String!) {
    AppKnowledge_Mutation{
        unLikeComment(commentId:$commentId, articleId:$articleId)
    }
  }`;

export const deleteCommentQuery = `mutation ($commentId: String!, $articleId: String!) {
    AppKnowledge_Mutation{
        deleteComment(commentId:$commentId, articleId:$articleId)
    }
  }`;