import { default as knowledgeBase_read } from './knowledgeBase.read';
import { default as articleTemplate_read } from './articleTemplate.read';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
    LOAD_KNOWLEDGE_BASES,
    POPULATE_KNOWLEDGE_BASES,
    KNOWLEDGE_BASE_SELECTED,
    LOAD_ARTICLE_TEMPLATES,
    POPULATE_ARTICLE_TEMPLATES,
    OPEN_KNOWLEDGE_FORM,
    CLOSE_MODAL,
    NOW_MODAL_CLOSE,
    PREVIEW_RECORD,
    LOAD_TABLE_MAP,
    TABLE_MAP_LOADED,
    LOAD_FIELD_MAP,
    FIELD_MAP_LOADED
} from './constants';
import { actionTypes } from '@servicenow/ui-core';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const loadKnowledgeBases = createGraphQLEffect(knowledgeBase_read, {
    successActionType: POPULATE_KNOWLEDGE_BASES
});

const tableMapQuery = `
query($queryConditions: String) {
    GlideRecord_Query {
        csm_table_map(queryConditions: $queryConditions) {
            _results {
                sys_id {
                    value
                }
                target_table {
                    value
                }
            }
        }
    }
}`;

const fieldMapQuery = `
query ($sysId: String!, $source: String!, $target: String!) {
    schema: AppKnowledge_Query {
        function: getFieldMappings (sysId: $sysId, source: $source, target: $target) {
          name
          value
        }
    }
}`;

const populateKnowledgeBases = ({ state, action, updateState, dispatch }) => {
    updateState({
        knowledgeBases: action.payload.data.schema.function,
        defaultKnowledgeBases: action.payload.data.schema.function,
        selectedKnowledgeBase: { value: '', displayValue: '' }
    });
    if (state.properties.source && state.properties.source != '') {
        dispatch(LOAD_TABLE_MAP, { 'queryConditions': 'active=true^source_table=' + state.properties.source });
    }
}

const loadArticleTemplates = createGraphQLEffect(articleTemplate_read, {
    successActionType: POPULATE_ARTICLE_TEMPLATES,
    variableList: ['query']
});

const populateArticleTemplates = ({ action, updateState, state }) => {
    var temps = action.payload.data.schema.function;
    updateState({
        articleTemplates: temps,
        selectedArticleTemplate: { value: '', displayValue: '-- Select --' }
    });
};

const closeModal = ({ updateState }) => {
    updateState({
        isModalOpened: false
    });
}

const openKnowledgeForm = ({ state, dispatch, updateState }) => {
    dispatch(CLOSE_MODAL);
    dispatch(PREVIEW_RECORD, {
        table: state.selectedArticleTemplate.value,
        sys_id: '-1',
        params: { query: state.query, mode: 'form' }
    });
}

const knowledgeBaseSelected = ({ action, dispatch }) => {
    dispatch(LOAD_ARTICLE_TEMPLATES, {
        query: action.payload.id
    });
}

export const action = {
    [LOAD_KNOWLEDGE_BASES]: {
        private: true
    },
    [POPULATE_KNOWLEDGE_BASES]: {
        private: true
    },
    [KNOWLEDGE_BASE_SELECTED]: {
        private: true
    },
    [LOAD_ARTICLE_TEMPLATES]: {
        private: true
    },
    [POPULATE_ARTICLE_TEMPLATES]: {
        private: true
    },
    [OPEN_KNOWLEDGE_FORM]: {
        private: true
    },
    [LOAD_TABLE_MAP]: {
        private: true
    },
    [TABLE_MAP_LOADED]: {
        default: true
    },
    [LOAD_FIELD_MAP]: {
        default: true
    },
    [FIELD_MAP_LOADED]: {
        default: true
    }
}

export const actionHandlers = {
    [LOAD_KNOWLEDGE_BASES]: loadKnowledgeBases,
    [POPULATE_KNOWLEDGE_BASES]: populateKnowledgeBases,
    [LOAD_ARTICLE_TEMPLATES]: loadArticleTemplates,
    [POPULATE_ARTICLE_TEMPLATES]: populateArticleTemplates,
    [OPEN_KNOWLEDGE_FORM]: openKnowledgeForm,
    [CLOSE_MODAL]: closeModal,
    [NOW_MODAL_CLOSE]: closeModal,
    [KNOWLEDGE_BASE_SELECTED]: knowledgeBaseSelected,
    [COMPONENT_BOOTSTRAPPED]: {
        effect({ state, dispatch }) {
            dispatch(LOAD_KNOWLEDGE_BASES);
        }
    },
    [LOAD_TABLE_MAP]: createGraphQLEffect(tableMapQuery,
        {
            successActionType: TABLE_MAP_LOADED,
            variableList: ['queryConditions']
        }),
    [TABLE_MAP_LOADED]: (props) => {
        var results = props.action.payload.data.GlideRecord_Query.csm_table_map._results;
        if (results && results.length > 0) {
            var temps = [];
            for (var i = 0; i < results.length; i++) {
                temps.push({ 'mapId': results[i].sys_id.value, 'target': results[i].target_table.value });
            }
            props.updateState({
                mapExists: true,
                tableMaps: temps
            });
        }
    },
    [LOAD_FIELD_MAP]: createGraphQLEffect(fieldMapQuery,
        {
            successActionType: FIELD_MAP_LOADED,
            variableList: ['sysId', 'source', 'target']
        }),
    [FIELD_MAP_LOADED]: (props) => {
        var prQuery = 'kb_knowledge_base=' + props.state.selectedKnowledgeBase.value;
        if (props.action.payload.data.schema.function != null) {
            var fieldMaps = props.action.payload.data.schema.function;
            for (var i = 0; i < fieldMaps.length; i++) {
                if(fieldMaps[i].name == 'kb_knowledge_base')
                    continue;
                prQuery = prQuery + "^" + fieldMaps[i].name + "=" + fieldMaps[i].value;
            }
        }
        props.updateState({
            query: prQuery
        });
        if (prQuery != '') {
            props.dispatch('SN_SCRIPTED_MODAL#DATA_SET', { query: prQuery, table: props.state.selectedArticleTemplate.value, kb: props.state.selectedKnowledgeBase.value});
        }
    }
}
