import {actionTypes} from '@servicenow/ui-core';
import {get} from 'lodash';
import {
    LOAD_LANGUAGES_AND_SOURCE, 
    LOAD_LANGUAGES_AND_SOURCE_SUCCESS, 
    LOAD_TRANSLATED_ARTICLE, 
    LOAD_TRANSLATED_SUCCESS, 
    TYPES, 
    EXCLUDE, 
    CREATE_OR_CHECKOUT_TRANSLATION,
    CREATE_OR_CHECKOUT_TRANSLATION_FROM_TASK,
    PREVIEW_TRANSLATED_RECORD,
    LOAD_TRANSLATION_TASK,
    LOAD_TRANSLATION_TASK_SUCCESS,
    BUTTON_CLICKED,
    MODAL_FOOTER_ACTION_CLICKED,
    DROPDOWN_ITEM_CLICKED,
    PREVIEW_RECORD,
    GET_BLOCK_COUNT,
    GET_BLOCK_COUNT_SUCCESS
} from './constants';
import {KB_SHOW_ALERT_MESSAGE} from '../knowledgeDiscussionWrapper/constants';
import  {fetchLanguageAndSourceDetails, loadTranslatedArticle, createOrCheckout, getBlockCount} from './util/articleGraphqlQueries';
import {loadTranslationTaskDetails, createOrCheckoutFromTask} from './util/taskGraphqlQueries';
import {translationDataForLoad, translationDataForCreate, sortSourceContent} from './util/translationData';
import { t } from 'sn-translate';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

let previousSelection =[];
let excludeFields = EXCLUDE;

export const actionHandlers = {
    [COMPONENT_BOOTSTRAPPED]: ({dispatch,properties:{sourceRecord, sourcePage, table}}) => {
        if(!(table == 'kb_knowledge' || table == 'kb_knowledge_block'))
            excludeFields.push('text');
        dispatch('DYNAMIC_TRANSLATION#IS_ENABLED_REQUESTED', {
            event: {eventName: 'KB - Workspace'}
        });
        if(sourcePage == 'task') {
            dispatch(LOAD_TRANSLATION_TASK, {
                sys_id: sourceRecord,
                table: table,
                types: TYPES,
                exclude: excludeFields
            })
        } 
        else {
            dispatch(GET_BLOCK_COUNT,{
                sourceArticle: sourceRecord
            });
            dispatch(LOAD_LANGUAGES_AND_SOURCE,{
                table: table,
                sourceArticle: sourceRecord,
                types: TYPES,
                exclude: excludeFields
            });
        }
    },

    'DYNAMIC_TRANSLATION#IS_ENABLED': ({action, updateState}) => {
        updateState({isDynamicTranslationEnabled:get(action,"payload.response.translation", false)})
    },

    /* GraphQL effects (Queries/Mutations) when loading content from Source article */
    [GET_BLOCK_COUNT]: getBlockCount,
    [GET_BLOCK_COUNT_SUCCESS]: ({action:{payload}, dispatch, updateState}) => {
        const blockCount = get(payload,"data.knowledge.getBlockCount", 0);
        if (blockCount > 0) {
            dispatch(KB_SHOW_ALERT_MESSAGE, {
                action: {
                    type: "dismiss"
                },
                status: "critical",
                icon: "bell-outline",
                content: t("Translation of articles with blocks is not supported.")
            });
            updateState({
                disableCreateArticle: true,
                isDynamicTranslationEnabled: false
            });
        }  
    },
    [LOAD_LANGUAGES_AND_SOURCE] : fetchLanguageAndSourceDetails,
    [LOAD_LANGUAGES_AND_SOURCE_SUCCESS]: ({action: {payload}, updateState, dispatch, properties:{table,sourceRecord}, state:{selectedLanguage}}) => {
        let languageResults = get(payload,"data.knowledge.languages",[]);
        
        languageResults = languageResults.filter(item => item.disabled == false);
        const languageItems = languageResults.map(({language, label}) => ({
            id: language,
            label: label
        }));
        selectedLanguage = [languageItems[0].id];
        const sourceContent = sortSourceContent(get(payload, "data.knowledge.source",[]));
        const srcLanguage = get(payload, "data.gr.knowledge._results[0].language.displayValue",[]);
        updateState({ 
            languageItems:languageItems, 
            selectedLanguage:selectedLanguage,
            sourceContent:sourceContent,
            sourceLanguage: {
                id: get(payload, "data.gr.knowledge._results[0].language.value",''),
                label: get(payload, "data.gr.knowledge._results[0].language.displayValue",'')
            }
        });
        dispatch(LOAD_TRANSLATED_ARTICLE,{
            sourceArticle: sourceRecord,
            table: table,
            types: TYPES,
            exclude: excludeFields,
            selectedLanguage: selectedLanguage
        })
    },
    [LOAD_TRANSLATED_ARTICLE]: loadTranslatedArticle,
    [LOAD_TRANSLATED_SUCCESS]: ({action:{payload}, state:{comparisonData, sourceContent}, updateState, dispatch, properties}) => {
        const results = get(payload,"data.src.knowledge._results[0]._query.trans.knowledge._results",[]);
        if (results.length == 0) {
            updateState({
                comparisonData: translationDataForLoad(sourceContent,sourceContent)
            });
        } else {
            const translated = get(results[0],"_query.knowledge.translated",[]);
            updateState({
                comparisonData:translationDataForLoad(sourceContent,translated)
            });
        }
    },
    [CREATE_OR_CHECKOUT_TRANSLATION]: createOrCheckout,

    /* GraphQL effects (Queries/Mutations) when loading content from Translation task */
    [LOAD_TRANSLATION_TASK]: loadTranslationTaskDetails,
    [LOAD_TRANSLATION_TASK_SUCCESS]:({action:{payload}, dispatch, properties, updateState}) => {
        const result = get(payload,"data.glide.task._results[0]", {});
        const sourceArticle = get(result,"source_article.value", "");
        dispatch(GET_BLOCK_COUNT, {
            sourceArticle: sourceArticle
        });
        const languageResults = get(result, "_query.knowledge.languages",[]);
        const srcLanguage  = {
            id: get(result, "_query.src.knowledge._results[0].language.value", ''),
            label: get(result, "_query.src.knowledge._results[0].language.displayValue", '')
        };
        const languageItems = languageResults.map(({language, label}) => ({
            id: language,
            label: label 
        }));
        
        updateState({
            languageItems: languageItems,
            selectedLanguage: [result.translated_language.value],
            disableLanguage: true,
            sourceLanguage: srcLanguage
        });
        const sourceContent = sortSourceContent(get(result, "_query.knowledge.source",[]));
        let transArticle = get(result, "_query.trans.knowledge._results",[]);
        if (transArticle.length == 0) {
            updateState({
                comparisonData: translationDataForLoad(sourceContent,sourceContent)
            });
        } else {
            const translatedContent = get(transArticle[0],"_query.knowledge.translated",[]);
            updateState({
                comparisonData:translationDataForLoad(sourceContent,translatedContent)
            });
        }
    },
    [CREATE_OR_CHECKOUT_TRANSLATION_FROM_TASK]: createOrCheckoutFromTask,

    /*Actions triggered on NOW components */
    [DROPDOWN_ITEM_CLICKED]: ({state:{selectedLanguage}, action:{payload}, updateState}) => {
        // get(payload);
        previousSelection = selectedLanguage;
        const changedLanguage = [get(payload,"item.id", "")];
        if (changedLanguage != selectedLanguage[0]) {
            updateState({
                'languageWarningOpened': true,
                'modalFor': 'languageChange'
            });
        }
        updateState({'selectedLanguage': changedLanguage});
    },
    [BUTTON_CLICKED]:({action:{payload},updateState, state:{comparisonData,selectedLanguage}, dispatch, properties}) => {
        if(payload.buttonFor == 'machineTranslate') {
            updateState({
                'languageWarningOpened': true,
                'modalFor': 'machineTranslate'
            });
        }
        if (payload.buttonFor == 'createDraftArticle') {
            let translationObj = translationDataForCreate(comparisonData,selectedLanguage[0]);
            const translationData = JSON.stringify(translationObj);
            if(properties.sourcePage == 'task') {
                dispatch(CREATE_OR_CHECKOUT_TRANSLATION_FROM_TASK,{
                    sourceRecord: properties.sourceRecord,
                    translationData: translationData
                })
            } else {
                dispatch(CREATE_OR_CHECKOUT_TRANSLATION,{
                    sourceRecord: properties.sourceRecord,
                    translatedLanguage: selectedLanguage[0],
                    translationData: translationData
                })
            }
        }
    }, 
    [MODAL_FOOTER_ACTION_CLICKED]: ({action:{payload}, updateState, state:{selectedLanguage, modalFor, comparisonData, sourceLanguage}, dispatch, properties}) => {
        const footerVariant = get(payload, "action.variant", "");
        if (footerVariant == "secondary") {
            if (modalFor == 'languageChange')
                updateState({'selectedLanguage':previousSelection});
        } else {
            if (modalFor == 'languageChange') {
                dispatch(LOAD_TRANSLATED_ARTICLE, {
                    table: properties.table,
                    sourceArticle: properties.sourceRecord,
                    selectedLanguage: selectedLanguage,
                    types: TYPES,
                    exclude: excludeFields
                });
            } else {
                updateState({
                    isTranslating: true,
                    disableCreateArticle: true
                });
                comparisonData.forEach((element, index) => {
                    dispatch('DYNAMIC_TRANSLATION#TEXT_TRANSLATION_REQUESTED', {
                        msg: element.originalContent,
                        sourceLanguage: sourceLanguage.id,
                        targetLanguages: selectedLanguage,
                        additionalParameters: [
                            {'parameterName': 'textType', 'parameterValue': element.contentType},
                            {'parameterName': 'elementIndex', 'parameterValue': index.toString()}
                        ],
                        event: {eventName: 'KB - Workspace'}
                    })
                });
                
            }
        }
        updateState({languageWarningOpened:false});
    },
    
    /*Auto translate article fields */
    'DYNAMIC_TRANSLATION#TEXT_TRANSLATED': ({action:{meta,payload}, state:{comparisonData}, updateState, dispatch   }) => {
        if (payload.response.code) {
            dispatch(KB_SHOW_ALERT_MESSAGE, {
                action: {
                    type: "dismiss"
                },
                status: "critical",
                icon: "bell-outline",
                content: t("Error while performing translation. If this persists, please contact administrator.")
            });
            updateState({
                isTranslating: false,
                disableCreateArticle: false
            });
        } else {
            let params = get(meta,"request.additionalParameters",[]);
            const elemIndex =  (params.filter((element) => element.parameterName=='elementIndex'))[0].parameterValue;
            const translatedData = comparisonData.map(function(element, index) {
                if(index==elemIndex) 
                    element.changedContent = get(payload,"response.translations[0].translatedText","");
                return element;
            });
            updateState({comparisonData: translatedData});
            if(elemIndex == comparisonData.length-1)
                updateState({
                    isTranslating: false,
                    disableCreateArticle: false
                });
        }
    },

    /* Preview translated record */
    [PREVIEW_TRANSLATED_RECORD]: ({action:{payload}, properties:{table, transRecord, sourcePage}, dispatch}) => {
        let translatedRecord;
        if(sourcePage=='task') 
            translatedRecord = get(payload, "data.glide.createOrCheckoutFromTask", "");
        else
            translatedRecord = get(payload, "data.glide.createOrCheckout", "");
        if (translatedRecord != 'undefined') {
            dispatch('RECORD_STATUS_CHANGED', {
                table: table,
                sys_id: transRecord,
                status: 'closed'
            });
            dispatch(PREVIEW_RECORD, {
                table: table,
                sys_id: translatedRecord,
                params: {
                    mode: 'form'
                }
            });
        } else {
            if (sourcePage == 'task') {
                dispatch(KB_SHOW_ALERT_MESSAGE, {
                    action: {
                        type: "dismiss"
                    },
                    status: "critical",
                    icon: "bell-outline",
                    content: t("A draft article already exists for this translation. Wait for it to be published.")
                });
            }
        }
       
    }
}
