import '../kbArticleTranslation/kbArticleTranslation';
import '../kbArticleComparator';
import '../knowledgeDetail';
import "@devsnc/sn-document-viewer";

export default ({ props }) => {
	const {
		table="kb_knowledge",
		sysId="-1",
		mode="view",
		src="",
		query="",
		view="workspace",
		dehydrate,
		active,
		selectedTabIndex,
		defaultTab,
		readOnlyForm,
		hideDetails,
		detailsContext,
		views } = props;
	if (mode === 'form' || sysId === '-1') {
		return (<now-record-form-connected
			table={table}
			sys-id={sysId}
			views={views}
			view={view}
			query={query}
			active={active}
			dehydrate={dehydrate}
			selectedTabIndex={selectedTabIndex}
			defaultTab={defaultTab}
			readOnlyForm={readOnlyForm}
			hideDetails={hideDetails}
			detailsContext={detailsContext}
		/>);

	} else if (mode === 'translate') {
		const srcdetails = src.split(':');
		return (
			<sn-kb-article-translation
				table={table}
				source-page={srcdetails[0]}
				source-record={srcdetails[1]}
				trans-record={sysId}
			/>
		);
	} else if (mode === 'compare') {
		const versions = sysId.split('_');
		return <sn-kb-article-comparator
			table={table}
			first-version={versions[0]}
			second-version={versions[1]}
		/>
	}else if (mode === 'docviewer') {
		return <sn-document-viewer sys-id={sysId} delete-hidden="true" close-hidden="true" render-office-format="true"/>;
	}

	return (<sn-knowledge-detail
		table={table}
		sys-id={sysId}
		mode={mode}
	/>);

	return null;
}
