import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import { isEmpty, mapKeys, has, get } from "lodash";
import { HTML_EDITOR_ACTION_PERFORMED_PRIVATE, SHOW_REPLY_BUTTON_CLICKED, HIDE_REPLY_BUTTON_CLICKED } from './constants'

import "@servicenow/now-icon";
import "@servicenow/now-avatar";
import "@servicenow/now-button";
import "@servicenow/now-dropdown";
import "@servicenow/now-record-html-editor";
import "../../../../timeAgo";
import { t } from 'sn-translate';

const TextEditor = ({ text="", dispatch, appendToPayload, value, language }) => {	
	return (
		<div className="html-editor">
			<now-record-html-editor
				placeholder={t("Type your comment here")}
				value={text}
				language={language}
			/>
			<div className="comment-buttons right">
				<now-button
					label={t("Cancel")}
					tooltip-content={t("Cancel")}
					size="md"
					variant="tertiary"
					on-click={e =>
						dispatch(HTML_EDITOR_ACTION_PERFORMED_PRIVATE, {
							action: "cancel",
							...appendToPayload
						})
					}
				></now-button>
				<now-button
					label={t("Submit")}
					tooltip-content={t("Submit")}
					size="md"
					disabled={isEmpty(value) ? true : false}
					variant="primary"
					on-click={e =>
						dispatch(HTML_EDITOR_ACTION_PERFORMED_PRIVATE, {
							action: "submit",
							text: value,
							...appendToPayload
						})
					}
				></now-button>
			</div>
		</div>
	);
}


const getTag = (tag, item, className = "", addClasses = false) => {

	const derivedAttributes = tag.derivedAttributes;
	const attributes = { ...tag.attributes };
	if (typeof tag.visible == "boolean") {
		if (!tag.visible) return;
	}

	if (typeof tag.visible == "function")
		if (!tag.visible(item)) return;

	mapKeys(derivedAttributes, (value, key) => {
		if (typeof value == "string")
			attributes[key] = get(item, value, "");

		else if (typeof value == "function")
			attributes[key] = value({ ...item });
	})
	attributes.appendToPayload = {
		attributes: { ...attributes },
		itemId: item.id
	}
	let classname
	if (addClasses)
		classname = get(tag, "hideMargin", false) == true ? className : ("feedback-button " + className);
	return (<span className={classname}><tag.tag {...attributes} /></span>);
};

const getDropDownItems = (item, id, actions) => {
	const tempArr = [];

	if (get(actions, "useDropdown", false)) {
		if (has(actions, "dropdownItems")) {
			actions.dropdownItems.map(x => {
				if ((typeof x.visible == "boolean" && x.visible) || (typeof x.visible == "function" && x.visible(item)) || typeof x.visible == "undefined") {
					tempArr.push({ ...x })
				}
			})

			if (tempArr.length > 0) return (
				<span>
					<now-dropdown
						bare
						hide-caret
						icon="ellipsis-v-outline"
						items={tempArr}
						select="none"
						selected-items=""
						size="md"
						tooltip-content={t("More Actions")}
						variant="secondary"
						append-to-payload={{ itemId: item.id }}
					/>
				</span>
			)
		}
	} else if (get(actions, "component")) {
		return getTag(actions.component, item, false)
	}

};

const populateMetaData = (metaData, item) => {
	let tempArr = [];
	metaData.map(x => {
		if ((typeof x.visible == "boolean" && x.visible) || (typeof x.visible == "function" && x.visible(item)) || typeof x.visible == "undefined") {
			if (typeof x.value == "string") {
				tempArr.push(
					<Fragment>
						<span className="pad-sides"></span>
						<span data-testid="metaData">{get(item, x.value, x.value)}</span>
					</Fragment>
				)
			}
			else if (typeof x.value == "function") {
				tempArr.push(
					<Fragment>
						<span className="pad-sides"></span>
						<span data-testid="metaData">{x.value(item)}</span>
					</Fragment>
				);
			}
		}
	})
	return tempArr;
}


const getUserDetails = item =>
	item.level > 0 ? (
		<Fragment>
			<span className="authorName"> {item.user.name} </span>
			<now-icon className="separator" icon="chevron-right-fill" size="sm"></now-icon>
			<span className="parentAuthorName"> {item.parent_obj.user.name} </span>
		</Fragment>
	) : (
			<Fragment>
				<span className="authorName"> {item.user.name} </span>
			</Fragment>
		);

const getClass = (item, index, last, expandedItems) => {
	var classes;
	var level = item.level;
	classes = level >= 3 ? "maxChild" : level == 2 ? "lv2-child" : level == 1 ? "lv1-child" : ""
	if (item.level >= 1 && !expandedItems.includes(item.root.id))
		classes += " hide"

	return classes;
};

const getRepliesButton = (item, expandedItems, dispatch, updateProperties, manageExpanded) => {
	if (get(item, "level", "") == 0 && get(item, "children.length", 0) > 0) {
		let showReplies = expandedItems.includes(item.id);
		return (
			<span className="right ">
				<now-button-bare
					data-testid="collapse"
					icon-name-start={
						showReplies
							? "chevron-up-fill"
							: "chevron-down-fill"
					}
					expanded={
						showReplies ? true : false
					}
					label={
						showReplies ? t("Hide replies") : t("Show replies")
					}
					tooltip-content={
						showReplies ? t("Hide replies") : t("Show replies")
					}
					size="sm"
					variant="primary"
					hide-padding
					on-click={() => {
						dispatch(showReplies ? HIDE_REPLY_BUTTON_CLICKED : SHOW_REPLY_BUTTON_CLICKED, {
							itemId: item.id
						});

						if (!manageExpanded) {
							if (showReplies)
								expandedItems.splice(expandedItems.indexOf(item.id), 1);
							else
								expandedItems.push(item.id);
							updateProperties({
								"expandedItems": expandedItems
							})
						}
					}
					}
				/>
			</span>
		)
	}
	return "";

}

const Comment = ({ item, className = "", buttons = [], actions = [], isEditing = false, currentEditorId = "", expandedItems = [], level = "", dispatch, updateProperties, metaData = [], manageExpanded = false, value="", behaviors, language }) => {
	if (!has(item, "children"))
		item.children = [];
	return (
		<Fragment>
			<div className={className} data-testid={item.id}>
				<div className="avatar">
					<now-avatar
						aria-hidden="true"
						image-src={item.user.avatar}
						size="sm"
						user-name={item.user.name}
					/>
				</div>
				<div className="comment-container">
					<div>
						<div style={{ display: "inline-block" }}>
							<span className="author">{getUserDetails(item)}</span>
							<div className="meta-data">
								<sn-timeago
									time-stamp={item.createdOn}
									system-time={true}
									behaviors={behaviors}
								/>
								{populateMetaData(metaData, item)}
							</div>
						</div>
						<span className="right actions">
							{getDropDownItems(item, item.id, actions)}
						</span>
					</div>
					<div className="text-display">
						{isEditing && currentEditorId == item.id ? (
							<TextEditor
								text={item.text}
								dispatch={dispatch}
								appendToPayload={{ type: "edit_comment", itemId: item.id }}
								value={value}
								language = {language}
							/>
						) : (
							<p innerHTML={item.text}></p>
						)}
					</div>
					<div className="feedback-buttons-group clearfix">
						{buttons.map(x => {
							if (get(x, "position", "left") == "left")
								return getTag(x, item, "", true);
						})}
						{getRepliesButton(
							item,
							[...expandedItems],
							dispatch,
							updateProperties,
							manageExpanded
						)}
						{buttons.map(x => {
							if (get(x, "position", "") == "right")
								return getTag(x, item, "right", true);
						})}
					</div>
					{!isEditing && currentEditorId == item.id ? (
						<div className="create-comment-container">
							<TextEditor
								text=""
								dispatch={dispatch}
								appendToPayload={{ type: "reply_comment", itemId: item.id }}
								value={value}
								language = {language}
							/>
						</div>
					) : (
						""
					)}
				</div>
			</div>
			{item.children.map((x, index) => {
				x.parent_obj = item;
				x.root = item.root;
				x.level = level + 1;
				return (
					<Comment
						item={x}
						className={getClass(
							x,
							index,
							item.children.length - 1 == index,
							expandedItems
						)}
						buttons={buttons}
						actions={actions}
						isEditing={isEditing}
						currentEditorId={currentEditorId}
						expandedItems={expandedItems}
						level={level + 1}
						dispatch={dispatch}
						updateProperties={updateProperties}
						manageExpanded={manageExpanded}
						metaData={metaData}
						value={value}
						behaviors={behaviors}
					/>
				);
			})}
		</Fragment>
	);
};

const snCommentsComponent = ({ value, properties: { actions, buttons, items, isEditing, currentEditorId, expandedItems, metaData, manageExpanded, language }, behaviors }, { dispatch, updateProperties }) => {
	let tempItems = JSON.parse(JSON.stringify(items));	
	return (
		<Fragment>
			<div className="sn-comments">
				{tempItems.map(item => {
					item.level = 0;
					item.root = item;
					return (
						<Comment
							item={item}
							className="parent"
							buttons={buttons}
							actions={actions}
							isEditing={isEditing}
							currentEditorId={currentEditorId}
							expandedItems={expandedItems}
							level={0}
							dispatch={dispatch}
							updateProperties={updateProperties}
							manageExpanded={manageExpanded}
							metaData={metaData}
							value={value}
							behaviors={behaviors}
							language = {language}
						/>
					);
				})}
				{
					items.length > 0 ? <div className="border-bottom"></div> : ""
				}
			</div>
		</Fragment>
	);
};

export default snCommentsComponent;
