import { t } from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import { primaryButtons, secondaryButtons, actionButtons } from '../utils/actionUtil';

export default (props) => {
    const evaluateActionCondition = (btn) => {
    	return btn.condition && !btn.condition(props) ? false : true
    };
	const getButtonLabel = (btn) => {
		if (btn.id === 'selectBlocks')
			return props.payload.article_attributes.blocksSelected ? t('Hide Blocks') : t('Show Blocks');
		else
			return btn.label;
	};

    return (
        <div className="kb-action-group">
			{actionButtons.map(btn => (
				evaluateActionCondition(btn) ?
					<div className="kb-action-highlight">
						<now-button className="kb-action-highlight-icon" size="md" variant={btn.variant}
							label={getButtonLabel(btn)} append-to-payload={{ "item": btn}}/>
					</div> : ""
			))}
            {primaryButtons.map(btn => (
                evaluateActionCondition(btn) ?
                <div className="kb-action-primary">
                    <now-button label={btn.label} size="md" variant={btn.variant} append-to-payload={{ "item": btn}}/>
                </div> : ""
            ))}
            <div className="kb-action-secondary">
                <now-dropdown hide-caret hide-label icon="ellipsis-h-fill" items={secondaryButtons.filter(evaluateActionCondition)} panel-fit-props='{}' select="none" size="md" variant="secondary" config-aria={{'aria-label': t('Action Menu. Please click for available actions on the article')}}/>
            </div>
        </div>
    )
}
