import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { t } from 'sn-translate';
import { MOUSE_ENTER, MOUSE_LEAVE, MOUSE_CLICK, KEY_DOWN, UPDATE_RATING, UPDATE_RATING_SUCCEEDED, UPDATE_RATING_FAILED, SHOW_MESSAGE, SUBMIT_RATING, MESSAGES } from './constants';
import { UPDATE_FEEDBACK_RATING } from './constants';
import { actionTypes } from '@servicenow/ui-core';
import { rating_update } from './query';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const updateKnowledgeRating = createGraphQLEffect(rating_update, {
	successActionType: UPDATE_RATING_SUCCEEDED,
	errorActionType: UPDATE_RATING_FAILED,
	variableList: ['rating', 'articleId']
});

export const actionHandlers = {
	[MOUSE_ENTER]: {
		effect({ action, updateState, state }) {
			updateState({
				current: get(action, 'payload.current')
			});
			if (!state.isEditing) {
				updateState({
					isEditing: true
				});
			}
		},
		stopPropagation: true
	},
	[MOUSE_LEAVE]: {
		effect({ properties, updateState }) {
			updateState({
				current: properties.rating
			});
			updateState({
				isEditing: false
			});
		},
		stopPropagation: true
	},
	[COMPONENT_BOOTSTRAPPED]: {
		effect({ properties, updateState, updateProperties }) {
			let rating = properties.halfRating ? Math.round(properties.rating * 2) / 2 : parseInt(properties.rating);
			let stars = parseInt(properties.stars);
			if (rating > stars) rating = stars;
			updateProperties({
				rating: rating
			});
			updateProperties({
				stars: parseInt(properties.stars)
			});
			updateState({
				current: rating
			});
		}
	},
	[KEY_DOWN]: {
		effect({ action, dispatch, state, updateState, updateProperties, properties }) {
			let halfRating = get(properties, 'halfRating', false);
			let current = state.current;
			let key = get(action, 'payload.key');
			let stars = properties.stars;
			let inc = 1;
			if (halfRating)
				inc = 0.5;
			if (key == 39 || key == 40) {
				if (current + inc <= stars)
					current += inc;
			}
			else if (key == 37 || key == 38) {
				if (current - inc >= 0)
					current -= inc;
			}
			else if (key == 13 && current >= 0 && current <= stars) {
				dispatch(SUBMIT_RATING, { articleId: properties.articleId, rating: current+'' });
			}
			updateState({
				current: current
			});
			if (!state.isEditing) {
				updateState({
					isEditing: true
				});
			}
		},
		stopPropagation: true
	},
	[MOUSE_CLICK]: {
		effect({action, dispatch, properties, updateProperties}) {
			let rating = get(action, 'payload.rating');
			dispatch(SUBMIT_RATING, { articleId: properties.articleId, rating: rating+'' });	
		}
	},
	[SUBMIT_RATING]: {
		effect({action, dispatch, properties, updateProperties}) {
			let rating = get(action, 'payload.rating');
			let threshold = properties.threshold;

			if(rating != properties.rating) {
				if(rating <= threshold) {
					dispatch(UPDATE_FEEDBACK_RATING, { rating: rating + '' });
				} else {
					dispatch(UPDATE_RATING, { articleId: properties.articleId, rating: rating+'' });
				}
			}
			updateProperties({
				rating: rating
			});
		}
	},
	[UPDATE_RATING]: updateKnowledgeRating,
	[UPDATE_RATING_SUCCEEDED]: {
		effect({dispatch, action}) {
            if(action.payload.data.GlideRecord_Mutation.insert_kb_feedback)
				dispatch(SHOW_MESSAGE, {
					status: "info",
					iconName: "circle-info-outline",
					content: MESSAGES.THANK_YOU,
					action: { type: "dismiss" }
				});
			else
				dispatch(SHOW_MESSAGE, {
					status: "critical",
					iconName: "circle-info-outline",
					content: MESSAGES.LIMIT_REACHED_MESSAGE,
					action: { type: "dismiss" }
				});
		},
		stopPropagation: true
	},
	[UPDATE_RATING_FAILED]: {
		effect({dispatch}) {
			dispatch(SHOW_MESSAGE, {
				status: "critical",
				iconName: "circle-info-outline",
				content: MESSAGES.LIMIT_REACHED_MESSAGE,
				action: { type: "dismiss" }
			});
		},
		stopPropagation: true
	}
};