import "../components/kbComments";
import { get } from "lodash";

export default prop => {
    const id = get(prop, "payload.articleResult.sysId", "");
    const isAdminorOwner = get(prop, "payload.article_attributes.isKBAdmin", false) || get(prop, "payload.article_attributes.isKBOwner", false) || false;
    const commentsThreshold = get(prop, "payload.article_attributes.commentsThreshold", 0);
    const hasComments = get(prop, "payload.article_attributes.hasComments", false);
    const user = get(prop, "payload.userDetail", {});
    return (
        <div className="kb-comment">
            <sn-knowledge-comments articleId={id} isAdminorOwner={isAdminorOwner} commentsThreshold={commentsThreshold} hasComments={hasComments} user={user}></sn-knowledge-comments>
        </div>
    )
}