import {
	FETCH_CATEGORIES,
	FETCH_CATEGORIES_FROM_ARTICLE,
	ITEM_CLICKED,
	FETCH_CATEGORIES_SUCCESS,
	FETCH_FAILURE,
	FETCH_NEXT_CATEGORIES,
	FETCH_NEXT_CATEGORIES_SUCCESS,
	FETCH_NEXT_CATEGORIES_FAILURE,
	CATGORY_PICKER_ITEM_SELECTED,
	NOW_CONTENT_TREE_LOADING_REQUESTED,
	VALUE_CHANGED,
	FETCH_CATEGORIES_FROM_ENCODED_RECORD
} from './constants';
import { get, has, isEmpty } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import { articleCategoryQuery, categoryQuery, encodedRecordCategoryQuery } from './queries';
import { t } from 'sn-translate';

const buildTree = (categories) => {
	var catTree = {};
	try {
		categories.forEach((x) => {
			if (has(catTree, x.id)) {
				catTree[x.id].label = x.label;
				catTree[x.id].id = x.id;
				catTree[x.id].path.unshift(x.parentId);
			} else {
				catTree[x.id] = {
					id: x.id,
					label: x.label,
					children: [],
					path: [x.parentId]
				};
				if (catTree[x.parentId] && catTree[x.parentId].path) {
					catTree[x.id].path.unshift(...catTree[x.parentId].path);
				}
			}

			if (has(catTree, x.parentId)) {
				catTree[x.parentId].children.push(catTree[x.id]);
			} else {
				catTree[x.parentId] = {
					children: [catTree[x.id]]
				};
			}
		});
		return catTree;
	} catch (e) {
		return {};
	}
};

const getExpandedPath = (path) => {
	let tempPath = [];
	path.forEach((element, i) => {
		if (i == 0)
			tempPath.push([element]);
		else
			tempPath.push([...tempPath[i - 1], element]);
	});
	return tempPath;
};

const getPathinString = (path, tree) => {
	let actualPath = 'items';
	let tempTree = tree;
	for (let i = 0; i < path.length; i++) {
		let index = tempTree.findIndex(y => y.id == path[i]);
		tempTree = tempTree[index].children;
		if(index!=-1)
			actualPath += `[${index}].children`;
	}
	return actualPath;
};

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: {
		effect({ dispatch, properties: { serializedChanges='{}', recordSysId, kbKnowledgeBase, value = '', encodedRecord = ''}, updateState }) {
			let changes = JSON.parse(serializedChanges);
			let hasValue = true;
			if (isEmpty(value)) {
				value = ""
				hasValue = false
			}
			if (changes.hasOwnProperty('kb_knowledge_base')) {
				if (!isEmpty(changes.kb_knowledge_base)) {
					dispatch(FETCH_CATEGORIES, { id: changes.kb_knowledge_base, hasValue: hasValue, value: value });
					updateState({
						path: 'kbKnowledgeBase',
						value: changes.kb_knowledge_base,
						operation: 'set'
					});
				} else
					dispatch(FETCH_FAILURE, { errorMessage: t('Select a knowledge base') });
				
			} else if (!isEmpty(recordSysId) && recordSysId.length == 32) {
				dispatch(FETCH_CATEGORIES_FROM_ARTICLE, { id: recordSysId, hasValue: hasValue, value: value });
			} else if (!isEmpty(kbKnowledgeBase)) {
				dispatch(FETCH_CATEGORIES, { id: kbKnowledgeBase, hasValue: hasValue, value: value });
				updateState({
					path: 'kbKnowledgeBase',
					value: kbKnowledgeBase,
					operation: 'set'
				});
			} else if (!isEmpty(encodedRecord)) {
				dispatch(FETCH_CATEGORIES_FROM_ENCODED_RECORD, { encodedRecord: encodedRecord, hasValue: hasValue, value: value });
			} else dispatch(FETCH_FAILURE);
		}
	},

	[FETCH_CATEGORIES]: createGraphQLEffect(categoryQuery, {
		variableList: ['id', 'hasValue', 'value'],
		successActionType: FETCH_CATEGORIES_SUCCESS,
		errorActionType: FETCH_FAILURE
	}),

	[FETCH_NEXT_CATEGORIES]: createGraphQLEffect(categoryQuery, {
		variableList: ['id', 'hasValue', 'value'],
		successActionType: FETCH_NEXT_CATEGORIES_SUCCESS,
		errorActionType: FETCH_NEXT_CATEGORIES_FAILURE
	}),

	[FETCH_CATEGORIES_FROM_ARTICLE]: createGraphQLEffect(articleCategoryQuery, {
		variableList: ['id', 'hasValue', 'value'],
		successActionType: FETCH_CATEGORIES_SUCCESS,
		errorActionType: FETCH_FAILURE
	}),

	[FETCH_CATEGORIES_FROM_ENCODED_RECORD]: createGraphQLEffect(encodedRecordCategoryQuery, {
		variableList: ['encodedRecord', 'hasValue', 'value'],
		successActionType: FETCH_CATEGORIES_SUCCESS,
		errorActionType: FETCH_FAILURE
	}),

	[ITEM_CLICKED]: {
		effect({ state, action, dispatch, updateState }) {
			dispatch(CATGORY_PICKER_ITEM_SELECTED, action.payload.item);
			if (state.opened) {
				updateState({
					path: 'opened',
					value: false,
					operation: 'set'
				});
				dispatch(VALUE_CHANGED, {
					name: 'kb_category',
					value: get(action, 'payload.item.id', ''),
					displayValue: get(action, 'payload.item.label', '')
				});
			}
		}
	},

	[FETCH_CATEGORIES_SUCCESS]: {
		effect({ state, updateState, action, properties: { value } }) {
			if (get(action,'payload.errors.length',0) == 0) {
				var data = get(action,'payload.data');
				var categories = [];
				var kbId = get(data, 'GlideRecord_Query.kb_knowledge._results[0].kb_knowledge_base.value', '');
				var fromArticle = false;
				if (kbId) {
					categories = get(data, 'GlideRecord_Query.kb_knowledge._results[0]._query.AppKnowledge_Query.first', []);
					fromArticle = true;
				} else {
					categories = get(data, 'AppKnowledge_Query.first',[]);
				}
				if (categories.length > 0) {
					updateState({
						path: 'items',
						value: categories,
						operation: 'set'
					});
					if (value) {
						let flatCategories = [];
						if (fromArticle)
							flatCategories = get(data, 'GlideRecord_Query.kb_knowledge._results[0]._query.AppKnowledge_Query.second', []);
						else
							flatCategories = get(data, 'AppKnowledge_Query.second', []);
						
						if (flatCategories.length > 0) {
							let categoryTree = buildTree(flatCategories);
							if (categoryTree[value]) {
								let path = categoryTree[value].path;
								if (path.length > 0) {
									path.shift();
									let index = categories.findIndex(y => y.id == path[0]);
									if (index != -1) {
										updateState({
											path: `items[${index}].children`,
											value: categoryTree[path[0]].children,
											operation: 'set'
										});
									}
									let expandedItems = getExpandedPath(path);
									path.push(value);
									if (expandedItems.length > 0) {
										updateState({
											path: 'expandedItems',
											value: expandedItems,
											operation: 'set'
										});
									}
									updateState({
										path: 'selectedItems',
										value: [path],
										operation: 'set'
									});
								}
							}
						} else {
							let index = categories.findIndex(y => y.id == value);
							if (index != -1) {
								updateState({
									path: 'selectedItems',
									value: [[value]],
									operation: 'set'
								});
							}
						}
					}
				}
			}
			updateState({
				path: 'loading',
				value: false,
				operation: 'set'
			});
		}
	},

	[NOW_CONTENT_TREE_LOADING_REQUESTED]: {
		effect({ action, dispatch, updateState }) {
			updateState({
				path: 'loadingItems',
				value: {
					path: action.payload.path,
					error: false,
					errorMessage: t('Unable to load')
				},
				operation: 'unshift'
			});
			dispatch(FETCH_NEXT_CATEGORIES, { id: action.payload.item.id, hasValue: false, value: '' });
		}
	},

	[FETCH_NEXT_CATEGORIES_SUCCESS]: {
		effect({ action, state, updateState }) {
			var categories = get(action.payload, 'data.AppKnowledge_Query.first', []);
			let id = get(action, 'meta.options.variables.id');
			let ind = state.loadingItems.findIndex(x => x.path[x.path.length - 1] == id);
			if (ind != -1) {
				let path = state.loadingItems[ind].path;
				if (path.length > 0) {
					updateState({
						path: getPathinString(path, state.items),
						value: categories,
						operation: 'set'
					});
					updateState({
						path: 'loadingItems',
						value: '',
						operation: 'splice',
						start: ind,
						deleteCount: 1
					});
				}
			}
		}
	},

	[FETCH_NEXT_CATEGORIES_FAILURE]: {
		effect({ action, updateState, state }) {
			let id = get(action, 'meta.options.variables.id');
			let ind = state.loadingItems.findIndex(x => x.path[x.path.length - 1] == id);
			if (ind != -1) {
				updateState({
					path: `loadingItems[${ind}].error`,
					value: true,
					operation: 'set'
				});
			}
		}
	},

	[FETCH_FAILURE]: {
		effect({ updateState, action }) {
			let errorMessage = get(action, 'payload.errorMessage', t('No categories found'));
			updateState({
				path: 'loading',
				value: false,
				operation: 'set'
			});
			updateState({
				path: 'errorMessage',
				value: errorMessage,
				operation: 'set'
			});
		}
	}
};

