import '../timeAgo';
import '../../_shared/utils';
import {createElement} from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import './socialQADetail.scss';
import '../socialQAAnswer';
import '@servicenow/now-icon';
import '@servicenow/now-avatar';

const view = (state, {dispatch}) => {
	if (!state.socialQA) return null;
	const articleResult = state.socialQA.kb_social_qa_question._results[0];
	const answerResult = state.socialQA.kb_social_qa_answer._results;
	if (!articleResult || !answerResult)
		return null;
	else if (articleResult && answerResult){
		let correctAnswer = null;
		if(articleResult.accepted_answer && articleResult.accepted_answer._reference && articleResult.accepted_answer._reference.sys_id.value) {
			correctAnswer = <sn-knowledge-socialqa-answer key={articleResult.accepted_answer._reference.sys_id.value} answer={articleResult.accepted_answer._reference} answered={true} behaviors={state.behaviors}/>;
		}
		let lastAnswer = answerResult.length > 0 ? answerResult[0] : null;

		const frameless = state.properties.frameless === 'true' || state.properties.frameless === true;
		let noBorderClass = '-bottom-border';
		if(frameless)
			noBorderClass = '-no-border';

		const detailBody = (
			<div className={'sn-card ' + noBorderClass} tabIndex='0'>
				<header className="sn-card-header">
					<div className="sn-card-row"><span>{articleResult.kb_knowledge_base.displayValue} {(articleResult.kb_category.displayValue && articleResult.kb_category.displayValue.length > 0) ?  ' | ' + articleResult.kb_category.displayValue : ''}</span></div>
					<div className="sn-card-row -title"><span>{articleResult.question.displayValue}</span></div>
					<div className="sn-card-row has-avatar">
						<div className="sn-card-content -static">
							<now-avatar
								size="md"
								image-src={'/' + articleResult.profile._reference.photo.displayValue}
								user-name={articleResult.profile.displayValue} />
						</div>
						<div className="sn-card-content">
							<ul className="sn-meta-list">
								<li className="sn-meta-list-item"><now-icon icon="user-outline" size="sm"></now-icon><span>{ t('Asked by {0}', articleResult.profile.displayValue)}</span></li>
								<li className="sn-meta-list-item"><now-icon icon="calendar-outline" size="sm"></now-icon><span>{t('Asked' + ' ')}<sn-timeago time-stamp={articleResult.sys_created_on.value} behaviors={state.behaviors} system-time={true}/></span></li>
							</ul>
						</div>
					</div>
				</header>
				<div className="sn-card-row -body" ref = {(article_content) => {
					if (!state.articleContent) {
						dispatch('SET_STATE', {articleContent: article_content}, {stopPropagation: true});
					}
				}}>
					<div className="sn-card-body">
						{createElement('div', {props: {innerHTML: articleResult.question_details.displayValue}})}
					</div>
					<div className="sn-card-footer">
						<ul className="sn-meta-list">
							<li className="sn-meta-list-item">{lastAnswer ? <span>{t('Answered') + ' '}<sn-timeago time-stamp={lastAnswer.sys_created_on.value} behaviors={state.behaviors} system-time={true}/></span> : t('Not Answered')}</li>
							<li className="sn-meta-list-item"><span>{articleResult.views.value + ' ' + (articleResult.views.value == 1 ? t('view') : t('views'))}  {articleResult.votes.value + ' ' + (articleResult.votes.value == 1 ? t('vote') : t('votes'))}  {answerResult.length + ' ' + (answerResult.length == 1 ? t('reply') : t('replies'))}</span></li>
						</ul>
					</div>
					<div className="sn-card-list">
						{correctAnswer}
						{
							answerResult.map((item) => {
								if(articleResult.accepted_answer._reference.sys_id.value !== item.sys_id.value)
									return (<sn-knowledge-socialqa-answer key={item.sys_id.value} answer={item} answered={false} behaviors={state.behaviors}/>);
								return null;
							})
						}
					</div>
				</div>
			</div>
		);
		if (state.articleContent) {

			try{//Added to resolve queryselector undefined.
				var aTags = state.articleContent.querySelectorAll('a');
				aTags.forEach(function (item) {
					item.addEventListener('click', function(e) {
						var redirectURL = e.currentTarget || e.target || e.srcElement;
						window.open(redirectURL, '_blank');
						e.stopPropagation();
						e.preventDefault();
					});
				});
			}
			catch(e){
			}

		}
		return (
			<div className='sn-agent-assist'>
				{ frameless ? (detailBody) : (
					<div className="sn-panel">
						{detailBody}
					</div>)
				}
			</div>
		);
	}
	return null;
};

export default view;
