import {
	LOAD_SOCIALQA,
	LOAD_SOCIALQA_FAILED,
	LOAD_SOCIALQA_SUCCEEDED,
	SET_STATE
} from './actions';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {createUpdateStateEffect} from '@servicenow/ui-effect-update-state';
import {default as socialqa_read} from './socialqa.read';

const fetchSocialQAEffect = createGraphQLEffect(socialqa_read, {
	successActionType: LOAD_SOCIALQA_SUCCEEDED,
	errorActionType: LOAD_SOCIALQA_FAILED, 
	variableList: ['sys_id', 'answerCondition']
});

export const actions = {
	[LOAD_SOCIALQA]: {
		private: true
	}
}; 

export const actionHandlers = {
	[LOAD_SOCIALQA]: fetchSocialQAEffect, 
	[LOAD_SOCIALQA_SUCCEEDED]: {
		stopPropagation: true,
		effect: ({action, updateState}) => updateState({
			socialQA: action.payload.data.GlideRecord_Query
		})
	},
	[LOAD_SOCIALQA_FAILED]: {
		stopPropagation: true,
		effect: ({state, updateState}) => updateState({...state})
	},
	[SET_STATE]: {
		stopPropagation: true,
		effect: ({action, updateState}) => updateState({
			articleContent: action.payload.articleContent
		})
	}
}; 