import '../timeAgo';
import '../../_shared/utils';
import { t } from 'sn-translate';
import '@servicenow/now-icon';
import {
	LOAD_ATTACHMENT,
	PROPERTIES_SET,
	DOWNLOAD_ATTACHMENT
} from './actions';

function renderCard(item, behaviors) {
	return ([
		(<div className="sn-attachment-content" key={item.sysId + '_' + new Date()} >
			<span className="sn-attachment-content-title" title={item.name}>{item.name}</span>
			<span>{item.userName}</span>
			<span>{t("Attached")+" "}<sn-timeago time-stamp={item.date} behaviors={behaviors} system-time={true}/></span>
		</div>)
	]);
}

const view = (state, dispatch) => {
	if (!state.graphQL || !state.classNameGraphQl) {
		return null;
	}
	const class_name = state.classNameGraphQl.GlideRecord_Query.kb_knowledge._results[0].sys_class_name.value;
	const table = state.properties.table;

	if (class_name !== table) {
		dispatch(PROPERTIES_SET, { table: class_name });
		dispatch(LOAD_ATTACHMENT, {sysId: state.properties.sysId, table: class_name});
	}
	let list = state.graphQL.query.model.attachments.map((item) => {
		let card = renderCard(item, state.behaviors);
		return (
			<div role="button" className="sn-attachment" tabIndex="0" on-click={() => {dispatch(DOWNLOAD_ATTACHMENT, {sysId: item.sysId, name: item.name});}} aria-label={t('Attachment:') + t(item.name)} key={item.sysId + '_' + new Date()}>
				<div className="sn-attachment-container">
					<div className="sn-attachment-content -static">
						<now-icon icon="document-fill" size="xl"/>
					</div>
					{card}
				</div>
			</div>
		);
	});
	if(!list || list.length == 0) return null;
	return (
		<div className="sn-attachment-list">
			{list}
		</div>
	);
};

export default view;
