export const articleCategoryQuery = `query($id :String!, $hasValue: Boolean!, $value: String!){
    GlideRecord_Query {
        kb_knowledge(sys_id: $id) {
            _results {
                kb_knowledge_base {
                    value
                }
                _query {
                    AppKnowledge_Query {
                        first: getCategories(id: "$$parent.kb_knowledge_base$$", isSelected: false) {
                            label
                            id
                            childrenAvailable
                        }
                        second: getCategories(id: $value, isSelected: true) @include(if: $hasValue){
                          label
                          id
                          parentId
                        }
                    }
                }
            }
        }
  }
}`;

export const categoryQuery = `query($id :String!, $hasValue: Boolean!, $value: String!){
  AppKnowledge_Query {
    first: getCategories(id: $id, isSelected: false) {
      label
      id
      childrenAvailable
    }
    second: getCategories(id: $value, isSelected: true) @include(if: $hasValue){
      label
      id
      parentId
    }
  }
}
`;

export const encodedRecordCategoryQuery = `query($encodedRecord :String!, $hasValue: Boolean!, $value: String!){
  AppKnowledge_Query {
    first: getCategoriesFromEncodedRecord(encodedRecord: $encodedRecord) {
      label
      id
      childrenAvailable
    }
    second: getCategories(id: $value, isSelected: true) @include(if: $hasValue){
      label
      id
      parentId
    }
  }
}
`;
