import {createElement, Fragment} from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '@servicenow/now-icon';
import '@servicenow/now-avatar';
import '../timeAgo';
import get from 'lodash/get';
import {
	LOAD_REPLIES,
	STATE_SET
} from './actions';

function toggleReplies(state, dispatch) {
	if (!state.properties.showReplies) {
		dispatch(LOAD_REPLIES, {id: state.properties.sysid, content_type: '8f0a215c6707220071b89a6617415aa7', depth: 1}, {stopPropagation: true});
	}
	dispatch(STATE_SET,{showReplies: !state.properties.showReplies}, {stopPropagation: true});
}

function getParentDivClass(nested) {
	return nested ? 'sn-card -comment' : 'sn-card -accepted';
}

function getNestedClass(nested) {
	return nested? '-nested' : '';
}

function getReplies(state) {
	const results = get(state, 'communityAnswers', null);
	return (results && results.length) ? results : [];
}

function getToggleBarContent(comment_count, name, state) {
	return state.properties.showReplies ? t('Hide replies to') + ' ' + name : t('Show') + ' ' + comment_count + ' ' + t('replies to') + ' ' + name;
}

function getIconType(state) {
	return state.properties.showReplies == true ? 'chevron-up-fill' : 'chevron-down-fill';
}

function getSpanClass(state) {
	return state.properties.showReplies == true ? 'chevron-up-outline' : 'chevron-down-outline';
}

const view = (state, {dispatch}) => {
	if (!state || !state.properties || !state.properties.comment) {
		return (
			<Fragment>
				<div>{t('Loading...')}</div>
			</Fragment>
		);
	}
	let {nested, lastelem, acceptedid, sysid, disablenest} = state.properties;
	let comment = state.properties.comment;
	let replies = getReplies(state);
	const content = (
		<div>
			<div className="sn-card-row has-avatar">
				<div className="sn-card-content -static">
					<now-avatar
						size="md"
						image-src={'/' + comment.userAvatarObject.userImage}
						user-name={comment.userAvatarObject.name} />
				</div>
				<div className="sn-card-content">
					<ul className="sn-meta-list">
						<li className="sn-meta-list-item -bold"><span>{comment.userAvatarObject.name}</span></li>
						<li className="sn-meta-list-item"><span>{comment.user_details.levelName}</span></li>
						<li className="sn-meta-list-item"><span><sn-timeago time-stamp={comment.edited_date} behaviors={state.properties.behaviors} system-time={true}/>  {comment.helpful_count} {' ' + comment.helpful_count == 1 ? t('vote') : t('votes')} </span></li>
					</ul>
				</div>
			</div>
			<div className="sn-card-body">
				{createElement('div',{props: {innerHTML: comment.text}})}
			</div>
			{
				(!(acceptedid == sysid) && comment.comment_count > 0) || (!disablenest && comment.comment_count > 0) ?
					<div className="sn-card-row"><a href="javascript:void(0)" on-click={() => toggleReplies(state, dispatch)} className="sn-show-more-btn"><span>{getToggleBarContent(comment.comment_count, comment.userAvatarObject.name, state)}</span><span className={getSpanClass(state)}><now-icon icon={getIconType(state)} size="md"></now-icon></span></a></div>
					: ''
			}
			{
				!replies || replies.length <= 0 || state.properties.showReplies == false ? '' :
					<div className="sn-card-list">
						{
							replies.map((item, index) => {
								if(index == replies.length - 1)
									return (<sn-community-qa-comment key={item.sys_id} comment={item} acceptedid={acceptedid} sysid={item.sys_id} nested={true} lastelem={true} behaviors={state.properties.behaviors}/>);
								else
									return (<sn-community-qa-comment key={item.sys_id} comment={item} acceptedid={acceptedid} sysid={item.sys_id} nested={true} behaviors={state.properties.behaviors}/>);
							})
						}
					</div>
			}
			{!lastelem && nested ? <div className="sn-card-separator"></div> : ''}
		</div>
	);
	return (acceptedid == sysid) ? (
		<div className={getParentDivClass(nested) + ' ' + getNestedClass(nested)}>
			<div className="sn-comment-header">
				<div className="sn-card-row">
					<now-icon icon="circle-check-fill" size="md"></now-icon>
					<span>Accepted Solution</span>
				</div>
			</div>
			{content}
		</div>
	) : (
		<div className={'sn-card -comment ' + getNestedClass(nested)}>
			{content}
		</div>
	);
};
export default view;
