import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { default as flag_article } from './query'
import { t } from 'sn-translate';
import { SUBMIT_FLAG_COMMENT, UPDATE_FLAG_COMMENT, UPDATE_FLAG_COMMENT_SUCCEEDED, UPDATE_FLAG_COMMENT_FAILED, FLAG_MODAL_RESET, BUTTON_CLICKED, DROPDOWN_CLICKED, MODAL_BUTTON_ESC, SHOW_MESSAGE, FOOTER_ACTION_CLICKED, MESSAGES } from './constants';

const updateKnowledgeFlag = createGraphQLEffect(flag_article, {
    successActionType: UPDATE_FLAG_COMMENT_SUCCEEDED,
    errorActionType: UPDATE_FLAG_COMMENT_FAILED,
    variableList: ['articleId', 'flagComment', 'reason']
});

export const actionHandlers = {
    [SUBMIT_FLAG_COMMENT]: {
        effect: ({ state, dispatch, action, updateState }) => {
            dispatch(UPDATE_FLAG_COMMENT, { 'articleId': state.properties.articleId, 'flagComment': state.flagInput, 'reason': (state.properties.enableActionableFeedback ? "Flagged" : "") });
            dispatch(FLAG_MODAL_RESET, action);
            updateState({ flagInput: "" });
        },
        stopPropagation: true
    },
    [UPDATE_FLAG_COMMENT]: updateKnowledgeFlag,
    [UPDATE_FLAG_COMMENT_SUCCEEDED]: {
        effect: ({ dispatch, action}) => {
            if(action.payload.data.GlideRecord_Mutation.insert_kb_feedback)
                dispatch(SHOW_MESSAGE, {
                    status: "info",
                    iconName: "circle-info-outline",
                    content: MESSAGES.ARTICLE_FLAGGED,
                    action: { type: "dismiss" }
                });
            else
                dispatch(SHOW_MESSAGE, {
                    status: "critical",
                    iconName: "circle-info-outline",
                    content: MESSAGES.LIMIT_REACHED_MESSAGE,
                    action: { type: "dismiss" }
                });
        },
        stopPropagation: true
    },
    [UPDATE_FLAG_COMMENT_FAILED]: {
        effect:({ dispatch}) =>{
            dispatch(SHOW_MESSAGE, {
                status: "critical",
                iconName: "circle-info-outline",
                content: MESSAGES.LIMIT_REACHED_MESSAGE,
                action: { type: "dismiss" }
            });
        },
        stopPropagation: true
    },
    [FOOTER_ACTION_CLICKED]: {
        effect : (props) => {
            if (props.action.payload.footerAction.id == "submit")
                props.dispatch(SUBMIT_FLAG_COMMENT, {});
        },
        stopPropagation: true
    },
    [MODAL_BUTTON_ESC]: {
        effect: ({ action, dispatch, updateState }) => {
            dispatch(FLAG_MODAL_RESET, action);
            updateState({ flagInput: "" });
        },
        stopPropagation: true
    }
};