import {createCustomElement} from '@servicenow/ui-core';
import {actions, actionHandlers} from './action';
import view from './view';
import styles from './kbArticleComparator.scss';

createCustomElement('sn-kb-article-comparator', {
    view,
    actions,
    actionHandlers,
    styles,
    initialState: {
        comparisonData:[]
    },
    properties: {
        table: {
            default: 'kb_knowledge',
            reflect: true
        },
        firstVersion: {
            default: '',
            reflect: true
        },
        secondVersion: {
            default: '',
            reflect: true
        }
    }
});