import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import { actionHandlers } from './actions';
import style from './kbStarRating.scss';
import { t } from 'sn-translate';
export const snKbStarRating = 'sn-kb-star-rating';

createCustomElement(snKbStarRating, {
	renderer: {
		type: snabbdom,
		view
	},

	initialState: {
		current: 0,
		isEditing: false
	},

	properties: {
		readOnly: { default: false },
		rating: { default: 0 },
		stars: { default: 5 },
		ariaLabel: { default: t('Rate')},
		halfRating: { default: false },
		size: {default:"lg"},
		articleId: {default:""},
		threshold: {default:-1}
	},
	styles: style,
	actionHandlers: actionHandlers
});