import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './kbFeedbackModal.scss';
import {actionHandlers} from './actions';
import { OTHER_REASON } from "./constants";

createCustomElement('sn-kb-feedback-modal' ,{
    renderer:{
        type: snabbdom,
        view
    },
    initialState: {
        reason: OTHER_REASON,
        comments: ""
    },
    actionHandlers,
    properties: {
        openModal: {
            default: false
        },
        userDetail: {
            default: []
        },
        choiceList: {
            default: []
        },
        articleId:{
            default: ""
        },
        helpful:{
            default: ""
        },
        rating:{
            default: ""
        }
    },
    styles: styles
});