import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-heading';
import {t} from 'sn-translate';
import '../kbSharedComparator';
import Comparator from '../kbSharedComparator/Comparator';

export default state => {
    const {comparisonData, originalVersionNumber, changedVersionNumber} = state;
    if(originalVersionNumber || changedVersionNumber){
        return (
            <Fragment>
                <now-heading has-no-margin className="header-left" label={t('Compare article versions')} variant="header-secondary"/>
                <ul>
                    <Comparator 
                        contentType="String"
                        heading={t('Article version')}
                        originalContent={originalVersionNumber} 
                        changedContent={changedVersionNumber}
                        name="version"
                        mode="normal"
                    />
                </ul>
                <sn-kb-shared-comparator mode="diff" comparison-data={comparisonData}/>
            </Fragment>
        );
    } else {
        null;
    }
}