export const knowledge_read = `query ($sys_id: String!, $ts_query_kb_id: String, $top_click_rank: String) {
  AppKnowledge_Query {
    getArticleDetails(articleID: $sys_id, searchId: $ts_query_kb_id, topClickRank: $top_click_rank) {
      articleHTML
      isEditable
      articleSysId
      infoMessage
      errorMessage
      templateFieldData {
        column
        content
        fieldStyle
        headingStyle
        label
        type
        collapsed
        collapsible
      }
      versionDetails {
      	isInstalled
        isEnabled
        history {
          sysClassName
          sysId
          versionLabel
          versionNumber
          versionText
        }
        showHistory
        versionDisplay
        versionDisplayLabel
        newVersionAvailable
        warningMessage
      }
      languageDetails {
        label
        sysId
        language
        selected
      }
      kbDocSysId
      showFeedbackOptions
      showStarRating
      showYNRating
      showFlag
      showFeedbackComments
      enableActionableFeedbackForRating
      enableActionableFeedbackForHelpful
      enableActionableFeedbackForFlag
      isKBAdmin
      isKBOwner
      commentsThreshold
      hasComments
      docviewerEnabled
      articleInfo {
				author
				displayAttachments
				kbCategory
				workflowState
				kbKnowledgeBase
				number
				shortDescription
				sysId
				sysUpdatedOn
				sysViewCount
				rating
				revisedBy
        baseVersion
        isExpired
      }
      totalUsefulCount
      usefulYesCount
    }
  }

  GlideDomain_Query {
    user {
      sys_id
      language
      avatar
      fullName
    }
  }
}`;
