import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-modal';
import { t } from 'sn-translate';
import { isEmpty } from 'lodash';

const getFooterActions = (flagInput) => {
	if (!isEmpty(extractContent(flagInput))) {
		return [{ id: "submit", label: t("Flag Article"), variant: "primary"}];
	} else {
		return [{ id: "submit", label: t("Flag Article"), variant: "primary", disabled: "true" }];
	}
}

const extractContent = (str) => {
	return (new DOMParser).parseFromString(str, "text/html").documentElement.textContent.toString().trim();
}

const snFlagModal = (state, { dispatch, updateState }) => {
	const content = (
		<div className="content">
			<div className="content-label">{t('Add a comment')}</div>
			<div className="content-input">
				<textarea id="flagCommentText" rows="10" value={state.flagInput} className="text-input" name="comment" on-input={e => {e.stopPropagation(); updateState({ flagInput: e.target.value }) }}></textarea>
			</div>
		</div>
	);
	return (
		<div className="kb-flag-modal">
			<now-modal
				manage-opened
				opened={state.properties.openModal}
				header-label={t("Flag this article")}
				footer-actions={getFooterActions(state.flagInput)}
				size="md">
				{content}
			</now-modal>
		</div>
	);
};

export default snFlagModal;