import { default as DetailMapper } from './DetailMapper';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { get } from 'lodash';
import '../knowledgeDetail';
import '../socialQADetail';
import '../communityQADetail';
import '../communityBlogDetail';
import '@servicenow/now-button';
import '@servicenow/now-alert';
import { NOW_ALERT_LIST_ITEMS_SET, CONTENT_UPDATED, SHOW_ALERT_MESSAGES } from './constants'

import KnowledgeViewHandler from './knowledgeViewHandler';

function setDetailToLoad(dispatch, id, sysid, table) {
	dispatch('PROPERTIES_SET', { detailToLoad: id, sys_id: sysid, table: table });
}

function setDetailToLoadFromResults(dispatch, state, dataSource, kbDotWalk, questionIDDotWalk, questionDefaultValue) {
	var communityKBResults = get(state, dataSource + '.GlideRecord_Query.sys_properties._results', '');
	var communityKBID = '';
	var questionKBID = '';
	var questionResults = get(state, dataSource + '.GlideRecord_Query.' + state.properties.table + '._results', '');
	if (communityKBResults && communityKBResults.length > 0) {
		communityKBResults.map((item) => {
			if (get(item, 'name.value', '') === 'sn_communities.knowledge_base_id') {
				communityKBID = get(item, 'value.value', '');
			}
		});
	}
	if (communityKBID.length > 0) {
		if (questionResults && questionResults.length > 0) {
			questionKBID = get(questionResults[0], kbDotWalk, '');
			var questionID = get(questionResults[0], questionIDDotWalk, '');
			if (questionKBID.length > 0 && questionKBID === communityKBID)
				dispatch('PROPERTIES_SET', { detailToLoad: 'community_question', sysIdToLoad: questionID });
			else
				dispatch('PROPERTIES_SET', { detailToLoad: 'socialqa', sysIdToLoad: questionID });
		}

	}
	else {
		dispatch(dispatch, 'PROPERTIES_SET', { detailToLoad: 'socialqa', sysIdToLoad: questionDefaultValue });
	}

}

const getView = (state, dispatch) => {
	const { table, baseTable } = state.properties;

	if (!state.properties.detailToLoad) {
		if (table === 'sn_communities_blog' || baseTable === 'sn_communities_blog') {
			dispatch('PROPERTIES_SET', { detailToLoad: 'community_blog', sysIdToLoad: state.properties.sysId });
		} else if (table === 'kb_social_qa_question' || baseTable === 'kb_social_qa_question') {
			setDetailToLoadFromResults(dispatch, state, 'question', 'kb_knowledge_base.value', 'sys_id.value', state.properties.sysId);
		} else if (table === 'kb_social_qa_answer' || baseTable === 'kb_social_qa_answer') {
			setDetailToLoadFromResults(dispatch, state, 'answer', 'kb_knowledge_base.value', 'sys_id.value', state.properties.sysId);
		}
	}
	var detailToLoad = get(state.properties, 'detailToLoad', null);
	var sysIdToLoad = get(state.properties, 'sysIdToLoad', null);
	if (!detailToLoad || !sysIdToLoad) return null;
	var DetailTag = DetailMapper[detailToLoad];
	if (!DetailTag)
		return null;

	return (
		<DetailTag table={table} sys-id={sysIdToLoad} />
	)
};

const getKnowledgeView = (state, dispatch) => {
	let { mode, detailToLoad, sysId } = state.properties;
	if (detailToLoad != mode) {
		if (sysId == -1) mode = 'form';
		dispatch('PROPERTIES_SET', { mode: mode, detailToLoad: mode });
		dispatch(CONTENT_UPDATED, { params: { 'mode': mode } });
		if (state.alertMessages && state.alertMessages.length)
			dispatch(NOW_ALERT_LIST_ITEMS_SET, { value: [] });
	}

	setTimeout(() => {
		dispatch(SHOW_ALERT_MESSAGES);
	}, 500);

	return (
		<Fragment>
			<now-alert-list config-aria-live="polite" items={state.alertMessages}></now-alert-list>
			<KnowledgeViewHandler props={state.properties} />
		</Fragment>
	)
};

export default (state, dispatch) => {
	const { table, baseTable } = state.properties;
	if (table === 'kb_knowledge' || baseTable === 'kb_knowledge' || table === 'kb_knowledge_block' || table.startsWith('kb_template') || table.startsWith('u_kb_template'))
		return getKnowledgeView(state, dispatch);
	else
		return getView(state, dispatch);
};
