import '@devsnc/sn-form-controls';
import '@servicenow/now-modal';
import '@servicenow/now-record-typeahead';
import { t } from 'sn-translate';
import { onSearchTermChange, onSelectionChange } from './utilities';
import {
    LOAD_FIELD_MAP
} from './constants';

export default (state, { dispatch, updateState }) => {
    const {
        selectedKnowledgeBase,
        selectedArticleTemplate,
        knowledgeBases,
        defaultKnowledgeBases,
        articleTemplates,
        isModalOpened,
        searchTerm,
        selectedOptionDisplayValue,
        mapExists
    } = state;

    const value = selectedOptionDisplayValue.length > 0 ? selectedOptionDisplayValue : searchTerm;

    const selectChild = (choice) => {
        updateState({
            selectedArticleTemplate: choice
        });
        var prQuery = '';
        if (mapExists)
            dispatch(LOAD_FIELD_MAP, { sysId: state.properties.current, source: state.properties.source, target: choice.value });
        else {
            prQuery = 'kb_knowledge_base=' + selectedKnowledgeBase.value;
            updateState({
                query: prQuery
            });
        }
        dispatch('SN_SCRIPTED_MODAL#BUTTON_ENABLED_SET', true);
        if (prQuery != '') {
            dispatch('SN_SCRIPTED_MODAL#DATA_SET', { query: prQuery, table: choice.value, kb: selectedKnowledgeBase.value });
        }
    }

    const getFooterActions = () => {
        if (selectedKnowledgeBase.value.trim() != '' && selectedArticleTemplate.value.trim() != '') {
            return [{
                "label": t('Create Article'),
                "variant": "primary", 
                "clickActionType": "OPEN_KNOWLEDGE_FORM"
            }, {
                "label": t('Cancel'),
                "variant": "secondary", 
                "clickActionType": "CLOSE_MODAL"
            }];
        } else {
            return [{
                "label": t("Create Article"),
                "variant": "primary", 
                "clickActionType": "OPEN_KNOWLEDGE_FORM", 
                "disabled": true
            }, {
                "label": t("Cancel"),
                "variant": "secondary", 
                "clickActionType": "CLOSE_MODAL"
            }];
        }
    }

    const knowlegeBaseDropdown = (
        <now-record-typeahead
            label={t("Knowledge Base")}
            required={true}
            options={knowledgeBases}
            value={value}
            displayValue={selectedKnowledgeBase.displayValue + ''}
            onValueChange={
                (a, b, selectionValue, selectionDisplayValue, e) => {
                    onSelectionChange(selectionValue, selectionDisplayValue, dispatch, updateState);
                }
            }
            onChange={
                (event) => {
                    onSearchTermChange(event, defaultKnowledgeBases, selectedOptionDisplayValue, dispatch, updateState);
                }
            }
        />
    );

    const articleTemplateDropdown = (
        <sn-record-choice
            readonly={selectedKnowledgeBase.value.trim() != '' ? false : true}
            label={t("Article Template")}
            required={true}
            choices={articleTemplates}
            value={selectedArticleTemplate.value + ''}
            displayValue={selectedArticleTemplate.displayValue + ''}
            onValueChange={selectChild}
        />
    );

    if (state.properties.modal == "true") {
        return (
            <now-modal
                opened={isModalOpened}
                manage-opened={true}
                header-label={t("Create Article")}
                footer-actions={getFooterActions()}
                size="md">

                <div className="header-statement">
                    {t("What type of article do you want to create?")}
                </div>

                <span>
                    <br /><br />
                    {knowlegeBaseDropdown}
                </span>

                <span>
                    <br /><br />
                    {articleTemplateDropdown}
                </span>
                <br /><br />
            </now-modal>
        );
    }
    else if (state.properties.modal == "false") {
        return (
            <div>
                <div className="header-statement">
                    {t("What type of article do you want to create?")}
                </div>

                <span>
                    <br /><br />
                    {knowlegeBaseDropdown}
                </span>

                <span>
                    <br /><br />
                    {articleTemplateDropdown}
                </span>
                <br /><br />
            </div>
        );
    }
};