export const translationDataForLoad = (source, translated) => {
    const changedContentMap = new Map();
    translated.forEach(({name,value}) => changedContentMap.set(name,value));
    const comparisonData = source.map(({name,value,type,label}) => ({
        name,
        contentType: type==='translated_html'?'html':type,
        heading: label,
        originalContent: value,
        changedContent: changedContentMap.get(name)
    }));
    return  comparisonData;
};

export const translationDataForCreate = (comparisonData, translatedLanguage) => {
    let translationObj = {htmlFieldArray:[], stringFieldArray:[]};
    comparisonData.forEach((element) => {
        let fieldObj = {
            name: element.name,
            value: element.changedContent,
            label: element.heading
        }
        if (element.contentType == 'html') {
            translationObj.htmlFieldArray.push(fieldObj);
        } else {
            translationObj.stringFieldArray.push(fieldObj);
        }
    });
    translationObj.language = translatedLanguage;
    return translationObj;
}

export const sortSourceContent = (source) => {
    source.sort((field1,field2) => {
        if(field1.name === 'short_description'){
            return -1;
        } else if(field2.name === 'short_description'){
            return 1;
        } else if(field1.type === 'html' || field1.type === 'translated_html'){
            return -1;
        } else if(field2.type === 'html' || field2.type === 'translated_html'){
            return 1;
        } else {
            return 0;
        }
    });
    return source;
}


