import '../timeAgo';
import '../../_shared/utils';
import {createElement} from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '@servicenow/now-icon';
import '@servicenow/now-avatar';


const view = (state) => {
	let answerClasses = '';
	if(state.properties.answered)
		answerClasses += ' -solved';
	return (
		<div className={'sn-card -response ' + answerClasses}>
			{
				(state.properties.answered) ? (<div className="sn-card-header">
					<div className="sn-card-row">
						<now-icon icon="circle-check-fill" size="md"></now-icon>
						<span>{t('Accepted Solution')}</span>
					</div>
				</div>
				) : ('')
			}

			<div className="sn-card-row has-avatar">
				<div className="sn-card-content -static">
					<now-avatar
						size="md"
						image-src={'/' + state.properties.answer.profile._reference.photo.displayValue}
						user-name={state.properties.answer.profile.displayValue}/>
				</div>
				<div className="sn-card-content">
					<ul className="sn-meta-list">
						<li className="sn-meta-list-item -bold"><span>{state.properties.answer.profile.displayValue}</span></li>
						<li className="sn-meta-list-item"><span><sn-timeago time-stamp={state.properties.answer.sys_created_on.value} behaviors={state.properties.behaviors} system-time={true}/></span></li>
						<li className="sn-meta-list-item"><span>{state.properties.answer.votes.value} {' ' + state.properties.answer.votes.value == 1 ? t('vote') : t('votes')}</span></li>
					</ul>
				</div>
			</div>
			<div className="sn-card-body">
				{createElement('div', {props: {innerHTML: state.properties.answer.answer.displayValue}})}
			</div>
		</div>
	);
};
export default view;
