import { t } from 'sn-translate';
import '@servicenow/now-button';
import { helpfulYesButton,  helpfulNoButton} from '../utils/actionUtil';
import {get} from 'lodash'
export default ({ props }) => {
    const showYNRating = get(props, "kbArticle.AppKnowledge_Query.getArticleDetails.showYNRating",false);
    const usefulYesCount = get(props, "helpfulData.usefulYesCount",0);
    const totalUsefulCount = get(props, "helpfulData.totalUsefulCount",0);
    const helpPercent = (showYNRating && usefulYesCount && totalUsefulCount) ? 
        Math.round(usefulYesCount / totalUsefulCount *100) : 0;
    return (
        <div className="kb-helpful">
            <span className="kb-helpful-label">{t('Helpful?')}</span>
            <span className="kb-helpful-yes"><now-button label={helpfulYesButton.label} size="md" variant={helpfulYesButton.variant} append-to-payload={{ "item": helpfulYesButton }}></now-button></span>
            <span className="kb-helpful-no"><now-button label={helpfulNoButton.label} size="md" variant={helpfulNoButton.variant} append-to-payload={{ "item": helpfulNoButton}}></now-button></span>
            {(helpPercent > 0 ? <span className="kb-helpful-info">{t('{0}% found this helpful.', helpPercent)}</span> : "")}
        </div>
    )
}