import { t } from "sn-translate";

const neutral18 = "#161b1c";
const secondary1 = "#01778e";

const commentButton = ({
	label,
	icon,
	weight = "regular",
	item,
	meta,
	size = "sm",
	iconColor = neutral18
}) => {
	let textStyles = {
		fontSize: "0.75rem",
		marginBottom: "-3px",
		color: neutral18
	};
	let buttonStyles = {
		marginRight: "0.25rem",
		color: iconColor
	};
	return (
		<now-button-bare appendToPayload={{ meta: meta, item: item }} hidePadding>
			<now-legacy-icon
				size={size}
				type={icon}
				weight={weight}
				style={buttonStyles}
			></now-legacy-icon>
			<span style={textStyles}>
				{label}
			</span>
		</now-button-bare>
	);
};

export const buttons = [
	{
		tag: commentButton,
		attributes: {
			icon: "thumbs-up",
			meta: "like",
			label: t("Like"),
			size: "sm"
		},
		derivedAttributes: {
			weight: item => {
				return item.isLiked ? "bold" : "regular";
			},
			item: item => item,
			iconColor: item => {
				return item.isLiked ? secondary1 : neutral18;
			}
		},
		visible: true
	},
	{
		tag: commentButton,
		attributes: {
			icon: "comment",
			meta: "reply",
			label: t("Reply"),
			size: "sm"
		},
		derivedAttributes: {
			item: item => item
		},
		visible: true
	}
];
export const commentActions = (user, properties) => ({
	useDropdown: false,
	dropdownItems: [],
	component: {
		tag: "now-button-iconic",
		attributes: {
			size: "md",
			variant: "tertiary",
			icon: "trash-outline",
			meta: "delete",
			tooltipContent: t("Delete"),
			bare: true,
			hidePadding: true
		},
		derivedAttributes: {
			hasFeedback: item => {
				if ((item.children && item.children.length > 0) || item.likesCount > 0)
					return true;
				return false;
			}
		},
		visible: item => item.user.id == user.id || properties.isAdminorOwner
	}
});
export const metaData = [
	{
		value: item => {
			if (item.likesCount)
				return item.likesCount == 1
					? t(`{0} like`, item.likesCount)
					: t(`{0} likes`, item.likesCount);
			return t("{0} likes", 0);
		},
		visible: true
	}
];
