import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './knowledgeDetail.scss';
import {view} from './view';
import {LOAD_KNOWLEDGE} from './constants';
import {actions, actionHandlers} from './actions';
import { timeAgoBehaviors } from "../../_shared/behaviors";

createCustomElement('sn-knowledge-detail', {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [...timeAgoBehaviors],
	initialState: {
		openFlagModal : false,
		openFeedbackModal :false,
		blocksSelected: false,
		blocksImpersonated: false,
		hasViewAccess: true,
		selectedSysId: '',
		rating:"",
		helpful: "",
		helpfulData: {
			usefulYesCount: 0,
			totalUsefulCount: 0
		}
	},
	properties: {
		table: '',
		sysId: '',
		mode: '',
		frameless: '',
		item: { default: '' }
	},
	actions,
	actionHandlers,
	onConnect(host, dispatch) {
		if (host.table === 'kb_knowledge' || host.table === 'kb_knowledge_block' || host.table.startsWith('kb_template') || host.table.startsWith('u_kb_template')) {
			if(host.item && host.item.link) {
				const parameters = new URLSearchParams(host.item.link);
				const rank = parameters.get('sysparm_rank');
				const searchId = parameters.get('sysparm_tsqueryId');

				dispatch(LOAD_KNOWLEDGE, {sys_id: host.sysId, ts_query_kb_id: searchId, top_click_rank: rank});
			} else {
				dispatch(LOAD_KNOWLEDGE, {sys_id: host.sysId});
			}
		}
	},
	styles: styles
});
