import { Fragment } from "@servicenow/ui-renderer-snabbdom";
import "@servicenow/now-avatar";
import "@servicenow/now-button";
import "@servicenow/now-badge";
import "@servicenow/now-modal";
import "@servicenow/now-heading";
import "@servicenow/now-record-html-editor";
import './sn-comments'
import { isEmpty } from "lodash";
import {
	HTML_EDITOR_ACTION_PERFORMED,
	TOGGLE_DISPLAY_ALL_COMMENTS,
	SHOW_MORE_COMMENTS
} from "./constants";
import { t } from 'sn-translate';


const footerActions = [{ "label": t("Yes, delete"), "variant": "primary", "meta": "delete" }, { "label": t("Cancel"), "variant": "secondary", "meta": "cancel" }];

export const TextEditor = ({ text = "", dispatch, appendToPayload, value, language }) => {
	return (
		<div className="html-editor">
			<now-record-html-editor
				value={text}
				placeholder={t("Type your comment here")}
				language={language}
			/>
			<div className="comment-buttons right">
				<now-button
					label={t("Post Comment")}
					tooltip-content={t("Post Comment")}
					size="md"
					variant="primary"
					disabled={isEmpty(value) ? true : false}
					on-click={() =>
						dispatch(HTML_EDITOR_ACTION_PERFORMED, {
							action: "submit",
							text: value,
							...appendToPayload
						})
					}
				></now-button>
			</div>
		</div>
	);
}

const snKnowledgeComments = (state, { dispatch, updateState }) => {
	if(state.properties.articleId)
	return (
		<div className="center">
			<div className="create-comment">
				<now-avatar
					aria-hidden="true"
					image-src={state.properties.user.avatar}
					size="md"
					user-name={state.properties.user.name}
					className="avatar"
				/>
				<div className="create-comment-container" >

					<TextEditor
						text=""
						dispatch={dispatch}
						appendToPayload={{ type: "new_comment" }}
						value={state.value}
						language={state.properties.user.userLanguage}
					/>
				</div>
			</div>
			<div className="header">
				<div className="inline-block" >
					<now-heading has-no-margin label={t("Comments")} level="1" variant="header-tertiary" className="heading"></now-heading>
					<now-badge round size="md" status="info" value={state.items.length} variant="secondary"></now-badge>
				</div>{
					state.hasChildren ? <now-button-bare expanded={state.expanded} data-testid="toggleReplies" className="right" icon-start={state.expanded ? "chevron-up-outline" : "chevron-down-outline"} label={state.expanded ? t("Hide all replies") : t("Show all replies")} tooltip-content={state.expanded ? t("Hide All Replies") : t("Show All Replies")} size="md" variant="primary" on-click={() => dispatch(TOGGLE_DISPLAY_ALL_COMMENTS)}></now-button-bare> : ""
				}
			</div>
			<sn-comments currentEditorId={state.currentEditorId} isEditing={state.isEditing} items={state.items.filter((item, index) => index <= state.commentsToDisplay-1)} buttons={state.buttons} actions={state.actions} expandedItems={state.expandedItems} metaData={state.metaData} manage-expanded language={state.properties.user.userLanguage}></sn-comments>
			{ state.items.length > state.commentsToDisplay ? <div className="show-more"><now-button-bare config-aria={{"aria-label":t("Show more comments")}} hide-padding label={t("Show more")} size="md" variant="primary" on-click={()=>dispatch(SHOW_MORE_COMMENTS)}></now-button-bare></div> : ""}
			<now-modal manage-opened opened={state.deleteModalOpened} content={state.deleteCommentData.message} footer-actions={footerActions} header-label={t('Are you sure you want to delete?')} size="md"></now-modal>
		</div>

	);
};

export default snKnowledgeComments;
