import { createGraphQLEffect } from "@servicenow/ui-effect-graphql";
import { t } from "sn-translate";
import { get, isEmpty } from "lodash";
import { AnswerQuery, QuestionQuery } from "./queries";
import { actionTypes } from "@servicenow/ui-core";
const { COMPONENT_RENDERED } = actionTypes;
import {
	LOAD_QUESTION,
	LOAD_ANSWER,
	LOAD_QUESTION_SUCCEEDED,
	LOAD_QUESTION_FAILED,
	LOAD_ANSWER_SUCCEEDED,
	LOAD_ANSWER_FAILED,
	COMPARE_ARTICLE_VERSIONS,
	KB_SHOW_ALERT_MESSAGE,
	NOW_ALERT_ACTION_CLICKED,
	NOW_ALERT_LIST_ITEMS_SET,
	SHOW_ALERT_MESSAGES,
	PREVIEW_RECORD,
	CONTENT_UPDATED
} from "./constants";

const fetchAnswerEffect = createGraphQLEffect(AnswerQuery, {
	successActionType: LOAD_ANSWER_SUCCEEDED,
	errorActionType: LOAD_ANSWER_FAILED,
	variableList: ["condition", "answerID"]
});

const fetchQuestionEffect = createGraphQLEffect(QuestionQuery, {
	successActionType: LOAD_QUESTION_SUCCEEDED,
	errorActionType: LOAD_QUESTION_FAILED,
	variableList: ["condition", "questionID"]
});

export const actions = {
	[LOAD_QUESTION]: {
		private: true
	},
	[LOAD_ANSWER]: {
		private: true
	}
};

export const actionHandlers = {
	[COMPONENT_RENDERED]: {
		effect: ({ dispatch, properties }) => {
			if(!isEmpty(properties.title))
			setTimeout(() => dispatch(CONTENT_UPDATED, {
            title: properties.title
      }), 500);
		}
	},
	[LOAD_QUESTION]: fetchQuestionEffect,
	[LOAD_ANSWER]: fetchAnswerEffect,
	[LOAD_QUESTION_SUCCEEDED]: {
		effect :({action, updateState}) =>{
			updateState({
				question: action.payload.data
			});
		},
		stopPropagation: true

	},
	[LOAD_QUESTION_FAILED]:  {
		effect :({state, updateState}) =>{
			updateState({
				...state
			});
		},
		stopPropagation: true
	},
	[LOAD_ANSWER_SUCCEEDED]: {
		effect :({action, updateState}) =>{
			updateState({
				answer: action.payload.data
			});
		},
		stopPropagation: true

	},
	[LOAD_ANSWER_FAILED]: {
		effect :({state, updateState}) =>{
			updateState({
				...state
			});
		},
		stopPropagation: true
	},
	[COMPARE_ARTICLE_VERSIONS]: {
		effect: ({ dispatch, action }) => {
			const { table, versions } = action.payload;
			const versionIds = versions.split(",");
			if (versionIds.length != 2) {
				dispatch(KB_SHOW_ALERT_MESSAGE, {
					action: {
						type: "dismiss"
					},
					status: "warning",
					icon: "bell-outline",
					content: t("You can compare only two versions of a knowledge article.")
				});
				return;
			}
			versionIds.sort();
			dispatch(PREVIEW_RECORD, {
				table,
				sys_id: versionIds.join("_"),
				params: {
					mode: "compare"
				}
			});
		},
		stopPropagation: true
	},
	[KB_SHOW_ALERT_MESSAGE]: {
		effect : ({ updateState, action }) => {
			if (action.payload.content) {
				action.payload.id = new Date().getTime();
				updateState({
					path: "alertMessages",
					operation: "push",
					value: action.payload
				});
			}
		},
		stopPropagation: true
	},
	[NOW_ALERT_ACTION_CLICKED]: {
		effect : ({ updateState, action, state }) => {
			if (action.payload.alertId) {
				let ind = state.alertMessages.findIndex(
					x => x.id == action.payload.alertId
				);
				if (ind != -1) {
					updateState({
						path: "alertMessages",
						operation: "splice",
						start: ind,
						deleteCount: 1
					});
				}
			}
		},
		stopPropagation: true
	},
	[NOW_ALERT_LIST_ITEMS_SET]: {
		effect : ({ updateState, action }) => {
			if (get(action, "payload.value", []).length == 0) {
				updateState({
					alertMessages: []
				});
			}
		},
		stopPropagation: true
	},
	[SHOW_ALERT_MESSAGES]: {
		effect : ({ updateState, properties, state }) => {
			let messages = get(properties, "alerts", "");
			if (!isEmpty(messages) && !state.messageDisplayed) {
				let msg = messages.split("::");
				let tempMsgs = [];
				msg.forEach(x => {
					tempMsgs.push({
						action: {
							type: "dismiss"
						},
						status: "info",
						icon: "circle-info-outline",
						content: x,
						id: new Date().getTime()
					})
				});
				updateState({
					alertMessages: [...state.alertMessages, ...tempMsgs],
					messageDisplayed: true
				});
			}
		},
		stopPropagation: true
	}
};
