import {SET_STATE} from './actions';
import {createElement} from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '../timeAgo';
import '@servicenow/now-icon';
import '@servicenow/now-avatar';

const view = (state, dispatch) => {
	if (!state.communityBlog) return null;
	const articleResult = state.communityBlog.data[0];
	if (!articleResult)
		return null;

	else if (articleResult){
		const frameless = state.properties.frameless === 'true' || state.properties.frameless === true;
		let noBorderClass = '-bottom-border';
		if(frameless)
			noBorderClass = '-no-border';

		const detailBody = (
			<div className={'sn-card ' + noBorderClass} tabIndex='0'>
				<header className="sn-card-header">
					<div className="sn-card-row"><span>{articleResult.forum.name} {articleResult.topics.length > 0 ? ' | ' + articleResult.topics[0].name : ''}</span></div>
					<div className="sn-card-row -title"><span>{articleResult.title}</span></div>
					<div className="sn-card-row has-avatar">
						<div className="sn-card-content -static">
							<now-avatar
								size="md"
								image-src={'/' + articleResult.userAvatarObject.userImage}
								user-name={articleResult.userAvatarObject.name} />
						</div>
						<div className="sn-card-content">
							<ul className="sn-meta-list">
								<li className="sn-meta-list-item"><now-icon icon="user-outline" size="sm"></now-icon><span>{t('Authored by') + ' ' + articleResult.userAvatarObject.name}</span></li>
								<li className="sn-meta-list-item"><now-icon icon="calendar-outline" size="sm"></now-icon><span>{t('Asked') + ' '}<sn-timeago time-stamp={articleResult.published_date} behaviors={state.behaviors} system-time={true}/></span></li>
								<li className="sn-meta-list-item"><now-icon icon="eye-outline" size="sm"></now-icon><span>{articleResult.view_count + ' ' + (articleResult.view_count == 1 ? t('view') : t('views'))}</span></li>
							</ul>
						</div>
					</div>
				</header>
				<div className="sn-card-row" ref = {(article_content) => {
					if (!state.articleContent) {
						dispatch(SET_STATE, {articleContent: article_content}, {stopPropagation: true});
					}
				}}>
					<div className="sn-card-body">
						{createElement('div',{props: {innerHTML: articleResult.description}})}
					</div>
				</div>
			</div>
		);
		if (state.articleContent) {
			var aTags = state.articleContent.querySelectorAll('a');
			aTags.forEach(function (item) {
				item.addEventListener('click', function(e) {
					var redirectURL = e.currentTarget || e.target || e.srcElement;
					window.open(redirectURL, '_blank');
					e.stopPropagation();
					e.preventDefault();
				});
			});
		}
		return (
			<div className='sn-agent-assist'>
				{ frameless ? (detailBody) : (
					<div className="sn-panel">
						{detailBody}
					</div>)
				}
			</div>
		);
	}
	return null;

};

export default view;
